import React, { PropsWithChildren } from "react"
import { OutlinedInput, outlinedInputClasses, OutlinedInputProps, Box, Chip, Stack, InputBase, ButtonBase, ButtonProps } from "@mui/material"
import { styled, Theme } from "@mui/system"
import { KeyboardArrowDown } from '@mui/icons-material';
import SharpBox from './SharpBox'
import SelectButton from './SelectButton'

interface BoxType extends ButtonProps {
  value: string[],
  onClick: (e: React.MouseEvent) => void
  InputComponent?: React.ReactElement<any>
}

const styling = ({ theme: {palette, spacing} }: any) => ({
  // border: `solid 1px ${palette.grey['A400']}`
  display: 'inline-flex',
  justifyContent: 'space-between',
  alignItems: 'center',
  height: 45,
  width: 250,
  maxWidth: 300,
  backgroundColor: palette.background.default,
  borderStyle: 'solid',
  borderWidth: 1,
  borderColor: palette.grey[300],
  borderRadius: 0,
  paddingRight: spacing(1.75),
  '&:hover': {
    borderColor: palette.grey[600],
  },
  '&:active': {
    borderWidth: 2,
    borderColor: palette.primary[500],
  }
})

const StyledBox = styled(Box)(styling)
const StyledInputBox = styled(Box)(({theme: {palette, spacing}}) => ({
  // padding: spacing(1.5, 3),
  maxWidth: 180,
  width: 120
}))

const StyledStack = styled(Stack)(({theme: {palette, spacing}}) => ({
  overflow: 'auto',
  '-ms-overflow-style': 'none',  /* Internet Explorer 10+ */
  'scrollbar-width': 'none',  /* Firefox */
  '&::-webkit-scrollbar': { 
    display: 'none'  /* Safari and Chrome */
  }
}))
const StyledChip = styled(Chip)(({theme: {palette}}) => ({
  height: 20,
  borderRadius: 0
}))

const S_InputBox = React.forwardRef<HTMLButtonElement | null, PropsWithChildren<BoxType>>((props: PropsWithChildren<BoxType>, ref: React.ForwardedRef<HTMLButtonElement | null>) => {
  const { InputComponent, children, value, ...rest } = props

  const inner = (
    <>
      <StyledInputBox>
        <StyledStack direction='row' spacing={0.2}>
          {value.length > 0 && value.map(val => <StyledChip label={val} size='small' />)}
        </StyledStack>
      </StyledInputBox>
      {/* <Box width='10%'> */}
      <KeyboardArrowDown />
      {/* </Box> */}
    </>
  )
  return (
    <>
      { InputComponent ?
        React.cloneElement(InputComponent, { ref: ref, ...props }, inner)
        : <SelectButton ref={ref} {...rest}>
          {inner}
        </SelectButton>
      }
    </>
  )
})

export default S_InputBox