import React, { Dispatch, PropsWithChildren, SetStateAction } from "react"
import { TrapFocus } from "@mui/base"
import {
	Box,
	ClickAwayListener,
	LinearProgress,
	Popper,
	PopperProps
} from "@mui/material"

type Props = {
	id?: string
	open: boolean
	anchorEl: HTMLElement | null
	setAnchorEl: Dispatch<SetStateAction<HTMLElement | null>>
	isLoading?: boolean
	PopperProps?: Partial<Omit<PopperProps, "open" | "anchorEl">>
	WrapperComponent: React.ElementType
}

const PopperMenu = (props: PropsWithChildren<Props>) => {
	const { id, open, anchorEl, setAnchorEl, children, isLoading, WrapperComponent } = props

	const escape = (e: React.KeyboardEvent<any>) => {
		if (e.code === "Escape") {
			anchorEl?.focus()
			setAnchorEl(null)
		}
	}

	return (
		<Popper
			id={id}
			open={open}
			anchorEl={anchorEl}
			onKeyDown={escape}
			placement="bottom-start"
		>
			<ClickAwayListener onClickAway={() => setAnchorEl(null)}>
				<Box>
					<TrapFocus open={open}>
						<Box>
							<WrapperComponent>
								{isLoading && <LinearProgress />}
								{children}
							</WrapperComponent>
						</Box>
					</TrapFocus>
				</Box>
			</ClickAwayListener>
		</Popper>
	)
}

PopperMenu.defaultProps = {
	WrapperComponent: Box
}

export default PopperMenu
