import { styled } from "@mui/material"
import { Box } from "@mui/system"
import { ISBOX } from "./IconContainer.types"

export const SBOX = styled(Box, {
	shouldForwardProp: prop => {
		return prop !== "isActive" && prop !== 'disabled'
	}
})<ISBOX>(({ theme: { palette, spacing }, isActive, width, disabled }) => {
	let borderColor = isActive ? palette.primary['A400'] : palette.secondary["800"]

	if(disabled) {
		borderColor = palette.action.disabled
	}

	return {
		// width: "100%",
		width: width ?? "100%",
		// minWidth: 60,
		height: 45,
		padding: spacing(0.8),
		border: isActive ? `1px solid` : 'none',
		borderColor: borderColor,
		borderRadius: "3.5px",
		display: "flex",
		justifyContent: "flex-start",
		alignItems: "center",
		cursor: 'pointer',
		// marginLeft: "auto"
		"&:hover" : {
			border: isActive ? `1px solid ${palette.primary['A400']}` : `1px solid ${palette.secondary['800']}`,
		},
		"& > svg": {
			maxWidth: width ?? "50%"
		}
	}
})
