import pickPractices from "../../utils/pickPractices"
import { IAppData, IUserType, TUserPracticeInfo } from "./UserContext.types"

export default function generateUserData(appData: IAppData[] | undefined): TUserPracticeInfo {
  let gender = null
  let type_of_user: IUserType = "external"

  const selectedApps: IAppData | null[] = []
  appData?.forEach(app => {
    if(app.selected) {
      let selectedRole
      const practices: { id: number }[] = []
      let tempApp: any = {}

      if(app.name == "ez-login") {
        type_of_user = "internal"
      }

      pickPractices(app, practices)

      app?.gender?.forEach(g => {
        if(g.isSelected) {
          gender = g.id
        }
      })

      app.roles.forEach(role => {
        if(role.isSelected) {
          selectedRole = {
            id: role.id,
            sub_role: role?.sub_roles?.find(subRole => subRole.isSelected)?.id || null
          }
        }
      })

      tempApp.id = app.id
      tempApp.practices = practices
      tempApp.role = selectedRole || {
        id: null,
        sub_role: null
      }
      selectedApps.push(tempApp)

    } else {
      selectedApps.push(null)
    }
  })

  const user: TUserPracticeInfo = {
    type_of_user,
    dr_iq_gender_id: gender,
    apps: selectedApps
  }

  return user
}