import {useMatch } from 'react-router-dom'

export default function() {
  let pattern=""
  Object.keys(trail).forEach((key)=>{
    const match=useMatch(key)
    if(match){
      pattern = match.pattern.path
    }
  })
  return trail[pattern] || []
}

type TrailType = {
  label: string,
  link?: string
}

const trail: Record<string, TrailType[]> = {
  'user-management': [{label: 'Home', link: '/'}, {label: 'User Management'}],
  'profile': [{label: 'Home', link: '/'}, {label: 'Profile'}],
  'audit-trail': [{label: 'Home', link: '/'}, {label: 'User Management', link: 'user-management'}, {label: 'Audit Trail'}],
  'create-user': [{label: 'Home', link: '/'}, {label: 'User Management', link: 'user-management'}, {label: 'Create User'}],
  'create-user/:id': [{label: 'Home', link: '/'}, {label: 'User Management', link: 'user-management'}, {label: 'Edit User'}],
  'account-access': [{label: 'Home', link: '/'},  {label: 'Account Access'}],
} 