import React, { useEffect, useMemo } from 'react'
import { Box, Chip, Stack, Typography } from '@mui/material'
import { ManagedSelect } from '../../components/FilterInput/FilterSelect'
import { IAppData } from '../../context/UserContext/UserContext.types'
import { useFormContext, useWatch } from 'react-hook-form'
import S_InputLabel from '../../components/FilterInput/InputLabel'
import Skeleton from '../../components/Skeleton/Skeleton'
import { DataLoadError } from '../../components/ErrorScreens'
import { IPermissionData } from './Profile.types'
import { Radar } from '@mui/icons-material'
import FilterInput from '../../components/FilterInput/FilterInput'

type Props = {
    permissions: IPermissionData[],
    isLoading: boolean,
    isError: boolean
}

const PermissionsPanel = (props: Props) => {
    const { permissions, isLoading, isError } = props
    const { getValues, setValue, control } = useFormContext()
    const watchProduct = useWatch({control, name: 'product'})
    const watchPractice = useWatch({control, name: 'practice'})

    const selectedProduct = permissions.find(pr => pr.name === getValues('product'))
    const selectedPractice = useMemo(() => {
        return selectedProduct?.practices?.find(pr => pr.id == getValues('practice'))
    }, [watchProduct, watchPractice])

    const currentRole = selectedProduct?.role

    useEffect(() => {
        setValue('role', selectedProduct?.role?.id || "")
        setValue('sub_role', selectedProduct?.role?.sub_role?.id || "")
        setValue('practice', null)
    }, [watchProduct])

    if(isError) {
        return <DataLoadError />
    }

    return (
        <Box
            sx={{
                display: "flex",
                flexDirection: "column",
                gap: "1rem"
            }}
        >
            <Typography variant="h6" component='h1' fontWeight="bold" color="primary">
                Roles And Practices
            </Typography>
            <Skeleton visible={isLoading} sx={{ minWidth: '100%' }}>
                <ManagedSelect name='product' label='Product'>
                    { permissions.map((product) => {
                        return <option label={product.sp_app_name} value={product.name} />
                    }) }
                </ManagedSelect>
            </Skeleton>
            <Stack direction={{ sm: 'column', md: 'row' }} gap={2} justifyItems='stretch' justifyContent='center'>
                <Skeleton visible={isLoading} sx={{ flex: 1 }}>
                    <FilterInput label='Role' value={currentRole?.name || ""} disabled />
                </Skeleton>
                <Skeleton visible={isLoading} sx={{ flex: 1 }}>
                    <FilterInput label='Sub Role' value={currentRole?.sub_role?.name || ""} disabled />
                </Skeleton>
            </Stack>
            <Skeleton visible={isLoading} sx={{ minWidth: '100%' }}>
                <ManagedSelect name='practice' label='Practice'>
                    { selectedProduct?.practices.map(practice => <option label={practice.name} value={practice.id} /> )}
                </ManagedSelect>
            </Skeleton>
            <Stack direction='column' spacing={2}>
                <S_InputLabel>Sub Practices</S_InputLabel>
                <Skeleton visible={isLoading} sx={{ minWidth: '100%' }}>
                    <Stack direction='row' flexWrap='wrap' gap={2} width='100%' height={300} overflow='auto' alignContent='flex-start' bgcolor='grey.100' p={2} borderRadius={1}>
                        { selectedPractice?.practices ?
                            selectedPractice?.practices?.map(sub_prac => <Chip label={sub_prac.name}></Chip>) :
                            <Box display='flex' width='100%' height='100%' alignItems='center' justifyContent='center' gap={2}>
                                <Radar /><Typography variant='caption' color='GrayText'>No Practices</Typography>
                            </Box>
                        }
                    </Stack>
                </Skeleton>
            </Stack>
        </Box>
    )
}

export default PermissionsPanel