import React, { ReactElement } from "react"
import { useAuthContext } from "../../context/AuthContext/AuthContext"
import { TRoles } from "../../context/AuthContext/AuthContext.types"

interface TProps {
	allowedRoles: TRoles[]
	children: ReactElement | null
}
const RenderIf = ({ allowedRoles, children }: TProps) => {
	const auth = useAuthContext()
	const userRoles = auth?.userInfo.roles
	const isAllowed = userRoles?.filter(role => allowedRoles.includes(role)) || []
	console.log(userRoles, isAllowed, allowedRoles)
	return isAllowed.length ? children : null
}

export default RenderIf
