import { useId } from "react"
import { Stack, Box, styled, Checkbox, Typography, Collapse, Avatar, Tooltip } from "@mui/material"
import Select from "../../components/FilterInput/FilterSelect"
import { IAppData } from "../../context/UserContext/UserContext.types"
import PracticesDropdown from "../PracticeDropdown/PracticesDropdown"
import { useUserContext } from "../../context/UserContext/UserContext"
import ErrorInfo from "../ErrorInfo"

export const products: Record<string, string> = {
  "ez-login": "EZ Login",
  "ez-doc": "EZ Doc",
  "ez-nav": "EZ Nav",
  "dr-iq": "Dr. iQ",
  "ez-analytics": "EZ Analytics",
  "ez-path": "EZ Path",
  "ez-web": "EZ Web"
}

type StyledBoxProps = {
  expanded: boolean
}

const StyledBox = styled(Stack)<StyledBoxProps>(({ expanded, theme }) => ({
  width: expanded ? '100%' : 180,
  transition: 'width 0.3s',
  transitionDelay: '0.1s',
  alignItems: 'flex-start',
  borderRadius: theme.shape.borderRadius,
  border: `solid 1px ${theme.palette.divider}`,
  padding: theme.spacing(1.2)
}))

const GridBox = styled(Box)<StyledBoxProps>(({ expanded, theme }) => ({
  width: expanded ? '100%' : 0,
  transition: 'width 0.3s',
  display: 'grid',
  gridTemplateColumns: '1fr 1fr',
  gap: theme.spacing(2),
  marginTop: theme.spacing(2),

  [theme.breakpoints.down('sm')]: {
    gridTemplateColumns: '1fr'
  }
}))

type Props = {
  index: number
  Logo: string
  selected: boolean | undefined
  app: IAppData
  errors: Record<string, any> | undefined
  disabled: boolean
}

const ProductList = (props: Props) => {
  const checkboxId = useId()
  const { index, Logo, selected, app, errors, disabled } = props
  const appData = useUserContext()

  const setApp = (data: IAppData, appIndex: number) => {
    if (appData?.appData) {
      appData?.setAppData([
        ...appData?.appData?.map((t_app, idx) => {
          if (idx == appIndex) {
            return data
          } else {
            return t_app
          }
        })
      ])
    }
  }

  const selectedRole = app.roles.find(role => role.isSelected)

  return (
    <StyledBox direction='column' expanded={!!selected}>
      <Stack direction='row' width='100%' alignItems='center'>
        <Checkbox
          id={checkboxId}
          size="small"
          name={app.name}
          checked={!!selected}
          onClick={() => {
            appData?.setSelected(app.id)
          }}
          onKeyUp={(e: any) => {
            if (e.code === 'Enter') {
              appData?.setSelected(app.id)
            }
          }}
          sx={theme => ({ padding: theme.spacing(0.2) })}
          disabled={disabled}
        />
        <Stack direction='column' width='100%' ml={2}>
          <label htmlFor={checkboxId}>
            <Stack direction='row' spacing={1.5} alignItems='center' width='fit-content' sx={{ cursor: 'pointer' }}>
              <Box height='auto' width={25}>
                <Tooltip title={products[app.name]} placement="bottom">
                  <Avatar
                    src={`${process.env.REACT_APP_SERVER_URL}/${Logo}`}
                    alt={`${app.name} Logo`}
                    sx={{ width: '100%', height: '100%', filter: disabled ? 'grayscale(100%)' : 'none' }}
                    variant="square"
                    imgProps={{
                      draggable: false,
                      sx: { objectFit: 'unset' }
                    }}
                  />
                </Tooltip>
              </Box>
              <Typography
                variant='body2'
                fontWeight='bolder'
                whiteSpace='nowrap'
                letterSpacing='0.49px'
              >
                {products[app.name]}
              </Typography>
            </Stack>
          </label>
        </Stack>
        <Box width={20} display='flex' alignItems='center'>
          <ErrorInfo
            show={!!errors && !!selected}
            messages={[
              ...(Array.isArray(errors) ? errors : []),
              // typeof errors === 'string' && errors,
              ...errors?.role?.id || [],
              ...errors?.role?.sub_role || [],
              ...errors?.practices || [],
              ...errors?.dr_iq_gender_id || []
            ]}
            fontSize='small'
          />
        </Box>
      </Stack>
      <Collapse in={selected} sx={{ marginTop: 0, width: '100%' }}>
        <Stack direction='row' width='100%' alignItems='flex-start'>
          <Checkbox
            size="small"
            sx={{ visibility: 'hidden' }}
            disabled={disabled}
          />
          <Stack direction='column' width='100%' ml={2}>
            <GridBox expanded={!!selected}>
              {app.name !== 'ez-login' && (
                <PracticesDropdown app={app} appIndex={index} setApp={setApp} disabled={disabled} />
              )}

              <Select
                value={selectedRole?.id?.toString() || ""}
                placeholder="Role"
                onChange={(value) => {
                  appData?.setSubrole(app.id, null)
                  if (value) {
                    appData?.setRole(app.id, parseInt(value))
                  } else {
                    appData?.setRole(app.id, null)
                  }
                }}
                disabled={disabled}
              >
                {
                  app.roles.map(role => {
                    return (
                      <option key={role.id} label={role.name} value={role.id}>
                        {role.name}
                      </option>
                    )
                  })
                }
              </Select>

              {app.name == 'dr-iq' &&
                <>
                  <Select
                    disabled={disabled}
                    value={app.gender.find(g => g.isSelected)?.id?.toString() || ""}
                    placeholder="Gender"
                    onChange={(value) => {
                      if (value) {
                        appData?.setGender(app.id, parseInt(value))
                      } else {
                        appData?.setGender(app.id, null)
                      }
                    }}
                  >
                    {
                      app.gender.map(gen => {
                        return (
                          <option key={gen.id} label={gen.name} value={gen.id}>
                            {gen.name}
                          </option>
                        )
                      })
                    }
                  </Select>
                  <Select
                    disabled={!selectedRole?.id || disabled}
                    value={selectedRole?.sub_roles?.find(ele => ele.isSelected)?.id?.toString() || ""}
                    placeholder="Sub Role"
                    onChange={(value) => {
                      if (value) {
                        appData?.setSubrole(app.id, parseInt(value))
                      } else {
                        appData?.setSubrole(app.id, null)
                      }
                    }}
                  >
                    {
                      selectedRole ? selectedRole?.sub_roles?.map(role => {
                        return (
                          <option key={role.id} label={role.name} value={role.id}>
                            {role.name}
                          </option>
                        )
                      }) : null
                    }
                  </Select>
                </>
              }
            </GridBox>
          </Stack>
        </Stack>
      </Collapse>
    </StyledBox>
  )
}

export default ProductList
