import React, { useEffect } from "react";
import { Collapse } from "@mui/material";
import { IPractice } from "../../context/UserContext/UserContext.types";
import Item from "./Item";

interface PanelProps {
  id: string
  option: IPractice
  onCheck: (item: IPractice, arrayIndex: number[]) => void
  index: number[]
  expanded?: boolean
  search?: string
}

export default function Panel(props: PanelProps) {
  const { option, onCheck, index, expanded, search, id } = props;
  const [_expanded, setExpanded] = React.useState(expanded);

  useEffect(() => setExpanded(expanded), [expanded])
  /* const labelId = `checkbox-list-label-${option.id}`; */ //NOSONAR
  const handleToggle = (_e: React.MouseEvent<HTMLDivElement>) => {
    setExpanded(!_expanded);
  };

  const onCheckHandler = (item: IPractice, indexArray: number[]) => {
    onCheck(item, indexArray);
  };

  /* const checkedPracticesLength = option?.practices?.filter((op) => op.isChecked).length || 0; */ //NOSONAR

  return (
    <>
      <Item
        id={id}
        name={option.name}
        expandable={!!option?.practices?.length}
        expanded={_expanded}
        onClick={handleToggle}
        {...(!option.isChecked ? {indeterminate:
          /* checkedPracticesLength > 0 &&
          checkedPracticesLength !== option?.practices?.length */
          !!option.isIndeterminate
        } : {})}
        checked={!!option.isChecked}
        onCheck={() => onCheckHandler(option, [...index])}
        level={index.length}
        labelId={`checklistitem-${index.join('-')}`}
      />
      {_expanded && option?.practices && (
        <>
        {/* <Collapse in={_expanded} timeout="auto"> */}
          {option?.practices?.map((prac, i) => (
            <Panel
              key={i}
              id={`${id}-${i}`}
              index={[...index, prac.id]}
              option={prac}
              onCheck={onCheck}
              expanded={ search ? !prac.name.includes(search) : false }
            />
          ))}
        {/* </Collapse> */}
        </>
      )}
    </>
  );
}
