import React from "react"
import { OutlinedInput, outlinedInputClasses, OutlinedInputProps, Box, Chip, Stack, InputBase } from "@mui/material"
import { styled, Theme } from "@mui/system"
import { KeyboardArrowDown } from '@mui/icons-material';

const styling = ({ theme: {palette, spacing, shape} }: any) => ({
  // border: `solid 1px ${palette.grey['A400']}`
  display: 'inline-flex',
  justifyContent: 'space-between',
  alignItems: 'center',
  height: 45,
  width: 250,
  maxWidth: 300,
  backgroundColor: palette.background.default,
  borderStyle: 'solid',
  borderWidth: 1,
  borderColor: palette.grey[300],
  borderRadius: shape.borderRadius,
  paddingRight: spacing(1.75),
  '&:hover': {
    borderColor: palette.grey[600],
  },
  '&:active': {
    borderWidth: 2,
    borderColor: palette.primary[500],
  }
})

const StyledBox = styled(Box)(styling)

const RoundBox = React.forwardRef<HTMLElement | undefined, any>((props: any, ref: React.ForwardedRef<HTMLElement | undefined>) => (
  <StyledBox ref={ref} {...props}>
    {props.children}
  </StyledBox>
))

export default RoundBox