import React from "react"
import { OutlinedInput, outlinedInputClasses, OutlinedInputProps, Box, Chip, Theme, styled } from "@mui/material"
import { MUIStyledCommonProps } from "@mui/system"

const styling = ({ theme }: MUIStyledCommonProps<Theme>) => ({
  // border: `solid 1px ${palette.grey['A400']}`
  height: 45,
  backgroundColor: theme?.palette.background.default,
  borderRadius: theme?.shape.borderRadius,
  [`& .${outlinedInputClasses.input}`]: {
    padding: theme?.spacing(1.5, 3)
  },
  [`& .${outlinedInputClasses.notchedOutline}`]: {
    borderColor: theme?.palette.grey[300],
    borderWidth: 2,
    '&:hover': {
      borderColor: theme?.palette.primary.main
    },
  },
})

const S_Input = styled(
  React.forwardRef<HTMLElement | undefined, OutlinedInputProps>((
    props: OutlinedInputProps,
    ref?: React.ForwardedRef<HTMLElement | undefined>
  ) => 
    <OutlinedInput margin="none" {...props} label="none" notched={false} ref={ref} />
  ))(styling)

export default S_Input