import { CardContent, styled, Box, CardActions } from "@mui/material"

export const SCardContent = styled(CardContent)(({ theme: { spacing } }) => ({
	display: "flex",
	flexDirection: "column",
	gap: spacing(2),
	paddingLeft: spacing(3),
	paddingRight: spacing(3)
}))
export const SBox = styled(Box)(() => ({
	display: "flex",
	justifyContent: "space-around",
	alignItems: "center"
}))

export const SCardActions = styled(CardActions)(({ theme: { spacing } }) => ({
	justifyContent: "flex-end",
	padding: spacing(3)
}))
