import React from "react"
import { Theme, ButtonProps, Button, buttonClasses, styled } from "@mui/material"
import { MUIStyledCommonProps } from "@mui/system"

const styling: any = ({ theme }: MUIStyledCommonProps<Theme>) => ({
  // border: `solid 1px ${palette.grey['A400']}`
  width: '100%',
  maxWidth: '100%',
  height: 45,
  justifyContent: 'space-between',
  backgroundColor: theme?.palette.background.default,
  [`& .MuiTypography-root`]: {
    width: '100%',
    whiteSpace: 'nowrap',
    overflow: 'hidden',
    textOverflow: 'ellipsis',
    textAlign: 'left'
  },
  [`& .${buttonClasses.endIcon}`]: {
    width: 20,
  },
  '&:hover': {
    borderWidth:2
  },
  padding: theme?.spacing(1.5, 3),
  borderWidth: 2,
  borderColor: theme?.palette.grey[300],
  borderRadius:theme?.shape.borderRadius,
  color: theme?.palette.action.active
})

const S_Button = styled(
  React.forwardRef<HTMLButtonElement | null, ButtonProps>((
    props: ButtonProps,
    ref: React.ForwardedRef<HTMLButtonElement | null>
  ) => 
    <Button variant="outlined" {...props} ref={ref} />
  ))(styling)

export default S_Button