import { useQuery, UseQueryOptions } from 'react-query';
import { axiosFetcher } from '../api/axiosFetcher';
export type Maybe<T> = T | null;
export type InputMaybe<T> = Maybe<T>;
export type Exact<T extends { [key: string]: unknown }> = { [K in keyof T]: T[K] };
export type MakeOptional<T, K extends keyof T> = Omit<T, K> & { [SubKey in K]?: Maybe<T[SubKey]> };
export type MakeMaybe<T, K extends keyof T> = Omit<T, K> & { [SubKey in K]: Maybe<T[SubKey]> };
/** All built-in and custom scalars, mapped to their actual values */
export type Scalars = {
  ID: string;
  String: string;
  Boolean: boolean;
  Int: number;
  Float: number;
  DateTime: any;
};

export enum ActionLevelEnum {
  Application = 'application',
  User = 'user'
}

export enum DirectionEnum {
  Backward = 'backward',
  Forward = 'forward'
}

export type PageInfo = {
  __typename?: 'PageInfo';
  endCursor?: Maybe<Scalars['String']>;
  hasNextPage: Scalars['Boolean'];
  hasPreviousPage: Scalars['Boolean'];
  pageCount?: Maybe<Scalars['Int']>;
  perPage?: Maybe<Scalars['Int']>;
  startCursor?: Maybe<Scalars['String']>;
  totalRecords?: Maybe<Scalars['Int']>;
};

export type Query = {
  __typename?: 'Query';
  userActions: UserActionConnection;
};


export type QueryUserActionsArgs = {
  actionLevel?: InputMaybe<ActionLevelEnum>;
  cursor?: InputMaybe<Scalars['String']>;
  direction?: DirectionEnum;
  endDateTime?: InputMaybe<Scalars['String']>;
  first?: Scalars['Int'];
  roleName?: InputMaybe<RoleEnum>;
  search?: InputMaybe<Scalars['String']>;
  startDateTime?: InputMaybe<Scalars['String']>;
  userEmail?: InputMaybe<Scalars['String']>;
};

export enum RoleEnum {
  PracticeAdmin = 'practice_admin',
  SubAdmin = 'sub_admin',
  SuperAdmin = 'super_admin'
}

export type UserAction = {
  __typename?: 'UserAction';
  actionDate?: Maybe<Scalars['DateTime']>;
  actionId?: Maybe<Scalars['Int']>;
  actionLabel?: Maybe<Scalars['String']>;
  actionLevel?: Maybe<Scalars['String']>;
  actionName?: Maybe<Scalars['String']>;
  appName?: Maybe<Scalars['String']>;
  id: Scalars['Int'];
  idpUserEmail?: Maybe<Scalars['String']>;
  idpUserId?: Maybe<Scalars['Int']>;
  idpUserName?: Maybe<Scalars['String']>;
  roleName?: Maybe<Scalars['String']>;
  status?: Maybe<Scalars['String']>;
};

export type UserActionConnection = {
  __typename?: 'UserActionConnection';
  edges: Array<UserActionEdge>;
  pageInfo: PageInfo;
};

export type UserActionEdge = {
  __typename?: 'UserActionEdge';
  cursor: Scalars['String'];
  node: UserAction;
};

export type AuditTrailsQueryVariables = Exact<{
  actionLevel?: InputMaybe<ActionLevelEnum>;
  cursor?: InputMaybe<Scalars['String']>;
  direction?: InputMaybe<DirectionEnum>;
  endDateTime?: InputMaybe<Scalars['String']>;
  first?: InputMaybe<Scalars['Int']>;
  roleName?: InputMaybe<RoleEnum>;
  search?: InputMaybe<Scalars['String']>;
  startDateTime?: InputMaybe<Scalars['String']>;
}>;


export type AuditTrailsQuery = { __typename?: 'Query', userActions: { __typename?: 'UserActionConnection', edges: Array<{ __typename?: 'UserActionEdge', node: { __typename?: 'UserAction', actionDate?: any | null, actionId?: number | null, actionLabel?: string | null, actionName?: string | null, appName?: string | null, actionLevel?: string | null, id: number, idpUserEmail?: string | null, idpUserName?: string | null, idpUserId?: number | null, roleName?: string | null, status?: string | null } }>, pageInfo: { __typename?: 'PageInfo', endCursor?: string | null, hasNextPage: boolean, hasPreviousPage: boolean, pageCount?: number | null, perPage?: number | null, startCursor?: string | null, totalRecords?: number | null } } };

export type ActivityLogQueryVariables = Exact<{
  actionLevel?: InputMaybe<ActionLevelEnum>;
  cursor?: InputMaybe<Scalars['String']>;
  direction?: InputMaybe<DirectionEnum>;
  first?: InputMaybe<Scalars['Int']>;
  search?: InputMaybe<Scalars['String']>;
  userEmail?: InputMaybe<Scalars['String']>;
}>;


export type ActivityLogQuery = { __typename?: 'Query', userActions: { __typename?: 'UserActionConnection', edges: Array<{ __typename?: 'UserActionEdge', node: { __typename?: 'UserAction', appName?: string | null, actionDate?: any | null } }>, pageInfo: { __typename?: 'PageInfo', endCursor?: string | null, hasNextPage: boolean, hasPreviousPage: boolean, pageCount?: number | null, perPage?: number | null, startCursor?: string | null, totalRecords?: number | null } } };


export const AuditTrailsDocument = `
    query AuditTrails($actionLevel: ActionLevelEnum, $cursor: String, $direction: DirectionEnum, $endDateTime: String, $first: Int, $roleName: RoleEnum, $search: String, $startDateTime: String) {
  userActions(
    actionLevel: $actionLevel
    cursor: $cursor
    direction: $direction
    endDateTime: $endDateTime
    first: $first
    roleName: $roleName
    search: $search
    startDateTime: $startDateTime
  ) {
    edges {
      node {
        actionDate
        actionId
        actionLabel
        actionName
        appName
        actionLevel
        id
        idpUserEmail
        idpUserName
        idpUserId
        roleName
        status
      }
    }
    pageInfo {
      endCursor
      hasNextPage
      hasPreviousPage
      pageCount
      perPage
      startCursor
      totalRecords
    }
  }
}
    `;
export const useAuditTrailsQuery = <
      TData = AuditTrailsQuery,
      TError = unknown
    >(
      variables?: AuditTrailsQueryVariables,
      options?: UseQueryOptions<AuditTrailsQuery, TError, TData>
    ) =>
    useQuery<AuditTrailsQuery, TError, TData>(
      variables === undefined ? ['AuditTrails'] : ['AuditTrails', variables],
      axiosFetcher<AuditTrailsQuery, AuditTrailsQueryVariables>(AuditTrailsDocument, variables),
      options
    );
export const ActivityLogDocument = `
    query ActivityLog($actionLevel: ActionLevelEnum, $cursor: String, $direction: DirectionEnum, $first: Int, $search: String, $userEmail: String) {
  userActions(
    actionLevel: $actionLevel
    cursor: $cursor
    direction: $direction
    first: $first
    search: $search
    userEmail: $userEmail
  ) {
    edges {
      node {
        appName
        actionDate
      }
    }
    pageInfo {
      endCursor
      hasNextPage
      hasPreviousPage
      pageCount
      perPage
      startCursor
      totalRecords
    }
  }
}
    `;
export const useActivityLogQuery = <
      TData = ActivityLogQuery,
      TError = unknown
    >(
      variables?: ActivityLogQueryVariables,
      options?: UseQueryOptions<ActivityLogQuery, TError, TData>
    ) =>
    useQuery<ActivityLogQuery, TError, TData>(
      variables === undefined ? ['ActivityLog'] : ['ActivityLog', variables],
      axiosFetcher<ActivityLogQuery, ActivityLogQueryVariables>(ActivityLogDocument, variables),
      options
    );