import * as yup from "yup"

const phoneValidation =
	/^[\+]?[(]?[0-9]{3}[)]?[-\s\.]?[0-9]{3}[-\s\.]?[0-9]{4,6}$/
export const profileSchema = yup
	.object({
		first_name: yup.string().trim().required("First name should not be empty."),
		last_name: yup.string().trim().required("Last name should not be empty."),
		contact_no: yup
			.string()
			.matches(phoneValidation, "Phone number is not valid")
			.required("Contact no should not be empty."),
		address: yup.string().required("Address should not be empty.")
	})
	.required()
