import * as yup from "yup"
export const loginSchema = yup
	.object({
		email: yup.string().email("Invalid email").required("Email required"),
		password: yup.string().when("$step", (name, schema) => {
			return name === "login" ? schema.min(4).required() : schema
		}),
		otp: yup.string().when("$step", (name, schema) => {
			return name === "otp-verifier"
				? schema.required("Please enter OTP")
				: schema
		}),
		appsData: yup
			.array()
			.of(
				yup.object().shape({
					id: yup.number(),
					display_name: yup.string(),
					name: yup.string(),
					info: yup.string(),
					host: yup.string(),
					sp_metadata: yup.string(),
					logo_url: yup.string(),
					is_active: yup.boolean(),
					created_date: yup.date(),
					updated_date: yup.date(),
					email_verification_url: yup.string(),
					selected: yup.boolean().default(false)
				})
			)
			.when("$step", (name, schema) => {
				return name == "email-products"
					? schema.test(
							"selected_test",
							"Please select at least one app",
							(values: any, context: any) => {
								return values.some((obj: any) => obj.selected)
							}
					  )
					: schema
			})
	})
	.required()
