import { alpha, Card, styled } from "@mui/material"
import { ISCard } from "./ImageCard.types"

const colors: Record<string, string> = {
	"dr-iq": "#d9ebff",
	"ez-doc": "#ffd6ff",
	"ez-path": "#e1d5fd",
	"ez-analytics": "#fcd5ff",
	"ez-nav": "#dcd0fa",
	"ez-web": "#d5e3ff",
	"ez-login": "#ecc6ff"
}

export const SCard = styled(Card, {
	//to prevent the prop injected to dom element=> returns true or false
	shouldForwardProp: prop =>
		!["disabled", "isBordered", "name"].includes(prop as string)
})<ISCard>(({ theme: { palette, spacing }, disabled, name, isBordered }) => ({
	display: "flex",
	flexDirection: "column",
	justifyContent: "center",
	alignItems: "center",
	gap: spacing(2),
	width: "155px",
	height: "149px",
	borderRadius: "25px",
	opacity: disabled ? 0.3 : 1,
	backgroundColor: disabled ? palette.grey[300] : name ? alpha(colors[name], 0.3) : "transparent",
	boxShadow: "none",
	...(isBordered && {
		backgroundColor: "transparent",
		border: `1px solid ${palette.grey?.[300]}`
	}),
	position: "relative"
}))
