import { Box, SvgIconProps, Tooltip } from '@mui/material'
import { InfoRounded } from '@mui/icons-material'

type Props = {
  show: boolean
  messages: string[]
} & SvgIconProps

const ErrorInfo = (props: Props) => {
  const { messages, show, ...restProps } = props

  return (
    <Tooltip title={
      <>{ messages?.map((msg, index) => <Box key={index}>{msg}</Box>) }</>
    }>
      <InfoRounded fontSize='inherit' color='error' sx={{ visibility: show ? 'visible' : 'hidden' }} {...restProps} />
    </Tooltip>
  )
}

export default ErrorInfo