import {
	Box,
	Link,
	List,
	ListItem,
	ListItemAvatar,
	ListItemText,
	styled,
	Typography
} from "@mui/material"
import React, { Dispatch, SetStateAction, useState } from "react"
import { FileIcon, SearchIcon } from "../../utils/Icons"
import FilterInput from "../FilterInput/FilterInput"
import PopperMenu from "../PopperMenu"
import { Footer, RoundedContainer } from "../PopperMenu/PopperMenu.styles"

const helpItems = [
	{ title: "Sed do eiusmod tempor incididunt et dolore magna aliqua." },
	{ title: "Lorem ipsum dolor sit amet?" },
	{ title: "Sed do eiusmod tempor incididunt et dolore magna aliqua." }
]

type Props = {
	open: boolean
	anchorEl: HTMLElement | null
	setAnchorEl: Dispatch<SetStateAction<HTMLElement | null>>
}

const StyledPaper = styled(Box)(({ theme }) => ({
	width: "100%",
	padding: theme.spacing(0, 4),
	display: "flex",
	flexDirection: "column",
	justifyContent: "center",
	alignItems: "center"
}))

const HelpMenu = (props: Props) => {
	const { open, anchorEl, setAnchorEl } = props
	const [searchQuery, setSearchQuery] = useState<string>()

	const onSearchChange: React.ChangeEventHandler<
		HTMLInputElement | HTMLTextAreaElement
	> = e => {
		const value = e.target.value
		setSearchQuery(value)
	}

	return (
		<PopperMenu
			open={open}
			anchorEl={anchorEl}
			setAnchorEl={setAnchorEl}
			WrapperComponent={RoundedContainer}
		>
			<>
				<StyledPaper sx={{ minWidth: 300, maxWidth: 400 }}>
					<Typography
						component="h1"
						variant="h6"
						textAlign="center"
						mt={4}
						mb={2}
					>
						Help
					</Typography>
					<FilterInput
						value={searchQuery}
						onChange={onSearchChange}
						fullWidth
						startAdornment={<SearchIcon color="action" />}
						sx={theme => ({ backgroundColor: theme.palette.grey["A700"] })}
					/>

					<Box>
						<Typography component="h2" variant="h6" mt={3} mb={2}>
							Help Resources
						</Typography>
						{helpItems.map((item, idx) => (
							<List key={idx}>
								<ListItem disablePadding disableGutters>
									<ListItemAvatar>
										<Box
											sx={theme => ({
												display: "flex",
												alignItems: "center",
												justifyContent: "center",
												width: 40,
												height: 40,
												backgroundColor: theme.palette.grey["A700"],
												border: `1px solid ${theme.palette.divider}`,
												borderRadius: "50%"
											})}
										>
											<FileIcon color="primary" />
										</Box>
									</ListItemAvatar>
									<ListItemText>
										<Link
											underline="hover"
											href="#"
											sx={theme => ({ color: theme.palette.text.primary })}
										>
											{item.title}
										</Link>
									</ListItemText>
								</ListItem>
							</List>
						))}
					</Box>
				</StyledPaper>
				<Footer>
					<Link
						target="_blank"
						href="/support"
						variant="body1"
						underline="none"
						sx={theme => ({ color: theme.palette.text.primary })}
					>
						Support
					</Link>
				</Footer>
			</>
		</PopperMenu>
	)
}

export default HelpMenu
