import { GifBox } from "@mui/icons-material"
import {
	Box,
	FormGroup,
	FormHelperText,
	Grow,
	Tooltip,
	Typography
} from "@mui/material"
import { useFormContext, FieldArrayWithId } from "react-hook-form"
import ScrollableStack from "../../layout/ScrollableStack"
import { products } from "../../pages/Home/Home"
import { TUseFormData } from "../../pages/Login/Login.types"
import { ArrowBackIcon } from "../../utils/Icons"
import FormInput from "../FormInput/FormInput"
import InputStatus from "../InputStatus"
import { SImg, SImgBox, SLabelBox } from "./index.styles"

type TApps = {
	fields: FieldArrayWithId<TUseFormData, "appsData", "fieldId">[]
	handleBackStep: () => void
	disabled: boolean
}

const FoundProducts = ({ fields, handleBackStep, disabled }: TApps) => {
	const { getValues, getFieldState, formState } = useFormContext()
	const { error } = getFieldState("appsData", formState)
	return (
		<>
			<Typography variant="h5" fontWeight={600}>
				SIGN IN
			</Typography>
			<ScrollableStack spacing={2}>
				<Box
					display="flex"
					alignItems="center"
					flexWrap="wrap"
					gap={1}
					fontWeight="medium"
					component={Typography}
					variant="subtitle1"
				>
					{disabled && (
						<Tooltip placement="top" arrow title="Choose apps">
							{/* <IconButton
							// onClick={handleStepBack}
							size="small"
							color="primary"
							aria-label="go back to email screen"
							disableRipple
						> */}
							<ArrowBackIcon onClick={handleBackStep} color="primary" />
							{/* </IconButton> */}
						</Tooltip>
					)}
					<Typography
						fontWeight="inherit"
						variant="subtitle1"
						color="primary"
						component={"span"}
					>
						{getValues("email")}
					</Typography>
					found in:
				</Box>
				{/* <FormInput
					label="Email"
					placeholder="Email"
					type="email"
					fullWidth
					variant="outlined"
					name="email"
				/> */}
				<Grow in={true}>
					<FormGroup sx={{ gap: "1rem" }}>
						{fields.map(({ display_name, logo_url, id, name }, idx) => (
							// <FormControlLabel
							// 	key={name}
							// 	control={<Checkbox disableRipple size="medium" />}
							// 	label={
							// <SLabelBox>
							// 	<SImg src={icon} alt="i" />
							// 	<Typography
							// 		fontWeight="bold"
							// 		variant="subtitle1"
							// 		component={"span"}
							// 		color="#050303"
							// 	>
							// 		{name}
							// 	</Typography>
							// </SLabelBox>
							// 	}
							// />
							<FormInput
								disabled={disabled}
								key={`${name}-${idx}`}
								name={`appsData.${idx}.selected`}
								variant="outlined"
								type={"checkbox"}
								label={
									<SLabelBox>
										<SImgBox>
											<SImg
												src={`${process.env.REACT_APP_SERVER_URL}/${logo_url}`}
												alt="i"
											/>
										</SImgBox>

										<Typography
											fontWeight="bold"
											variant="subtitle1"
											component={"span"}
											color="#050303"
										>
											{display_name}
										</Typography>
									</SLabelBox>
								}
							/>
						))}
						{!!error?.message && (
							<FormHelperText error>
								<InputStatus text={error.message} />
							</FormHelperText>
						)}
					</FormGroup>
				</Grow>
			</ScrollableStack>
		</>
	)
}

export default FoundProducts
