import React from 'react'
import {
	Box,
	InputAdornment,
	InputLabel,
	Skeleton,
	Typography
} from "@mui/material"
import { useNavigate } from "react-router-dom"
import FormInput from "../../components/FormInput/FormInput"
import StyledButton from "../../components/StyledButton"
import { EditIcon } from "../../utils/Icons"

type Props = {
    mutationLoading: boolean
	isLoading: boolean
}

const AccountPanel = ({mutationLoading, isLoading}: Props) => {
	const navigate = useNavigate()

    return (
        <Box
            sx={{
                display: "flex",
                flexDirection: "column",
                gap: "1rem"
            }}
        >
            <Typography variant="h6" component='h1' fontWeight="bold" color="primary">
                {isLoading ? <Skeleton animation="wave" /> : "Account Settings"}
            </Typography>
            <Box
                sx={theme => ({
                    display: "grid",
                    gridTemplateColumns: `repeat(2,1fr)`,
                    mt: "1rem",
                    gap: "1rem",
                    [theme.breakpoints.down('md')]: {
                        gridTemplateColumns: `1fr`,
                    }
                })}
            >
                <Box
                    sx={{
                        display: "flex",
                        flexDirection: "column",
                        gap: "0.75rem"
                    }}
                >
                    <InputLabel variant="standard" htmlFor="first_name">
                        <Typography color="#444444">First Name</Typography>
                    </InputLabel>
                    {isLoading ? (
                        <Skeleton animation="wave" height={60} />
                    ) : (
                        <FormInput
                            id="first_name"
                            label={false}
                            InputLabelProps={{ shrink: false }}
                            inputProps={{ style: { height: "1.6875rem" } }}
                            fullWidth
                            placeholder="First Name"
                            size="medium"
                            name="first_name"
                            variant="outlined"
                            type="text"
                        />
                    )}
                </Box>
                <Box
                    sx={{
                        display: "flex",
                        flexDirection: "column",
                        gap: "0.75rem"
                    }}
                >
                    <InputLabel variant="standard" htmlFor="last_name">
                        <Typography color="#444444">Last Name</Typography>
                    </InputLabel>

                    {isLoading ? (
                        <Skeleton animation="wave" height={60} />
                    ) : (
                        <FormInput
                            id="last_name"
                            label={false}
                            InputLabelProps={{ shrink: false }}
                            inputProps={{ style: { height: "1.6875rem" } }}
                            fullWidth
                            placeholder="Last Name"
                            size="medium"
                            name="last_name"
                            variant="outlined"
                        />
                    )}
                </Box>
                <Box
                    sx={{
                        display: "flex",
                        flexDirection: "column",
                        gap: "0.75rem"
                    }}
                >
                    <InputLabel variant="standard" htmlFor="email_input">
                        <Typography color="#444444">Email</Typography>
                    </InputLabel>

                    {isLoading ? (
                        <Skeleton animation="wave" height={60} />
                    ) : (
                        <FormInput
                            id="email_input"
                            label={false}
                            InputLabelProps={{ shrink: false }}
                            disabled
                            aria-disabled
                            inputProps={{ style: { height: "1.6875rem" } }}
                            fullWidth
                            placeholder="example@gmail.com"
                            size="medium"
                            name="email"
                            variant="outlined"
                            InputProps={{
                                endAdornment: (
                                    <InputAdornment position="start">
                                        <EditIcon fontSize="small" />
                                    </InputAdornment>
                                )
                            }}
                        />
                    )}
                </Box>
                {/* <Box
						sx={{
							display: "flex",
							flexDirection: "column",
							gap: "0.75rem"
						}}
					>
						<InputLabel variant="standard">
                    <Typography color="#444444">Password</Typography>
                </InputLabel>
                <FormInput
                    inputProps={{ style: { height: "1.6875rem" } }}
                    fullWidth
                    placeholder="password"
                    size="small"
                    name="password"
                    type="password"
                    variant="outlined"
                    InputProps={{
                        endAdornment: (
                            <InputAdornment position="start">
                                <EditIcon fontSize="small" />
                            </InputAdornment>
                        )
                    }}
                />
					</Box> */}
                <Box
                    sx={{
                        display: "flex",
                        flexDirection: "column",
                        gap: "0.75rem"
                    }}
                >
                    <InputLabel variant="standard" htmlFor="contact_no">
                        <Typography color="#444444">Phone number</Typography>
                    </InputLabel>

                    {isLoading ? (
                        <Skeleton animation="wave" height={60} />
                    ) : (
                        <FormInput
                            id="contact_no"
                            label={false}
                            InputLabelProps={{ shrink: false }}
                            inputProps={{ style: { height: "1.6875rem" } }}
                            fullWidth
                            placeholder="xxxx xxxx xxxx"
                            size="medium"
                            name="contact_no"
                            type="tel"
                            variant="outlined"
                            InputProps={{
                                endAdornment: (
                                    <InputAdornment position="start">
                                        <EditIcon fontSize="small" />
                                    </InputAdornment>
                                )
                            }}
                        />
                    )}
                </Box>
                <Box></Box>
                <Box
                    sx={{
                        display: "flex",
                        flexDirection: "column",
                        gap: "0.75rem",
                        gridColumn: "1/-1"
                    }}
                >
                    <InputLabel variant="standard" htmlFor="address_input">
                        <Typography color="#444444">Address</Typography>
                    </InputLabel>
                    {isLoading ? (
                        <Skeleton animation="wave" height={60} />
                    ) : (
                        <FormInput
                            id="address_input"
                            label={false}
                            InputLabelProps={{ shrink: false }}
                            inputProps={{ style: { height: "1.6875rem" } }}
                            fullWidth
                            multiline
                            // rows={4}
                            minRows={4}
                            placeholder="Address"
                            size="medium"
                            name="address"
                            variant="outlined"
                        />
                    )}
                </Box>
            </Box>
            <Box marginLeft="auto" mt="2rem" display="flex" justifyContent='center' gap={"1rem"} flexWrap='wrap'>
                <StyledButton
                    variant="outlined"
                    onClick={() => navigate('/')}
                    role='link'
                    sx={{ flexGrow: 1 }}
                >
                    Cancel
                </StyledButton>
                <StyledButton
                    disabled={mutationLoading}
                    type="submit"
                    variant="contained"
                    sx={{ flexGrow: 1 }}
                >
                    Save
                </StyledButton>
            </Box>
        </Box>
    )
}

export default AccountPanel