import { Box, Link, Paper, styled } from "@mui/material";

export const SPaper = styled(Box)(({ theme: { spacing, palette } }) => ({
	maxWidth: 340,
	minWidth: 300,
	height: "auto",
	padding: spacing(2),
	display: 'grid',
	gridTemplateColumns: "1fr 1fr 1fr",
	gap: 2
}))
export const SAppIcon = styled(Link)(({ theme: { spacing, palette, breakpoints } }) => ({
	width: "100px",
	minHeight: "100px",
	borderRadius: spacing(4),
	display: "flex",
	flexDirection: "column",
	alignItems: "center",
	justifyContent: "space-evenly",
	cursor: "pointer",
  padding: spacing(1),
	"&:hover, &:focus": {
		backgroundColor: palette.grey['A700'],
		"& span": {
			whiteSpace: "normal",
			wordWrap: "break-word"
		}
	},
	[breakpoints.down('sm')]: {
		width: "85px",
		minHeight: "85px",
	}
}))