import TableCell, { tableCellClasses } from "@mui/material/TableCell"
import TableRow from "@mui/material/TableRow"
import TableBody from "@mui/material/TableBody"
import { styled } from "@mui/material/styles"
import { Box, Table, Pagination, PaginationItem } from "@mui/material"
import { SyncAlt } from "@mui/icons-material"

const StyledTable = styled(Table)(({ theme: { spacing } }) => ({
	borderCollapse: "separate",
	borderSpacing: `0 ${spacing(1)}`
}))

const StyledPaper = styled(Box)(({ theme }) => ({}))

const StyledTableBody = styled(TableBody)(() => ({}))

const StyledTableCell = styled(TableCell)(({ theme: { palette } }) => ({
	border: "none",
	[`&.${tableCellClasses.head}`]: {
		fontWeight: "bold"
	}
}))

const StyledHeaderRow = styled(TableRow)(
	({ theme: { palette, shape, spacing } }) => ({
		background: palette.background.backgroundGradientMulti?.(),
		border: `1px solid transparent`,

		"& th:first-child": {
			borderTopLeftRadius: +shape.borderRadius * 8
		},
		"& th:last-child": {
			borderTopRightRadius: +shape.borderRadius * 8
		}
	})
)

const StyledTableRow = styled(TableRow, {
	shouldForwardProp: prop => prop !== "highlighted"
})<{ highlighted: boolean }>(
	({ highlighted, theme: { palette, shape, spacing } }) => ({
		borderRadius: +shape.borderRadius * 4,
		border: `1px solid transparent`,
		...(highlighted
			? {
					backgroundColor: palette.secondary[200],
					"& td:first-child": {
						borderTopLeftRadius: 20,
						borderBottomLeftRadius: 20
					},
					"& td:last-child": {
						borderTopRightRadius: 20,
						borderBottomRightRadius: 20
					}
			  }
			: {})
	})
)

const PaginationContainer = styled(Box)(
	({ theme: { palette, spacing, shape } }) => ({
		position: "sticky",
		bottom: 0,
		width: "100%",
		display: "flex",
		justifyContent: "space-between",
		alignItems: "center",
		border: `solid 2px ${palette.grey[300]}`,
		borderRadius: shape.borderRadius,
		marginTop: spacing(1),
		padding: spacing(1.5, 4),
		backgroundColor: palette.background.paper,
		"& > div": {}
	})
)

const StyledPagination = styled(Pagination)(() => ({}))

const StyledPaginationItem = styled(PaginationItem)(
	({ theme: { palette, shape } }) => ({
		height: 26,
		minWidth: 26,
		display: "flex",
		alignItems: "center",
		justifyContent: "center",
		borderRadius: parseInt(`${shape.borderRadius}`) / 2,
		border: `solid 1px ${palette.grey[200]}`
	})
)

const StyledSortIcon = styled(SyncAlt)(() => ({
	transform: "rotate(90deg)"
}))

export {
	StyledTable,
	StyledTableBody,
	StyledTableCell,
	StyledHeaderRow,
	StyledTableRow,
	StyledPaper,
	PaginationContainer,
	StyledPagination,
	StyledPaginationItem,
	StyledSortIcon
}
