import React, { useRef, useState, useMemo, Children, useEffect, KeyboardEvent, useId } from "react"
import { useSelect, ClickAwayListener } from "@mui/base"
import { Box, Theme } from "@mui/system"
import InputBase from "./InputBase"
import SelectButton from "./SelectButton"
import InputLabel from "./InputLabel"
import { KeyboardArrowDown } from "@mui/icons-material"
import { MenuItem, Popper, SxProps, OutlinedInputProps, Button, Typography, Tooltip, ButtonProps, LinearProgress } from "@mui/material"
import Listbox from "./SelectListbox"
import ClearMenuItem from "./ClearMenuItem"
import PopperMenu from "../PopperMenu"
import { useController, useFormContext } from "react-hook-form"

interface ChildProps {
  label: string
  value: string | number
  style?: Record<string, any>
}

export interface SelectProps {
  label?: string | null
  value?: string | null,
  onChange: (value: string | null) => void
  suffix?: string,
  defaultValue?: string,
  children: React.ReactElement<ChildProps>[] | React.ReactElement<ChildProps> | null | undefined,
  sx?: SxProps<Theme>
  placeholder?: string,
  disabled?: boolean,
  isLoading?: boolean,
  buttonProps?: ButtonProps,
  InputComponent?: React.ReactElement<{
    value: string
    onClick: () => void
    endAdornment: OutlinedInputProps["endAdornment"]
    placeholder: string
    disabled: boolean,
    ref: React.MutableRefObject<HTMLElement | undefined>
    sx?: SxProps<Theme>
  } & HTMLElement>
}

export interface ManagedSelectProps extends Omit<SelectProps, 'value' | 'onChange'> {
  name: string
}

type OptionsType = {
  label: string,
  value: string | null
}

export const ManagedSelect = (props: ManagedSelectProps) => {
  const { name, defaultValue } = props
  const { control, setValue, formState: { errors }, } = useFormContext()
  const { field: { ref, ...inputProps }, } = useController({ name, control, defaultValue: defaultValue || "", })
  return (
    <Select
      {...props}
      value={inputProps.value}
      onChange={inputProps.onChange}
      // error={props.isError || errors?.[name]?.message}
      // clear={() => setValue(name, null)}
    />
  )
}

function Select(props: SelectProps) {
  const { label, children, onChange, suffix, defaultValue, sx, value, InputComponent, placeholder, disabled, buttonProps, isLoading } = props
  // const inputRef = useRef<HTMLElement>()
  const inputRef = useRef<HTMLButtonElement | null>(null)
  const listBoxRef = useRef<HTMLUListElement>(null)
  const [listboxVisible, setListboxVisible] = useState<HTMLElement | null>(null);
  const [displayValue, setDisplayValue] = useState("")
  const uuid = useId()

  const options = useMemo(
    () => {
      if (!children || Children.count(children) <= 0) {
        return []
      } else {
        return Children.map(children, child => {
          if (!child.props.value || !child.props.label) {
            return null
          }
          return { label: child.props.label, value: child.props.value.toString() }
        })
          .filter(opt => opt)
      }
    }, [children]
  )

  const menuListSelect = (e: KeyboardEvent, fn: Function) => {
    const code = e.code

    if (code === 'Enter') {
      fn(e)
      setListboxVisible(null)
    }
  }

  const { getButtonProps, getListboxProps, getOptionProps, open } =
    useSelect({
      // listboxRef,
      options: options,
      open: Boolean(listboxVisible),
      onChange: onChange,
      defaultValue,
      // value
      ...(value !== "" || value !== undefined ? { value } : { value: "" })
    })


  useEffect(() => {
    setDisplayValue(options.find(opt => opt.value == value)?.label || "")
    setListboxVisible(null)
  }, [value])

  const suffixString = suffix ? ` ${suffix}` : ''
  const clearOptionProps = getOptionProps({ value: "", label: "" })

  return (
    <Box display='flex' sx={sx} minWidth={180}>
      <Box width='100%' position='relative'>
        {label && <InputLabel htmlFor={uuid}>{label}</InputLabel>}
        {InputComponent ? <></>
          // React.cloneElement(InputComponent, {
          //   value: `${displayValue}${suffixString}`,
          //   ...(!disabled && { onClick: () => setListboxVisible(true) }),
          //   // onClick: () => setListboxVisible(true),
          //   endAdornment: <KeyboardArrowDown />,
          //   placeholder: placeholder || "",
          //   disabled,
          //   ref: inputRef,
          //   tabIndex: -1
          // }, null)
          :
          <Tooltip title={displayValue || ""}>
            <SelectButton
              id={uuid}
              color='primary'
              {...getButtonProps()}
              onClick={() => setListboxVisible(inputRef.current)}
              endIcon={<KeyboardArrowDown />}
              disabled={disabled}
              ref={inputRef}
              role='listbox'
              {...buttonProps}
            >
              <Typography variant="body1">
                {`${displayValue ? `${displayValue}${suffixString}` : placeholder || ""}`}
              </Typography>
            </SelectButton>
          </Tooltip>
        }
        {
          <PopperMenu open={open} anchorEl={inputRef.current} setAnchorEl={setListboxVisible}>
            <Listbox
              {...getListboxProps()}
              style={{ width: inputRef?.current?.clientWidth }}
            >
              <ClearMenuItem
                {...clearOptionProps}
                onKeyUp={(e: KeyboardEvent) => menuListSelect(e, clearOptionProps.onClick)}
              />
              {children && Children.map(children, (child) => {
                const optionProps = getOptionProps({ value: child.props.value.toString(), label: child.props.label })

                if (!child.props.value || !child.props.label) {
                  return null
                }
                return (
                  <MenuItem
                    key={child.props.value}
                    onKeyUp={(e: KeyboardEvent) => menuListSelect(e, optionProps.onClick)}
                    className={optionProps["aria-selected"] ? 'Mui-selected' : ''}
                    {...optionProps}
                  >
                    <Tooltip title={child.props.label} placement='right-end'>
                      {React.cloneElement(child, {
                        style: {
                          width: '100%',
                          whiteSpace: 'nowrap',
                          overflow: 'hidden',
                          textOverflow: 'ellipsis',
                          textAlign: 'left'
                        }
                      })}
                    </Tooltip>
                  </MenuItem>
                )
              })}
            </Listbox>
          </PopperMenu>
        }
        {isLoading && <LinearProgress sx={{ position: 'absolute', bottom: 0, left: 0, width: '100%' }} />}
      </Box>
    </Box>
  )
}

Select.defaultProps = {
  disabled: false
}

export default Select