import React, { PropsWithChildren, useEffect, useMemo } from "react"
import TableContainer from "@mui/material/TableContainer"
import TableHead from "@mui/material/TableHead"
import {
	StyledPaper,
	StyledTableCell,
	StyledTableRow,
	StyledTableBody,
	StyledTable,
	StyledHeaderRow,
	PaginationContainer,
	StyledPagination,
	StyledPaginationItem,
	StyledSortIcon
} from "./Table.styles"
import { ITable } from "./Table.types"
import { Box } from "@mui/system"
import { Skeleton, Typography } from "@mui/material"
import {
	ArrowForward,
	ArrowBack,
	ExpandLess,
	ExpandMore
} from "@mui/icons-material"
import { useTable, useFilters, usePagination, useSortBy } from "react-table"
import typedMemo from "../../utils/typedMemo"

function Table<DataType extends object, PaginationType extends 'cursor' | 'paged'>(
	props: PropsWithChildren<ITable<DataType, PaginationType>>
) {
	const {
		columns,
		data,
		filters,
		rowsPerPage,
		useSorting,
		hiddenColumns,
		metaData,
		setPage,
		page: pageNo,
		paginationType,
		disabled,
		dataLoading,
		noHeader,
		rowHighlight,
		size
	} = props

	const memoizedColumns = useMemo(() => columns, [])
	const memoizedData = useMemo(() => data, [data])

	//add plugins to the table
	//order of adding is important here check docs
	const plugins: any[] = [useFilters]
	if (useSorting) {
		plugins.push(useSortBy)
	}
	plugins.push(usePagination)

	const tableInstance = useTable<DataType>(
		{
			columns: memoizedColumns,
			data: memoizedData,

			initialState: {
				pageSize: rowsPerPage || 8,
				pageIndex: 0,
				hiddenColumns
			},

			//used for server side pagination
			manualPagination: true,
			pageCount: 'page_count' in metaData ? metaData?.page_count : 0,
			autoResetPage: false
		},
		...plugins
	)

	const {
		getTableProps,
		getTableBodyProps,
		headerGroups,
		prepareRow,
		setFilter,
		page,
		pageCount,
		gotoPage,
		setPageSize,
		canNextPage,
		state: { pageIndex, pageSize }
	} = tableInstance

	useEffect(() => {
		if(filters) {
			Object.entries(filters).forEach(([key, value]) => setFilter(key, value))
		}
	}, [filters])

	useEffect(() => {
		if (rowsPerPage) {
			setPageSize(rowsPerPage)
			//reset the page to 0 for every row entries change
			gotoPage(0)
		}
	}, [rowsPerPage])

	const cursorPagination = paginationType === 'cursor'
	const emptyRows = Math.abs(pageSize - page.length)
	const startRowNo = pageIndex * pageSize + 1
	//for last page the final value would be the total records
	const endRowNo = !canNextPage
		? metaData?.total_records
		: pageIndex * pageSize + pageSize

	return (
		<Box>
			<TableContainer component={StyledPaper} sx={{ width: '100%' }}>
				<StyledTable
					aria-label="customized table"
					{...getTableProps()}
				>
					{ !noHeader && 
						<TableHead>
							{
								// Loop over the header rows
								headerGroups.map(headerGroup => (
									// Apply the header row props
									<StyledHeaderRow {...headerGroup.getHeaderGroupProps()}>
										{
											// Loop over the headers in each row
											headerGroup.headers.map((column, _id) => (
												// Apply the header cell props
												<StyledTableCell
													{...column.getHeaderProps(
														useSorting ? column.getSortByToggleProps() : undefined
													)}
												>
													<Box
														display="flex"
														width="100%"
														justifyContent="space-between"
														alignItems="center"
													>
														<>
															{
																// Render the header
																column.render("Header")
															}
															{useSorting && (
																<Box component="span">
																	{column.isSorted ? (
																		column.isSortedDesc ? (				//NOSONAR
																			<ExpandLess />
																		) : (
																			<ExpandMore />
																		)
																	) : column.canSort ? (					//NOSONAR
																		<StyledSortIcon />
																	) : null}
																</Box>
															)}
														</>
													</Box>
												</StyledTableCell>
											))
										}
									</StyledHeaderRow>
								))
							}
						</TableHead>
					}
					<StyledTableBody {...getTableBodyProps()}>
						{
							// Loop over the table rows
							page.map(row => {
								// Prepare the row for display
								prepareRow(row)
								return (
									// Apply the row props
									<StyledTableRow highlighted={Boolean(rowHighlight)} {...row.getRowProps()}>
										{
											// Loop over the rows cells
											row.cells.map(cell => {
												// Apply the cell props
												return (
													<StyledTableCell {...cell.getCellProps()} size={size}>
														<Typography variant="body1">
															{ dataLoading ?
																<Skeleton variant='rectangular' width='100%' animation='wave'>
																	<Box sx={{ visibility: 'hidden' }}>{cell.render("Cell")}</Box>
																</Skeleton> :
																<>{cell.render("Cell")}</>
															}
														</Typography>
													</StyledTableCell>
												)
											})
										}
									</StyledTableRow>
								)
							})
						}
						{new Array(emptyRows).fill(0).map(() => {
							return (
								<StyledTableRow highlighted={false} sx={() => ({ backgroundColor: "transparent" })}>
									<StyledTableCell colSpan={columns.length}>
										{dataLoading ?
											<Skeleton width='100%'><Typography>&nbsp;</Typography></Skeleton> :
											<Typography variant="body1">&nbsp;</Typography>
										}
									</StyledTableCell>
								</StyledTableRow>
							)
						})}
					</StyledTableBody>
				</StyledTable>
			</TableContainer>
			<PaginationContainer>
					<Box>
						<Typography fontWeight={600} variant='body2'>
							{ paginationType === 'cursor' ?
									`${metaData?.total_records || ""} total records` 
								: `Showing ${startRowNo} to ${endRowNo} of ${metaData?.total_records || ""}`
							}
						</Typography>
					</Box>
					<Box>
						<StyledPagination
							color="primary"
							count={'page_count' in metaData ? metaData?.page_count || pageCount : 0}
							shape="rounded"
							page={pageNo}
							disabled={disabled}
							onChange={(_e, pNo) => {
								//manuaaly invoke react-table page start with 0 so -1 to the page
								gotoPage(pNo - 1)
								//Manually Invoke setPage offset start with 1
								setPage?.(pNo)
							}}
							renderItem={item => {
								const direction = item.type === 'next' ? 'forward' : 'backward'
								const paginationBtnDisabled = item.type === 'next' ? !metaData?.hasNextPage : !metaData?.hasPreviousPage

								if(cursorPagination && item.type === 'page') {
									return null
								}

								if(item.type === 'page') {
									return <StyledPaginationItem
										{...item}
										components={{ next: ArrowForward, previous: ArrowBack }}
										onClick={(_e) => {
											if(item?.page){
												//manuaaly invoke react-table page start with 0 so -1 to the page
												gotoPage(item.page - 1)
												//Manually Invoke setPage offset start with 1
												setPage?.(item.page)
											}
										}}
									/>
								}

								return <StyledPaginationItem
									{...item}
									components={{ next: ArrowForward, previous: ArrowBack }}
									{...(cursorPagination ? { disabled: paginationBtnDisabled } : {})}
									onClick={cursorPagination ? () => setPage?.(null, direction) : item.onClick}
								/>
							}}
						/>
					</Box>
				</PaginationContainer>
		</Box>
	)
}

Table.defaultProps = {
	hiddenColumns: [],
	paginationType: 'paged',
	dataLoading: false,
	size: 'small',
	rowHiglight: false
}

export default typedMemo(Table)
