import 'antd/dist/antd.css'
import { Button, styled, Typography, useTheme } from '@mui/material'
import { Box } from '@mui/system'
import { DatePicker } from 'antd'
import { RangePickerProps } from 'antd/lib/date-picker'
import 'antd/dist/antd.css'
import './styles.css'
import GlobalStyles from '@mui/material/GlobalStyles';
import { useId, useMemo } from 'react'
import moment from 'moment'
import InputLabel from './InputLabel'
import { DateRangeOutlined } from '@mui/icons-material'

const { RangePicker } = DatePicker

const StyledRangePicker = styled(Box)((theme) => ({
  '& .ant-picker-cell-in-view.ant-picker-cell-today .ant-picker-cell-inner::before': {
    backgroundColor: 'black',
    borderRadius: '50%'
  }
}))



type Props = {
  label: string,
  startDate?: string | null,
  endDate?: string | null
} & RangePickerProps

const DateRange = (props: Props) => {
  const theme = useTheme()
  const uuid = useId()
  const { label, onChange, startDate, endDate, ...restProps } = props

  const globalStyles = useMemo(() => ({
    // main outer container
    '.ant-picker.ant-picker-range.ant-picker-large': {
      height: 45,
      width: '100%',
      padding: theme.spacing(2, 1.5),
      borderRadius: theme.shape.borderRadius,
      borderWidth: 2
    },
    '.ant-picker-range-wrapper .ant-picker-panel-container': {
      display: 'flex',
      borderTopLeftRadius: parseInt(`${theme.shape.borderRadius}`) * 4,
      borderTopRightRadius: parseInt(`${theme.shape.borderRadius}`) * 4,
      fontFamily: theme.typography.fontFamily
    },
    '.ant-picker-range-wrapper .ant-picker-footer': {
      width: '150px',
      borderLeft: `solid 1px ${theme.palette.divider}`,
      borderBottom: `solid 1px ${theme.palette.divider}`
    },
    '.ant-picker-range-wrapper .ant-picker-footer .ant-picker-ranges': {
      display: 'grid',
      gridTemplateColumns: '1fr',
      paddingTop: theme.spacing(6),
      '&::before': {
        content: '"Predefined periods:"',
        color: theme.palette.grey['A400'],
        fontSize: '0.98em'
      }
    },
    '.ant-picker-range-wrapper .ant-picker-footer .ant-picker-ranges .ant-picker-preset .ant-tag': {
      color: theme.palette.text.primary,
      background: 'transparent',
      border: 'none',
      padding: 0,
      fontWeight: 600,
      '&:hover': {
        color: theme.palette.text.secondary
      }
    },
    'td.ant-picker-cell.ant-picker-cell-in-view': {
      width: 32,
      height: 32,
      padding: theme.spacing(0.8),
    },
    'td.ant-picker-cell .ant-picker-cell-inner': {
      width: 32,
      height: 32,
      display: 'flex',
      alignItems: 'center',
      justifyContent: 'center',
      fontWeight: 600
    },
    '.ant-picker-cell.ant-picker-cell-in-view.ant-picker-cell-today .ant-picker-cell-inner::before': {
      borderColor: theme.palette.primary.main,
      borderRadius: '50%'
    },
    '.ant-picker-cell.ant-picker-cell-in-view.ant-picker-cell-selected .ant-picker-cell-inner,\
     .ant-picker-cell-in-view.ant-picker-cell-range-start .ant-picker-cell-inner,\
     .ant-picker-cell-in-view.ant-picker-cell-range-end .ant-picker-cell-inner': {
      background: theme.palette.primary.main,
      borderRadius: '50%'
    },
    '.ant-picker-cell.ant-picker-cell-in-view.ant-picker-cell-range-end:not(.ant-picker-cell-range-start):is(.ant-picker-cell-range-end) .ant-picker-cell-inner,\
     .ant-picker-cell.ant-picker-cell-in-view.ant-picker-cell-selected.ant-picker-cell-range-start:not(.ant-picker-cell-range-end) .ant-picker-cell-inner,\
     .ant-picker-cell.ant-picker-cell-in-view.ant-picker-cell-range-start:not(.ant-picker-cell-range-end):is(.ant-picker-cell-range-start) .ant-picker-cell-inner': {
      background: theme.palette.primary.main,
      borderRadius: '50%'
    },
    '.ant-picker-cell.ant-picker-cell-in-view.ant-picker-cell-in-range::before': {
      height: 32,
      background: '#E6F2FF'
    },
    '.ant-picker-cell.ant-picker-cell-in-view.ant-picker-cell-range-start:not(.ant-picker-cell-range-start-single)::before,\
     .ant-picker-cell.ant-picker-cell-in-view.ant-picker-cell-range-end:not(.ant-picker-cell-range-end-single)::before': {
      height: 32,
      background: '#E6F2FF'
    },
    '.ant-picker-body .ant-picker-content th': {
      color: theme.palette.grey['A400'],
      fontWeight: 600
    },
    '.ant-picker-content tbody tr td:first-of-type::before': {
      borderTopLeftRadius: parseInt(`${theme.shape.borderRadius}`) * 2,
      borderBottomLeftRadius: parseInt(`${theme.shape.borderRadius}`) * 2
    },
    '.ant-picker-content tbody tr td:last-of-type::before': {
      borderTopRightRadius: parseInt(`${theme.shape.borderRadius}`) * 2,
      borderBottomRightRadius: parseInt(`${theme.shape.borderRadius}`) * 2
    },
    '.ant-picker.ant-picker-range.ant-picker-large.ant-picker-focused': {
      borderColor: theme.palette.primary.main,
      // boxShadow: `0 0 0 1px ${theme.palette.primary.main}`,
      boxShadow: `none`,
      paddingLeft: -1,
      paddingRight: -1
    },
    '.ant-picker.ant-picker-range .ant-picker-clear': {
      right: 42
    },
    '.ant-picker.ant-picker-range .ant-picker-suffix': {
      color: theme.palette.action.active
    },
    '.ant-picker-header .ant-picker-header-view': {
      fontWeight: 900
    }
  }), [theme])

  return (
    <>
      <GlobalStyles styles={globalStyles} />
      <Box>
        <InputLabel htmlFor={uuid}>{label}</InputLabel>
        <RangePicker
          id={uuid}
          size='large'
          placeholder={['Start Date', 'End Date']}
          ranges={{
            "Yesterday": [moment().subtract(1, 'days'), moment().subtract(1, 'days')],
            "Last week": [moment().subtract(1, 'weeks'), moment()],
            "Last month": [moment().subtract(1, 'months'),moment()],
            "Last year": [moment().subtract(1, 'years'), moment()]
          }}
          separator={null}
          suffixIcon={<DateRangeOutlined fontSize='small' />}
          renderExtraFooter={() => <ExtraFooter startDate={startDate} endDate={endDate} />}
          onChange={onChange}
          {...restProps}
        />
      </Box>
    </>
  )
}

const FooterContainer = styled(Box)(({ theme: { palette, shape, spacing } }) => ({
  width: '100%',
  height: 50,
  display: 'flex',
  alignItems: 'center',
  justifyContent: 'space-between',
  padding: spacing(1),
  position: 'absolute',
  bottom: -42,
  left: 0,
  background: palette.background.default,
  boxShadow: '0 2px 3px 0 rgba(0, 0, 0, 0.28)',
  borderBottomLeftRadius: parseInt(`${shape.borderRadius}`) * 4,
  borderBottomRightRadius: parseInt(`${shape.borderRadius}`) * 4
}))

const ExtraFooter = ({startDate, endDate}: { startDate?: string | null, endDate?: string | null}) => {
  return (
    <FooterContainer>
      <Button size='small'>Cancel</Button>
      <Box display='flex' alignItems='center'>
        { startDate && endDate && 
          <Box display='flex' mr={2}>
            <Typography variant='body2' sx={{ color: theme => theme.palette.text.mute }} >Selected:&nbsp;</Typography>
            <Typography variant='body2' fontWeight={600}>
              {moment(endDate).diff(moment(startDate), 'days') + 1}
            </Typography>
          </Box>
        }
        <Button variant='contained' size='small'>Done</Button>
      </Box>
    </FooterContainer>
  )
}

export default DateRange