export default function invertSelected(obj: Record<any, any> | undefined, id: number | null) {
  if(obj) {
    return obj.map((ele: any) => {
      if(ele.id == id){
        return { ...ele, isSelected: true }
      } else {
        return { ...ele, isSelected: false }
      }
    })
  } else{
    return undefined
  }
}