import React, { Dispatch, SetStateAction } from "react"
import { Typography, Box, Link } from "@mui/material"
import { SPaper, SAppIcon } from "./AppsMenu.styles"
import PopperMenu from "../PopperMenu"
import { Footer, RoundedContainer } from "../PopperMenu/PopperMenu.styles"
import { SettingsIcon } from "../../utils/Icons"
import { Link as RouterLink } from "react-router-dom"
import { IServiceProvider } from "../../layout/AppLayout/AppLayout.types"

type Props = {
	apps: IServiceProvider[] | undefined
	open: boolean
	anchorEl: HTMLElement | null
	setAnchorEl: Dispatch<SetStateAction<HTMLElement | null>>
	isLoading: boolean
}

const AppsMenu = (props: Props) => {
	const { anchorEl, setAnchorEl, open, isLoading, apps } = props
	const id = open ? "apps-menu" : undefined

	return (
		<PopperMenu
			id={id}
			open={open}
			anchorEl={anchorEl}
			setAnchorEl={setAnchorEl}
			isLoading={isLoading}
			WrapperComponent={RoundedContainer}
		>
			<>
				<SPaper>
					{apps?.map(
						({ id, name, display_name, is_accessible, image, host_url }) => (
							<SAppIcon
								key={name}
								target="_blank"
								href={`http://${host_url}`}
								underline="none"
								tabIndex={0}
							>
								<img
									style={{ maxWidth: "40%", flex: 1 }}
									src={`${process.env.REACT_APP_SERVER_URL}/${image}`}
									alt={name}
									aria-hidden={true}
								/>

								<Typography
									sx={{
										maxWidth: "100%",
										overflow: "hidden",
										whiteSpace: "nowrap",
										textOverflow: "ellipsis",
										textAlign: "center",
										color: "gray"
									}}
									color="inherit"
									variant="caption"
									fontWeight={900}
								>
									{display_name}
								</Typography>
							</SAppIcon>
						)
					)}
				</SPaper>
				{/* <Footer>
					<Link
						underline="none"
						component={RouterLink}
						to="manage-products/add-product"
						sx={({ palette, spacing }) => ({
							color: palette.text.primary,
							display: "flex",
							alignItems: "center",
							gap: spacing(0.5)
						})}
					>
						<SettingsIcon color="primary" />
						Manage Products
					</Link>
				</Footer> */}
			</>
		</PopperMenu>
	)
}

export default AppsMenu
