import { Box, Pagination, PaginationItem, Table } from "@mui/material"
import { styled } from "@mui/material/styles"
import TableBody from "@mui/material/TableBody"
import TableCell, { tableCellClasses } from "@mui/material/TableCell"
import TableRow from "@mui/material/TableRow"

const StyledTableContainer = styled(Table)(({ theme: { spacing } }) => ({
	borderCollapse: "separate",
	borderSpacing: `0 ${spacing(0.3)}`
}))

const StyledPaper = styled(Box)(({ theme: { shape } }) => ({}))

const StyledTableBody = styled(TableBody)(({ theme: { palette } }) => ({}))

const StyledTableCell = styled(TableCell)(({ theme: { shape, palette } }) => ({
	border: "none",
	[`&.${tableCellClasses.head}`]: {
		fontWeight: "bold",
		color: palette.text.mute,
		borderBottom: "2px solid lightgrey"
	}
	// [`&.${tableCellClasses.body}`]: {
	//   border: 'none'
	// },
}))

const StyledHeaderRow = styled(TableRow)(({ theme: { palette, spacing } }) => ({
	backgroundColor: palette.background.paper,
	border: `2px solid green`
}))

const StyledTableRow = styled(TableRow)(({ theme: { palette, spacing } }) => ({
	backgroundColor: palette.background.paper,
	// border: `solid ${spacing(1)} black`,
	"&:nth-of-type(even)": {
		backgroundColor: palette.action.hover
	}
}))

const PaginationContainer = styled(Box)(({ theme: { palette, spacing } }) => ({
	width: "100%",
	display: "flex",
	justifyContent: "space-between",
	alignItems: "center",
	border: `solid 1px ${palette.grey[300]}`,
	marginTop: spacing(1),
	padding: spacing(2),
	backgroundColor: palette.background.paper,
	"& > div": {}
}))

const StyledPagination = styled(Pagination)(() => ({}))

const StyledPaginationItem = styled(PaginationItem)(
	({ theme: { palette, shape } }) => ({
		height: 32,
		display: "flex",
		alignItems: "center",
		justifyContent: "center",
		borderRadius: parseInt(`${shape.borderRadius}`) / 2,
		border: `solid 1px ${palette.grey[200]}`
	})
)

export {
	StyledTableContainer,
	StyledTableBody,
	StyledTableCell,
	StyledHeaderRow,
	StyledTableRow,
	StyledPaper,
	PaginationContainer,
	StyledPagination,
	StyledPaginationItem
}
