import { MenuList } from "@mui/material"
import { styled, Theme as ITheme } from '@mui/system';

const Listbox = styled(MenuList)(
  ({ theme }: {theme: ITheme}) => `
  box-sizing: border-box;
  padding: 5px;
  list-style: none;
  height: auto;
  transition: opacity 0.1s ease;
  width: 100%;
  box-shadow: ${Array.isArray(theme.shadows) ? theme.shadows?.[1] : ''};
  background: ${theme.palette.mode === 'dark' ? theme.palette.grey[900] : '#fff'};
  border: 1px solid ${theme.palette.mode === 'dark' ? theme.palette.grey[800] : theme.palette.grey[200]};
  z-index: 1;
  outline: 0px;
  overflow: auto;
  `,
);

export default Listbox