import { Box, OutlinedInputProps } from "@mui/material";
import InputLabel from './InputLabel'
import InputBase from './InputBase'
import { ReactElement, useId } from "react";

export interface IFilterInputProps<T> extends Omit<OutlinedInputProps, 'ref'>{
  label?: string | ReactElement | null,
  value: T,
}

function FilterInput<T>(props: IFilterInputProps<T>) {
  const { label, endAdornment, ...restProps } = props
  const uuid = useId()

  return (
    <Box width='100%' display='flex' flexDirection='column' justifyContent='center'>
      { label && <InputLabel htmlFor={uuid}>{label}</InputLabel> }
      <InputBase id={uuid} {...restProps} endAdornment={endAdornment}/>
    </Box>
  )
}

export default FilterInput