import { Box, FormControlLabel, Stack, Link, Typography } from "@mui/material"
import { useCallback, useRef, useState } from "react"
import { Link as RouterLink, useNavigate, Navigate } from "react-router-dom"
import AuthContainer from "../../components/AuthContainer"
import StyledButton from "../../components/StyledButton"
import StyledSwitch from "../../components/StyledSwitch"
import {
	IFormData,
	ILoginResponse,
	IRedirectionResponse,
	ISendOTPFormData,
	ISendOTPProductsErrorResponse,
	ISendOTPProductsResponse,
	ISteps,
	IVerifyEmailError,
	IVerifyEmailResponse,
	IVerifyOTPFormData,
	IVerifyOTPResponse,
	TUseFormData
} from "./Login.types"
import useLocalStorage from "../../hooks/useLocalStorage"
import { useForm, FormProvider, useFieldArray } from "react-hook-form"
import { useMutation, UseMutationResult } from "react-query"
import { post } from "../../api/utils"
import { AxiosError, AxiosResponse } from "axios"
import EmailVerify from "../../components/AuthComponents/EmailVerify"
import LoginVerify from "../../components/AuthComponents/LoginVerify"
import { loginSchema } from "./LoginValidations"
import { yupResolver } from "@hookform/resolvers/yup"
import RedirectToSp from "../../components/AuthComponents/RedirectToSp"
import { useAuthContext } from "../../context/AuthContext/AuthContext"
import { DevTool } from "@hookform/devtools"
import FoundProducts from "../../components/AuthComponents/FoundProducts"
import jwt_decode, { JwtPayload } from "jwt-decode"
import OTPVerify from "../../components/AuthComponents/OTPVerify"
import { useAppContext } from "../../context/AppContext/AppContext"

function getProductsIds(apps: TUseFormData["appsData"]): string[] {
	//takes objects of array and return array of strings
	return apps.reduce((prev: any, curr: any) => {
		if (curr.selected) {
			return [...prev, String(curr.id)]
		}
		return prev
	}, [])
}

const Login = () => {
	//first Step is verify-email
	//case 1: email found in IDP =>Proceed to password screen.
	//case 2: email found in Sps => Proceed to migration screen
	const [step, setStep] = useState<ISteps>("email-verifier")
	const [redirectMeta, setRedirectMeta] = useState({
		redirect_url: "",
		saml_response: ""
	})
	let navigate = useNavigate()
	const auth = useAuthContext()
	const appState = useAppContext()

	let formRef = useRef<HTMLFormElement>(null)
	let { setLocalStorage } = useLocalStorage("access_token")
	let { setLocalStorage: setRefreshToken } = useLocalStorage("refresh_token")
	let { setLocalStorage: setRoleData } = useLocalStorage("user_info")

	const {
		register,
		handleSubmit,
		control,
		formState,
		setError,
		reset,
		getValues,
		...rest
	} = useForm<TUseFormData>({
		defaultValues: {
			email: "",
			password: "",
			appsData: [],
			otp: ""
		},
		context: {
			step
		},
		resolver: yupResolver(loginSchema)
	})
	const { fields, replace } = useFieldArray({
		control,
		name: "appsData",
		keyName: "fieldId"
	})

	const methods = {
		register,
		control,
		handleSubmit,
		setError,
		formState,
		reset,
		getValues,
		...rest
	}
	//Mutation Functions
	function emailMutation(formdata: Omit<IFormData, "password">) {
		return post<IVerifyEmailResponse>("email-verifier", formdata, {
			withCredentials: true
		})
	}

	function loginMutation(formdata: IFormData) {
		return post<ILoginResponse>("login", formdata, { withCredentials: true })
	}

	function sendOtpMutation(formdata: ISendOTPFormData) {
		return post<AxiosResponse<ISendOTPProductsResponse>>(
			"send-otp-products",
			formdata,
			{
				withCredentials: true
			}
		)
	}

	function verifyOTPMutation(formdata: IVerifyOTPFormData) {
		return post<AxiosResponse<IVerifyOTPResponse>>(
			"verify-otp-products",
			formdata,
			{
				withCredentials: true
			}
		)
	}

	const handleBackStep = useCallback(() => {
		setStep("email-products")
	}, [])
	//STEP 1 : email-verifier
	const {
		mutate,
		isLoading
	}: UseMutationResult<
		IVerifyEmailResponse,
		AxiosError<IVerifyEmailError>,
		Omit<IFormData, "password">
	> = useMutation<
		IVerifyEmailResponse,
		AxiosError<IVerifyEmailError>,
		Omit<IFormData, "password">
	>(emailMutation, {
		onSuccess: ({ data: response, status: resStatus }) => {
			const { message, verification } = response
			if (resStatus === 200 && verification && message === "success") {
				setStep("login")
			}
		},
		onError: err => {
			console.log({ err })
			const { response } = err
			if (!response) return
			const { data } = response
			const { message, statuscode, data: appsData } = data
			//for found product screen.
			if (statuscode == 308 && message == "redirection") {
				//setup for verification
				//setState of form.
				replace(appsData)
				setStep("email-products")
			} else {
				setError("email", { type: "server", message })
			}
		}
	})

	//STEP 2 : Login
	const {
		mutate: _mutate,
		isError: _isError,
		data: _data,
		error: _error,
		isLoading: _isLoading,
		status: _status
	}: UseMutationResult<ILoginResponse, AxiosError, IFormData> = useMutation<
		ILoginResponse,
		AxiosError<IRedirectionResponse | IVerifyEmailError>,
		IFormData
	>(loginMutation, {
		onSuccess: ({ data: response }) => {
			const { access_token, refresh_token, statuscode, roles } = response

			if (statuscode === 200) {
				//extract the email and roles and add to context
				const { sub } = jwt_decode<JwtPayload>(access_token)
				if (typeof sub == "string") {
					auth?.setUserInfo({ roles, email: sub })
					setRoleData(JSON.stringify({ roles, email: sub }))
				}
				auth?.signIn()
				setLocalStorage(access_token)
				setRefreshToken(refresh_token)
				navigate("/")
			}
		},
		onError: err => {
			//check for redirect request
			const { response } = err
			if (!response) {
				return
			}
			const { data } = response

			if (
				response.status === 307 &&
				"redirect_url" in data &&
				"saml_response" in data
			) {
				const { redirect_url, saml_response, access_token, refresh_token } =
					data

				setRedirectMeta({ redirect_url, saml_response })
				setLocalStorage(access_token)
				setRefreshToken(refresh_token)
				setStep("redirect")
				setError("password", { type: "server", message: data.message })
			} else if (response.status === 401) {
				setError("password", { type: "server", message: data.message })
			}
		}
	})

	//Step 3: email to otp generator
	const {
		mutate: __mutate,
		isLoading: __isLoading
	}: UseMutationResult<
		AxiosResponse<ISendOTPProductsResponse>,
		AxiosError<ISendOTPProductsErrorResponse>,
		ISendOTPFormData
	> = useMutation<
		AxiosResponse<ISendOTPProductsResponse>,
		AxiosError<ISendOTPProductsErrorResponse>,
		ISendOTPFormData
	>(sendOtpMutation, {
		onSuccess: ({ data: { expires, status_code } }) => {
			if (status_code == 200 && expires) {
				appState?.setAlert({
					show: true,
					message: "OTP successfully sent to email",
					status: "success"
				})
				setStep("otp-verifier")
			}
		},
		onError: err => {
			//For Network Error 406 500
			const { response } = err
			if (!response) {
				return
			}
			const { data } = response
			const { message, statuscode } = data
			if (statuscode == 406 && message) {
				setError("appsData", { type: "server", message })
			} else {
				setError("appsData", { type: "server", message: err.message })
			}
		}
	})

	//step 4: Last Step OTP Verify
	const {
		mutate: _otpMutate,
		isLoading: _otpIsLoading
	}: UseMutationResult<
		AxiosResponse<IVerifyOTPResponse>,
		AxiosError<ISendOTPProductsErrorResponse>,
		IVerifyOTPFormData
	> = useMutation<
		AxiosResponse<IVerifyOTPResponse>,
		AxiosError<ISendOTPProductsErrorResponse>,
		IVerifyOTPFormData
	>(verifyOTPMutation, {
		onSuccess: res => {
			const { data } = res
			const { message, statuscode } = data
			//show something to the user and reset the form.
			if (statuscode === 200 && message) {
				appState?.setAlert({
					show: true,
					message: message,
					status: "success"
				})
				//reset the whole form and redirect to sign-in page again.
				reset({ email: "", password: "", appsData: [], otp: "" })
				navigate("/")
			}
		},
		onError: err => {
			console.log(err)
			const { response } = err
			if (!response) {
				return
			}
			const { data } = response
			const { message, statuscode } = data
			if (statuscode == 400 && message) {
				setError("otp", { type: "server", message })
			} else {
				setError("otp", { type: "server", message: err.message })
			}
		}
	})

	//Call on every button click
	const onSubmit = handleSubmit(submittedData => {
		const { email, appsData, otp, password } = submittedData
		if (step === "email-verifier") {
			mutate({ email })
		}
		if (step === "login") {
			_mutate({ email, password })
		}
		if (step === "email-products") {
			//filter and map the selected apps
			const selected_products = getProductsIds(appsData)
			//to generate otp.
			__mutate({ email, selected_products })
		}
		if (step === "otp-verifier") {
			const selected_products = getProductsIds(appsData)
			_otpMutate({ email, selected_products, otp })
		}
	})

	const handleResendCode = () => {
		const { appsData, email } = getValues()
		//Resend OTP and clear otp field in form.
		const selected_products = getProductsIds(appsData)
		if (email && selected_products.length > 0) {
			__mutate({ email, selected_products })
		}
	}

	if (auth?.authed) return <Navigate to="/" />
	return (
		<FormProvider {...methods}>
			<DevTool control={control} />
			<form
				style={{ height: "100%", display: "flex", alignItems: "center" }}
				ref={formRef}
				{...(redirectMeta?.redirect_url && {
					action: redirectMeta?.redirect_url,
					method: "POST"
				})}
				{...(step !== "redirect" && { onSubmit: onSubmit })}
			>
				<AuthContainer
					loading={isLoading || _isLoading || __isLoading || _otpIsLoading}
				>
					<Stack spacing={2}>
						{step === "email-verifier" && <EmailVerify />}
						{step == "login" && <LoginVerify />}
						{step == "redirect" && (
							<RedirectToSp
								redirect_url={redirectMeta.redirect_url}
								saml_response={redirectMeta.saml_response}
								formRef={formRef}
							/>
						)}
						{/* Products will be displayed on OTP Step Too */}
						{["email-products", "otp-verifier"].includes(step) && (
							<FoundProducts
								fields={fields}
								handleBackStep={handleBackStep}
								disabled={step == "otp-verifier" || __isLoading}
							/>
						)}
						{step === "otp-verifier" && (
							<OTPVerify handleResendCode={handleResendCode} />
						)}

						{/* Area for Buttons */}
						{step == "login" && (
							<Box
								sx={{
									display: "flex",
									alignItems: "center",
									justifyContent: "space-between"
								}}
							>
								<FormControlLabel
									sx={{
										marginLeft: 0,
										gap: "0.5rem"
									}}
									control={<StyledSwitch defaultChecked />}
									label="Stay signed in"
								/>
								<Link
									component={RouterLink}
									color="primary"
									underline="none"
									variant="body1"
									to="/forgot-password"
								>
									Forgot password?
								</Link>
							</Box>
						)}

						{step !== "redirect" && (
							<Box sx={{ display: "flex", width: "100%" }}>
								<StyledButton
									type="submit"
									disabled={
										isLoading || _isLoading || __isLoading || _otpIsLoading
									}
									sx={{ marginRight: "auto" }}
									variant="contained"
								>
									{step === "otp-verifier" ? "Verfiy" : "Continue"}
								</StyledButton>
							</Box>
						)}
					</Stack>
				</AuthContainer>
			</form>
		</FormProvider>
	)
}
export default Login
