import {
	Box,
	Button,
	Card,
	CardHeader,
	Container,
	Divider,
	LinearProgress,
	Skeleton,
	Stack,
	Switch,
	TextField,
	Typography
} from "@mui/material"
import { SBox, SCardActions, SCardContent } from "./index.styles"
import ProductList from "../../components/ProductList/ProductList"
import { IAddUser } from "./AddUser.types"
import { useUserContext } from "../../context/UserContext/UserContext"
import ErrorInfo from "../../components/ErrorInfo"
import { useNavigate } from "react-router-dom"
import { DataLoadError } from "../../components/ErrorScreens"
import FilterInput from "../../components/FilterInput/FilterInput"

const AddUser = (props: IAddUser) => {
	const { editMode } = props
	const navigate = useNavigate()

	const appData = useUserContext()

	const productList = appData?.appDataLoading ? (
		<Box width="100%" display="flex" flexDirection="column">
			{new Array(6).fill(0).map((_ele, idx) => (
				<Skeleton key={idx} height={100} sx={{ margin: -2 }} />
			))}
		</Box>
	) : (
		appData?.appData?.map((app: any, index: any) => {
			return (
				<ProductList
					key={app.id}
					app={app}
					index={index}
					Logo={app.sp_app_image}
					selected={app.selected}
					disabled={/* !app.selected ||  */appData?.disableAll}
					errors={appData?.errors?.apps?.[index]}
				/>
			)
		})
	)

	return (
		<Box sx={{ maxWidth: 800, margin: "auto" }}>
			<Typography
				variant="h5"
				component="h1"
				color="primary"
				fontWeight={"bold"}
				mb={2}
			>
				{editMode ? "Edit User" : "Add New User"}
			</Typography>

			{appData?.dataLoadError ? (
				<DataLoadError />
			) : (
				<>
					<SCardContent>
						<Stack
							direction={{ xs: "column", sm: "column", md: "row" }}
							spacing={{ xs: 1, sm: 2, md: 3 }}
						>
							<FilterInput
								label={
									<Stack direction="row" alignItems="center" spacing={1}>
										First Name&nbsp;&nbsp;
										<ErrorInfo
											show={appData?.errors?.firstname}
											messages={appData?.errors?.firstname}
											fontSize="small"
										/>
									</Stack>
								}
								sx={{ flex: 4 }}
								fullWidth
								placeholder="First Name"
								value={appData?.userInfo?.firstname}
								onChange={e =>
									appData?.setUserInfo({
										...appData?.userInfo,
										firstname: e.target.value
									})
								}
								disabled={appData?.disableAll}
							/>

							<FilterInput
								label={
									<Stack direction="row" alignItems="center" spacing={1}>
										Last Name&nbsp;&nbsp;
										<ErrorInfo
											show={appData?.errors?.lastname}
											messages={appData?.errors?.lastname}
											fontSize="small"
										/>
									</Stack>
								}
								sx={{ flex: 4 }}
								fullWidth
								placeholder="Last Name"
								value={appData?.userInfo?.lastname}
								onChange={e =>
									appData?.setUserInfo({
										...appData?.userInfo,
										lastname: e.target.value
									})
								}
								disabled={appData?.disableAll}
							/>
							<FilterInput
								label={
									<Stack direction="row" alignItems="center" spacing={1}>
										Email&nbsp;&nbsp;
										<ErrorInfo
											show={appData?.errors?.email}
											messages={appData?.errors?.email}
											fontSize="small"
										/>
									</Stack>
								}
								sx={{ flex: 4 }}
								fullWidth
								placeholder="username@email.com"
								value={appData?.userInfo?.email}
								onChange={e =>
									appData?.setUserInfo({
										...appData?.userInfo,
										email: e.target.value
									})
								}
								disabled={appData?.disableAll || editMode}
							/>
						</Stack>

						{editMode && (
							<SBox
								{...(appData?.errors?.is_active && { bgcolor: "error.A100" })}
							>
								<Typography
									variant="caption"
									fontWeight="bold"
									sx={{ flex: 1 }}
								>
									Status
								</Typography>
								<Stack direction="row" spacing={1} alignItems="center" flex={4}>
									<Typography variant="caption">Inactive</Typography>
									<Switch
										checked={appData?.userInfo?.is_active}
										onChange={(_e, checked) =>
											appData?.setUserInfo({
												...appData?.userInfo,
												is_active: checked
											})
										}
										inputProps={{ "aria-label": "user status" }}
										disabled={appData?.disableAll}
									/>
									<Typography variant="caption">Active</Typography>
								</Stack>
							</SBox>
						)}
						<Box display="flex" flexDirection="column">
							<Stack direction="row" spacing={2} mb={2} sx={{ flex: 1 }}>
								<Typography variant="body1" component="h2" fontWeight="bold">
									Products
								</Typography>
								<ErrorInfo
									show={typeof appData?.errors?.apps?.[0] === "string"}
									messages={appData?.errors?.apps}
									fontSize="small"
								/>
							</Stack>
							<Stack direction="column" spacing={1} justifyContent="flex-start">
								{productList}
							</Stack>
						</Box>
					</SCardContent>
					<Stack direction="row" spacing={2} mt={5}>
						<Button
							variant="outlined"
							onClick={() => navigate("/user-management")}
						>
							Cancel
						</Button>
						{!appData?.disableAll && (
							<Button
								variant="contained"
								onClick={appData?.addUser}
								disabled={appData?.appDataLoading}
								size="small"
							>
								Save
							</Button>
						)}
						{appData?.disableAll && !appData.appDataLoading && (
							<Button
								variant="contained"
								onClick={appData?.addNew}
								size="small"
							>
								Add Another
							</Button>
						)}
					</Stack>
				</>
			)}
			{appData?.isCreatingUser && (
				<Box mt={2}>
					<LinearProgress />
				</Box>
			)}
		</Box>
	)
}

export default AddUser
