import * as yup from "yup"
import { IUserType, IUserData, TValidationErrors, IAppData } from "./UserContext.types"
import { object, number, string, array } from 'yup';
import flatToNested from "../../utils/FlatToNested";

type RecursivePartial<T> = {
  [P in keyof T]?: RecursivePartial<T[P]>;
};

export const validationSchema: yup.SchemaOf<RecursivePartial<IUserData>, any> = object({
  firstname: string().defined("First name is required").trim().required("First name must be defined"),
  lastname: string().defined("Last name is required").trim().required("Last name is required"),
  email: string().defined("Email is required").required("Email is required").email(),
  type_of_user: new yup.StringSchema<IUserType>().required("User type required"),
  dr_iq_gender_id: number().nullable().when('$selectedApps', (selectedApps, schema) => {
    const appFound = !!selectedApps.find((app: any) => app.name == 'dr-iq')
    return appFound ? schema.required("Gender required") : schema
  }),
  apps: array().of(object({
    id: number().required("ID is required"),
    practices: array().of(object({
      id: yup.number().required("ID is required")
    })).test(
      "min1",
      "Must have atleast one practice",
      (value, {parent , options}) => {
        const appId = options?.context?.selectedApps?.find((app: any) => app.name == 'ez-login')?.id

        if(parent.id !== appId && Array.isArray(value) && value.length < 1) {
          return false
        }
        return true;
      }
    ),
    role: object({
      id: number().nullable().required("Role is required"),
      sub_role: number().nullable()
    })
  }).nullable())
  .required().min(1, "Atleast one app should be selected")
  .test('atleast-one-app-selected',
  'Atleast one app should be selected',
  (value) => {
    return !!(value && value?.filter(val => val)?.length > 0)
  })
  /* .test('has-another-app-when-ez-doc',
  'You must select another app',
  (value, context) => {
    const appName = context?.appData?.find((ele) => ele.id === value.id)
    return appData === 'ez-login' ? schema.min(2) : schema.min(1);
  }), */
})

export async function validate(user: IUserData, appData: IAppData[] | undefined): Promise<TValidationErrors> {
  const valErrors: TValidationErrors = {}

  try{
    await validationSchema.validate(user, {
      abortEarly: false,
      recursive: true,
      context: {
        selectedApps: appData?.filter(app => app.selected)
      }
    })
    // const result = await validationSchema.
  } catch(err: any/* yup.ValidationError */) {
    err?.inner?.forEach((innerError: any) => {
      flatToNested(innerError.path, innerError.errors, valErrors)
    })

    if(valErrors.dr_iq_gender_id) {
      const drIQindex = appData?.findIndex(app => app.name === 'dr-iq' )
      if(drIQindex !== undefined && isFinite(drIQindex)) {
        if(!valErrors.apps) {
          valErrors.apps = []
        }
        valErrors.apps[drIQindex] = {...valErrors.apps[drIQindex] || {}}
        valErrors.apps[drIQindex].dr_iq_gender_id = valErrors.dr_iq_gender_id
      }
    }
  }

  return valErrors
}