import { BorderBottom } from "@mui/icons-material"
import {
	Avatar,
	Box,
	Breadcrumbs,
	Button,
	Chip,
	ClickAwayListener,
	Container,
	Grid,
	IconButton,
	LinearProgress,
	Link as MuiLink,
	Menu,
	MenuItem,
	Popper,
	Stack,
	Tooltip,
	Typography
} from "@mui/material"
import TrapFocus from "@mui/base/TrapFocus"
import React, { MouseEvent } from "react"
import { ReactComponent as Logo } from "../../assets/svgs/main.svg"
import AppsIcon from "@mui/icons-material/Apps"
import {
	SBox,
	SHeaderAppBar,
	SHeaderToolbar,
	SLogoBox,
	SMenu
} from "./index.style"
import { ExpandMoreIcon, HelpIcon } from "../../utils/Icons"
import { stringAvatar } from "../../utils/AvatarUtill"
import getBreadCrumbs from "../../utils/getBreadCrumbs"
import {
	useLocation,
	Link,
	useNavigate,
	useParams,
	useMatch
} from "react-router-dom"
import { useAuthContext } from "../../context/AuthContext/AuthContext"
import { useQuery, useQueryClient, UseQueryResult } from "react-query"
import { AxiosError, AxiosResponse } from "axios"
import { get } from "../../api/utils"
import { IGetUserInfo } from "../../pages/Profile/Profile.types"
import { getProfile } from "../../pages/Profile/Profile"
import HelpOutlineIcon from "@mui/icons-material/HelpOutline"
import Loader from "../../pages/Loader"
import HelpMenu from "../../components/HelpMenu"
import AppsMenu from "../../components/AppsMenu"
import ProfileMenu from "../../components/ProfileMenu"
import { IGetServiceProvidersData, IServiceProvider } from "./AppLayout.types"

function getServiceProviders() {
	return get("service-providers")
}

const AppNavbar = () => {
	const queryClient = useQueryClient()
	const [anchorElNav, setAnchorElNav] = React.useState(null)
	const [anchorElUser, setAnchorElUser] = React.useState<null | HTMLElement>(
		null
	)
	const [anchorElApps, setAnchorElApps] = React.useState<null | HTMLElement>(
		null
	)
	const [anchorElHelp, setAnchorElHelp] = React.useState<null | HTMLElement>(
		null
	)
	const location = useLocation()
	const navigate = useNavigate()
	const auth = useAuthContext()
	const open = Boolean(anchorElApps)
	const openProfile = Boolean(anchorElUser)

	const {
		data: result,
		isLoading,
		isError,
		error
	}: UseQueryResult<
		AxiosResponse<IGetServiceProvidersData>,
		AxiosError
	> = useQuery("splist", getServiceProviders, {
		refetchOnWindowFocus: false
		// enabled: open
	})

	const {
		data: profileData,
		isLoading: _isLoading,
		isFetching: _isFetching,
		isError: _isError,
		error: _error
	}: UseQueryResult<AxiosResponse<IGetUserInfo>, AxiosError> = useQuery(
		"profile",
		getProfile,
		{
			refetchOnWindowFocus: false
		}
	)

	const profile_image = profileData?.data.user_info?.profile_image
	const first_name = profileData?.data.user_info?.first_name
	const last_name = profileData?.data.user_info?.last_name
	const email = profileData?.data.user_info?.email
	let fullName = ". ."
	if (first_name || last_name) {
		fullName = `${first_name} ${last_name}`
	}
	const serviceproviders = result?.data?.serviceproviders

	const handleOpenNavMenu = (event: any) => {
		setAnchorElNav(event.currentTarget)
	}
	const handleOpenUserMenu = (event: any) => {
		setAnchorElUser(event.currentTarget)
	}

	const handleCloseUserMenu = () => {
		setAnchorElUser(null)
	}

	const handleClickApps = (e: MouseEvent<HTMLElement>) => {
		setAnchorElApps(anchorElApps ? null : e.currentTarget)
		e.currentTarget?.focus()
	}

	const handleClickHelp = (e: MouseEvent<HTMLElement>) => {
		setAnchorElHelp(anchorElHelp ? null : e.currentTarget)
		e.currentTarget?.focus()
	}

	const logout = async () => {
		if (auth) {
			const res = await auth.logOut()
			if (res.status == 200) {
				queryClient.removeQueries()
			}
		}
	}

	const breadCrumbs = getBreadCrumbs()
	return (
		<Grid item>
			<SHeaderAppBar position="static" color="inherit" elevation={0}>
				<SHeaderToolbar disableGutters>
					<SLogoBox onClick={() => navigate("/")} />
					<SBox display="flex">
						<Stack direction="row" spacing={2}>
							<IconButton
								aria-label="Help Menu"
								aria-expanded={!!anchorElHelp}
								onClick={handleClickHelp}
							>
								<HelpIcon fontSize="large" color="action" />
							</IconButton>

							<IconButton
								aria-label="Apps Menu"
								aria-expanded={!!anchorElApps}
								onClick={handleClickApps}
							>
								<AppsIcon fontSize="large" color="action" />
							</IconButton>

							<Tooltip title="Open settings">
								<IconButton onClick={handleOpenUserMenu} /* sx={{ p: 0 }} */>
									{profile_image ? (
										<Avatar
											src={`${process.env.REACT_APP_SERVER_URL}/user/profile-image/${profile_image}`}
										/>
									) : (
										<Avatar />
									)}
								</IconButton>
							</Tooltip>
						</Stack>
					</SBox>
				</SHeaderToolbar>
			</SHeaderAppBar>
			{breadCrumbs.length > 0 && (
				<Box
					width="100%"
					pl={2.5}
					sx={theme => ({ bgcolor: theme.palette.grey["A700"] })}
				>
					<Container maxWidth="xl">
						<Breadcrumbs aria-label="breadcrumb">
							{breadCrumbs.map((crumb, idx) => {
								if (crumb.link) {
									return (
										<MuiLink
											key={idx}
											component={Link}
											to={crumb.link}
											underline="hover"
											color="primary"
										>
											{crumb.label}
										</MuiLink>
									)
								} else {
									return (
										<Typography key={idx} color="text.primary">
											{crumb.label}
										</Typography>
									)
								}
							})}
						</Breadcrumbs>
					</Container>
				</Box>
			)}
			<HelpMenu
				open={!!anchorElHelp}
				anchorEl={anchorElHelp}
				setAnchorEl={setAnchorElHelp}
			/>
			<AppsMenu
				apps={serviceproviders}
				open={!!anchorElApps}
				anchorEl={anchorElApps}
				setAnchorEl={setAnchorElApps}
				isLoading={isLoading}
			/>
			<ProfileMenu
				open={openProfile}
				anchorEl={anchorElUser}
				setAnchorEl={setAnchorElUser}
				isLoading={_isLoading}
				userInfo={{
					fullName,
					email,
					image: profile_image
				}}
				logout={logout}
			/>
		</Grid>
	)
}

export default AppNavbar
