import React, { useState } from "react"
import { IconButton, InputAdornment, Typography } from "@mui/material"
import { useFormContext } from "react-hook-form"
import ScrollableStack from "../../layout/ScrollableStack"
import { VisibilityOff, Visibility } from "@mui/icons-material"
import StyledInput from "../StyledInput"
import { TextFieldStyled } from "../../pages/Login/index.styles"
import { IEmailVerfiy } from "./Authcomponents.types"
import FormInput from "../FormInput/FormInput"

const LoginVerify = () => {
	const { register } = useFormContext()
	const [showPassword, setShowPassword] = useState(false)

	const handleButton = () => {
		setShowPassword(!showPassword)
	}

	return (
		<>
			<Typography variant="h5" fontWeight={600}>
				SIGN IN
			</Typography>
			<ScrollableStack spacing={2}>
				<Typography variant="body1">Continue to Ez login</Typography>
				<TextFieldStyled
					{...register("email")}
					placeholder="Email"
					type="email"
					fullWidth
					variant="outlined"
					label="Email"
					// error={isError}
					// helperText={isError && error && <InputStatus text={error?.message} />}
				/>

				<FormInput
					label="Password"
					name="password"
					variant="outlined"
					type={showPassword ? "text" : "password"}
					placeholder="Password"
					fullWidth
					InputProps={{
						endAdornment: (
							<InputAdornment position="end">
								<IconButton
									onClick={handleButton}
									aria-label="toggle password visibility"
									edge="end"
								>
									{showPassword ? <VisibilityOff /> : <Visibility />}
								</IconButton>
							</InputAdornment>
						)
					}}
				/>
				{/* <TextField
					type={showPassword ? "text" : "password"}
					{...register("password")}
					placeholder="Password"
					fullWidth
					error={!!passwordError}
					// helperText={
					// 	(!!passwordError || (isError && error)) && (
					// 		<InputStatus text={passwordError || error?.message} />
					// 	)
					// }
					helperText={!!passwordError && <InputStatus text={passwordError} />}
					InputProps={{
						endAdornment: (
							<InputAdornment position="end">
								<IconButton
									onClick={handleButton}
									aria-label="toggle password visibility"
									edge="end"
								>
									{showPassword ? <VisibilityOff /> : <Visibility />}
								</IconButton>
							</InputAdornment>
						)
					}}
				/> */}
			</ScrollableStack>
		</>
	)
}

export default LoginVerify
