import { CircularProgress } from "@mui/material";
import { PropsWithChildren } from "react";
import { FullScreenLoader } from "./index.styles";

function Loader({children}: PropsWithChildren<any>) {
  return (
    <FullScreenLoader>
      <CircularProgress color="info" />
      {children}
    </FullScreenLoader>
  );
}

export default Loader;
