import React from'react'
import { useRef, useState, useMemo, Children } from 'react';
import { useSelect, ClickAwayListener, UseSelectMultiParameters } from '@mui/base';
import { Box, styled, Theme as ITheme } from '@mui/system';
import InputBox from './InputBox'
import InputLabel from './InputLabel';
import { MenuList, MenuItem, Popper, SxProps, CircularProgress, LinearProgress } from '@mui/material';
import Listbox from './SelectListbox'
import RoundBox from './RoundBox';
import PopperMenu from '../PopperMenu';

interface ChildProps {
  label: string,
  value: string | number
}

export interface MultiSelectProps {
  label?: string,
  value: string[],
  isLoading?: boolean,
  disabled?: boolean,
  onChange: UseSelectMultiParameters<string>["onChange"];
  children: React.ReactElement<ChildProps>[] | React.ReactElement<ChildProps>
  InputComponent?: React.ReactElement<any>
  sx?: SxProps<ITheme>
}

const MultiSelect = (props: MultiSelectProps) => {
  const { label, children, onChange, value, InputComponent, sx, isLoading, disabled, ...restProps } = props
  const inputRef = useRef<HTMLButtonElement | null>(null)
  const [listboxVisible, setListboxVisible] = useState<HTMLElement | null>(null);

  const options = useMemo(() => 
    Children.map(children, (child) => {
      if(!child.props.value || !child.props.label) {
        return null
      }
      return { label: child.props.label, value: child.props.value.toString() }
    }).filter(opt => opt),
    [children]
  )

  const menuListSelect = (e: KeyboardEvent, fn: Function) => {
    const code = e.code

    if (code === 'Enter') {
      fn(e)
    }
  }

  const { getButtonProps, getListboxProps, getOptionProps, value : hookValue, open } = useSelect({
    // listboxRef,
    multiple: true,
    options: options,
    open: !!listboxVisible,
    onChange: onChange,
    defaultValue: [],
    ...(value ? {value} : {})
  });

  return (
    // <ClickAwayListener onClickAway={() => setListboxVisible(false)}>
    <>
      <Box position='relative' sx={sx}>
        { label && <InputLabel>{label}</InputLabel> }
        { InputComponent ?
          React.cloneElement(InputComponent, {
            value: hookValue,
            onClick: () => setListboxVisible(inputRef.current),
            ref: inputRef
          })
          : <InputBox
            {...getButtonProps()}
            value={hookValue}
            onClick={() => setListboxVisible(inputRef.current)}
            ref={inputRef}
            disabled={disabled}
          />
        }
        { isLoading && <LinearProgress sx={{ position: 'absolute', bottom: 0, left: 0, width: '100%' }} /> }
      </Box>
      <PopperMenu open={open} anchorEl={inputRef.current} setAnchorEl={setListboxVisible}>
        <Listbox {...getListboxProps()} style={{ width: inputRef?.current?.clientWidth }}>
          {options.map((option) => {
            const optionProps = getOptionProps({ value: option.value.toString(), label: option.label })

            if(!option.value || !option.label) {
              return null
            }
            return (
              <MenuItem
                key={option.value}
                onKeyUp={(e: any) => menuListSelect(e, optionProps.onClick)}
                className={optionProps["aria-selected"] ? 'Mui-selected' : ''}
                {...optionProps}
              >
                {option.label}
              </MenuItem>
            )
          })}
        </Listbox>
      </PopperMenu>
    </>
  )
}

export default MultiSelect