import { StackStyled } from "./index.styles";
import { IScrollableStack } from "./ScrollableStack.types";

const ScrollableStack = ({ spacing = 1, children }: IScrollableStack) => {
  return (
    <StackStyled direction="column" alignItems="flex-start" spacing={spacing}>
      {children}
    </StackStyled>
  );
};

export default ScrollableStack;
