import { Box, Paper, styled } from "@mui/material";

export const RoundedContainer = styled(Paper)(({ theme: { spacing, shape, palette, breakpoints } }) => ({
  position: 'relative',
	borderRadius: +shape.borderRadius * 7,
	border: `1px solid ${palette.secondary["A200"]}`,
	overflow: 'hidden',
  [breakpoints.between('xs', 'sm')]: {
    maxWidth: 320,
  }
}))

export const RoundedContainer2 = styled(Paper)(({ theme }) => ({
  position: 'relative',
  overflow: 'hidden',
  width: '100%',
  border: `1px solid ${theme.palette.divider}`,
  borderRadius: +theme.shape.borderRadius * 7,
  padding: theme.spacing(0, 4),
  display: 'flex',
  flexDirection: 'column',
  justifyContent: 'center',
  alignItems: 'center',
  paddingBottom: theme.spacing(9),
  [theme.breakpoints.between('xs', 'sm')]: {
    maxWidth: 320,
  }
}))

export const Footer= styled(Box)(({ theme }) => ({
  border: 'none',
  width: '100%',
  display: 'flex',
  justifyContent: 'center',
  background: theme.palette.background.backgroundGradientSingle?.('45deg'),
  marginTop: theme.spacing(2),
  paddingTop: theme.spacing(1.5),
  paddingBottom: theme.spacing(1.5),
  color: theme.palette.text.primary
}))