import { Typography, Skeleton, Tooltip, Zoom, Box } from "@mui/material"
import { useQuery, UseQueryResult } from "react-query"
import { FullWidthBoxStyled } from "../../layout/ScrollableStack/index.styles"
import ImageCard from "../../components/ImageCard"

import { IProductContainer } from "./ProductContainer.types"
import { get } from "../../api/utils"
import { AxiosError, AxiosResponse } from "axios"
import { DataLoadError } from "../ErrorScreens"
import { SCard } from "../ImageCard/index.styles"
import { SGridBox } from "./index.styles"
import { LockIcon } from "../../utils/Icons"
import { PropsWithChildren } from "react"
import { Link } from "react-router-dom"

function getServiceProviders() {
	return get("user/selected/apps")
}

export interface IServiceProvider {
	host_url: string
	id: number
	logo_url: string
	is_selected: boolean
	sp_app_name: string
	sp_app_image: string
	name: string
}

export interface IGetServiceProvidersData {
	message: "success"
	selected_unselected_sp_apps: IServiceProvider[]
	statuscode: number
}

const ProductContainer = ({ isBordered = false }: { isBordered?: boolean }) => {
	const {
		data: result,
		isLoading,
		isError,
		error
	}: UseQueryResult<
		AxiosResponse<IGetServiceProvidersData>,
		AxiosError
	> = useQuery("userSplist", getServiceProviders)

	const serviceproviders = result?.data?.selected_unselected_sp_apps

	const ServiceBox = ({ host, selected, children }: PropsWithChildren<{ host: IServiceProvider['host_url'], selected: IServiceProvider['is_selected'] }>) => {
		if (selected) {
			return (
				<a target="_blank" href={`http://${host}`}>{children}</a>
			)
		} else {
			return (
				<Link to='' style={{ position: 'relative', pointerEvents: 'none' }}>{children}</Link>
			)
		}
	}

	if (isLoading)
		return (
			<SGridBox>
				{Array(7)
					.fill(true)
					.map((val, idx) => (
						<Skeleton key={idx} variant="rectangular">
							<SCard key={idx} disabled={true}></SCard>
						</Skeleton>
					))}
			</SGridBox>
		)

	if (isError) return <DataLoadError />
	if (!serviceproviders?.length)
		return (
			<Typography textAlign="center" variant="body1">
				No Product available
			</Typography>
		)
	return (
		<SGridBox>
			{serviceproviders?.map(
				({ id, name, sp_app_name, is_selected, logo_url, host_url }) => (
					<Tooltip
						title={
							!is_selected
								? `Contact support to unlock ${name}`
								: `Go to ${name}`
						}
						placement="top"
						TransitionComponent={Zoom}
						key={sp_app_name}
					>
						<Box>
							<ServiceBox host={host_url} selected={is_selected}>
								<Box
									sx={{
										position: "absolute",
										top: 15,
										right: 15,
									}}
								>
									{!is_selected && <LockIcon color="primary" fontSize="small" />}
								</Box>
								<SCard
									key={id}
									name={name}
									disabled={!is_selected}
									isBordered={isBordered}
								>
									<ImageCard icon={logo_url} disabled={!is_selected} name={name} />
									<Typography fontWeight="bold" variant="body2">
										{sp_app_name}
									</Typography>
								</SCard>
							</ServiceBox>
						</Box>
					</Tooltip>
				)
			)}
		</SGridBox>
	)
}

export default ProductContainer
