import React from "react"
import { IIconContainer } from "./IconContainer.types"
import { SBOX } from "./index.styles"
import CheckCircleIcon from '@mui/icons-material/CheckCircle';
import { Box } from "@mui/system";

const IconContainer = ({
	icon: Icon,
	isActive,
	children,
	width,
	disabled
}: IIconContainer) => {
	return (
		<SBOX width={width} isActive={isActive} disabled={disabled}>
			{/* <img
        style={{ maxWidth: "100%" }}
        src={icon ?? "https://picsum.photos/200"}
        alt="logo"
      /> */}
			<Box display="flex" alignItems="center" sx={theme => ({ fontSize: 16, width:25, color: disabled ? theme.palette.action.disabled : theme.palette.primary['A400'] })}>
				{ isActive && <CheckCircleIcon color="inherit" fontSize="inherit" /> }
			</Box>
			<Box display="flex" alignItems="center" width="100%" sx={() => ({
				display: "inline-block",
				position: "relative",
				width: "100%",
				// paddingBottom: "30%", 
				verticalAlign: "middle", 
				overflow: "hidden",

				height: "100%",
				'& svg': {
					display: "inline-block",
					position: "absolute",
					top: 0,
					left: 0,
				}
			}) }>
				{children}
			</Box>
		</SBOX>
	)
}

export default IconContainer
