import React, { Dispatch, SetStateAction } from "react"
import {
	Link as MuiLink,
	Typography,
	Avatar,
	Chip,
	Button,
	Link
} from "@mui/material"
import { useNavigate } from "react-router-dom"
import { SLinkBox, SProfileBox, SProfilePaper } from "./ProfileMenu.styles"
import PopperMenu from "../PopperMenu"
import { Footer, RoundedContainer } from "../PopperMenu/PopperMenu.styles"
import RenderIf from "../RenderIf/RenderIf"

const settings = [
	"User Management",
	"Audit Trail",
	"Permission Access Requests"
]

type Props = {
	open: boolean
	anchorEl: HTMLElement | null
	setAnchorEl: Dispatch<SetStateAction<HTMLElement | null>>
	isLoading: boolean
	userInfo: {
		fullName: string | undefined
		email: string | undefined
		image: string | undefined
	}
	logout: () => void
}

const ProfileMenu = (props: Props) => {
	const navigate = useNavigate()
	const { anchorEl, setAnchorEl, open, isLoading, userInfo, logout } = props
	const { fullName, email, image } = userInfo
	const ProfileId = open ? "profile" : undefined

	const handleProfileClick = () => {
		navigate("profile")
	}

	const handleUserMenuClick = async (setting: string) => {
		switch (setting) {
			case "Profile": {
				navigate("/profile")
				break
			}
			case "Home": {
				navigate("/")
				break
			}
			case "User Management": {
				navigate("/user-management")
				break
			}
			case "Audit Trail": {
				navigate("audit-trail")
				break
			}
			case "Logout": {
				logout()
				break
			}
		}
		setAnchorEl(null)
	}

	return (
		<PopperMenu
			id={ProfileId}
			open={open}
			anchorEl={anchorEl}
			setAnchorEl={setAnchorEl}
			isLoading={isLoading}
			WrapperComponent={RoundedContainer}
			PopperProps={{
				placement: "bottom-start"
			}}
		>
			<>
				<SProfilePaper>
					<SProfileBox>
						<Avatar
							sx={{ width: "111px", height: "111px" }}
							src={`${process.env.REACT_APP_SERVER_URL}/user/profile-image/${image}`}
						/>
						<Typography variant="subtitle1">{fullName}</Typography>
						<Typography color="text.mute">{email}</Typography>
						<Chip
							sx={({ spacing }) => ({
								px: spacing(2),
								fontWeight: "medium"
							})}
							size="small"
							onClick={handleProfileClick}
							label="Edit Profile"
							variant="outlined"
						/>
					</SProfileBox>
					<RenderIf
						allowedRoles={[
							"super-admin",
							"sub-admin",
							"practice-administrator"
						]}
					>
						<SLinkBox>
							{settings.map((setting, idx) => (
								<MuiLink
									tabIndex={0}
									fontWeight="medium"
									component="button"
									underline="none"
									color="grey.700"
									textAlign="left"
									onClick={() => handleUserMenuClick(setting)}
									key={idx}
								>
									{setting}
								</MuiLink>
							))}
						</SLinkBox>
					</RenderIf>
				</SProfilePaper>
				<Footer>
					<Link
						component="button"
						underline="none"
						onClick={() => handleUserMenuClick("Logout")}
						sx={({ palette }) => ({ color: palette.text.primary })}
					>
						Sign out
					</Link>
				</Footer>
			</>
		</PopperMenu>
	)
}

export default ProfileMenu
