import { Stack, Link, Container, Typography } from "@mui/material"
import { Link as RouterLink } from "react-router-dom"
import { SFooterGrid } from "./index.style"

const AppFooter = () => {
	const extLinks = [
		{
			name: "FAQs",
			link: "/faqs"
		},
		{
			name: "Privacy",
			link: "/privacy"
		},
		{
			name: "Terms",
			link: "/terms"
		}
	]

	return (
		<SFooterGrid maxWidth={false}>
			<Container maxWidth='lg'>
				<Stack
					direction="row"
					spacing={{xs: 2, sm: 4, md: 10}}
					gap={2}
					flexWrap='wrap'
					sx={theme => ({
						[theme.breakpoints.down('sm')]: {
							justifyContent: 'space-around'
						}
					})}
				>
					{extLinks.map(({ name, link }) => (
						<Link
							component={RouterLink}
							underline="none"
							variant="subtitle2"
							to={link}
							fontWeight="bold"
							key={link}
							sx={(theme) => ({ color: theme.palette.text.primary })}
						>
							{name}
						</Link>
					))}
					<Typography
						flexGrow={1}
						fontWeight='bold'
						sx={theme => ({
							textAlign: 'end',
							[theme.breakpoints.down('sm')]: {
								textAlign: 'center',
								marginLeft: '0 !important'
							}
						})}
					>
						© {new Date().getFullYear()} Ez Login
					</Typography>
				</Stack>
			</Container>
		</SFooterGrid>
	)
}

export default AppFooter
