import { Card, CardContent, Box, LinearProgress } from "@mui/material"
import { FC } from "react"
import { IAuthContainer } from "./AuthContainer.types"
import { ReactComponent as SecondaryLogo } from "../../assets/svgs/secondary.svg"

const BoxStyle = {
	display: "flex",
	flexDirection: "column",
	justifyContent: "flex-start",
	alignItems: "center",
	gap: "1rem",
	width: "100%",
	marginBottom: "6rem"
}

const extLinks = [
	{
		name: "FAQs",
		link: "/faqs"
	},
	{
		name: "Privacy",
		link: "/privacy"
	},
	{
		name: "Terms",
		link: "/terms"
	}
]

const AuthContainer: FC<IAuthContainer> = ({ loading = false, children }) => {
	return (
		<Box sx={BoxStyle}>
			<Box>
				<SecondaryLogo />
			</Box>
			<Card
				sx={{
					width: `clamp(18rem,90%,31rem)`,
					// minWidth: 440,
					minHeight: "max-content",
					// height: "100%",
					// maxHeight: 800,
					display: "flex",
					flexDirection: "column",
					borderRadius: 2.5,
					"&.MuiCard-root": {
						borderRadius: "1.875rem"
					}
				}}
				elevation={11}
				variant="elevation"
				raised
			>
				{loading && <LinearProgress />}
				{/* <CardHeader sx={{ mx: 2 }} avatar={<EZLOGO />} /> */}
				<CardContent
					sx={{
						// display: "flex",
						// flexDirection: "column",
						// flex: "1 0 0",
						// overflow: "auto",
						m: 3
					}}
				>
					{children}
				</CardContent>
				{/* <CardActions sx={{ backgroundColor: "#f4f4f4", height: "80px" }}>
					<Stack direction="row" spacing={4} sx={{ margin: "auto" }}>
						{extLinks.map(({ name, link }) => (
							<Link
								component={RouterLink}
								color="inherit"
								underline="none"
								variant="subtitle2"
								to={link}
								fontWeight="bold"
								key={link}
							>
								{name}
							</Link>
						))}
					</Stack>
				</CardActions> */}
			</Card>
		</Box>
	)
}

export default AuthContainer
