import { Box, FormHelperText, Grow, Link, Typography } from "@mui/material"
import React from "react"
import AuthCode from "react-auth-code-input"
import { SAuthContainer } from "../../pages/ResetPassword/index.styles"
import { Controller, useFormContext } from "react-hook-form"
import InputStatus from "../InputStatus"
type TOTPVerifyProps = {
	handleResendCode: () => void
}
const OTPVerify = ({ handleResendCode }: TOTPVerifyProps) => {
	const { control, getValues, getFieldState, formState } = useFormContext()
	const { error } = getFieldState("otp", formState)
	return (
		<Grow in={true}>
			<Box bgcolor="##fafafa">
				<Typography fontWeight="medium" variant="subtitle1">
					Enter the code we just sent to
				</Typography>
				<Typography fontWeight="medium" variant="subtitle1" color="primary">
					{getValues("email")}
				</Typography>
				<Controller
					name="otp"
					control={control}
					render={({ field: { onChange, ref } }) => (
						<SAuthContainer my={1}>
							<AuthCode
								ref={ref}
								isPassword
								ariaLabel="please enter the otp"
								autoFocus
								length={4}
								onChange={onChange}
								containerClassName="auth-container"
								inputClassName="auth-input"
							/>
						</SAuthContainer>
					)}
				/>

				<Box
					component={Typography}
					display="flex"
					alignItems="center"
					flexWrap="wrap"
					color="text"
					fontWeight="medium"
				>
					Didn't get the code? &nbsp;
					<Link color="primary" onClick={handleResendCode}>
						Resend Code
					</Link>
				</Box>
				{!!error?.message && (
					<FormHelperText error>
						<InputStatus text={error.message} />
					</FormHelperText>
				)}
			</Box>
		</Grow>
	)
}

export default OTPVerify
