import { Box, styled } from "@mui/material"

export const SImg = styled("img")(({ theme: { palette } }) => ({
	maxWidth: "100%",
	height: "100%"
}))
export const SImgBox = styled(Box)(({ theme: { palette, spacing } }) => ({
	width: "50px",
	height: "50px",
	padding: spacing(0.8),
	backgroundColor: palette.grey?.A700,
	borderRadius: "50%",
	display: "flex",
	justifyContent: "center",
	alignItems: "center"
}))
export const SLabelBox = styled(Box)(({ theme: { palette, spacing } }) => ({
	display: "flex",
	alignItems: "center",
	gap: spacing(2)
}))
