import React, { useEffect, useRef, useState } from "react";
import { Box, Button, Popper, Typography, ClickAwayListener, ListItem, InputBase } from "@mui/material";
import Panel from "../ProductList/Panel";
import { IAppData, IPractice } from "../../context/UserContext/UserContext.types";
import { useUserContext } from "../../context/UserContext/UserContext";
import S_InputBox from "../FilterInput/SharpBox";
import { KeyboardArrowDown } from "@mui/icons-material";
import { styled } from "@mui/system";
import produce, { current } from "immer";
import { useDebouncedValue } from "../../hooks";
import S_Button from "../FilterInput/SelectButton";

interface DDProps {
  // isLoading: boolean
  setApp: (app: IAppData, appIndex: number) => void
  appIndex: number
  app: IAppData
  disabled: boolean
}

const SearchBox = styled(InputBase)(({theme}) => ({
  padding: theme.spacing(1, 2)
}))

export default function PracticesDropdown(props: DDProps) {
  const { app, appIndex, setApp, disabled } = props;
  const [ search, setSearch ] = useState('')
  const deferredSearch = useDebouncedValue(search, 700)

  const [localState, setLocalState] = React.useState<IAppData>(app)
  const [filteredPractices, setFilteredPractices] = React.useState<IAppData>(app)
  const [anchorEl, setAnchorEl] = React.useState<HTMLElement | null>(null);
  const parentFocus = React.useRef<HTMLElement | null>(null);
  const childIndex = React.useRef<number>(0)
  const appData = useUserContext()

  useEffect(() => {
    setLocalState(app)
  }, [app])

  useEffect(() => {
    const filtered = filterPractices()
    setFilteredPractices(filtered)
  }, [localState])

  useEffect(() => {
    if(deferredSearch) {
      const filtered = filterPractices()
      setFilteredPractices(filtered)
    } else {
      setFilteredPractices(localState)
    }
  }, [deferredSearch])

  const escape = (e: any) => {
    if(e.code === 'Escape') {
      anchorEl?.focus()
      setAnchorEl(null)
    }
  }

  const handleDropdownOpen = (event: React.KeyboardEvent<any> | React.MouseEvent<any>) => {
    if('code' in event) {   
      if(event?.code === 'Enter') {
        setAnchorEl(anchorEl ? null : event.currentTarget as HTMLElement);
      }
    } else {
      setAnchorEl(anchorEl ? null : event.currentTarget as HTMLElement);
    }
    escape(event)
  };

  const deselectPrev = (obj: IPractice | IAppData, status: boolean) => {
    if (!obj?.prev) {
      return;
    }

    if (obj?.checkedLength && obj?.practices && (obj?.checkedLength - 1 > 0 || status)) {
      obj.isIndeterminate = true;
    } else {
      obj.isIndeterminate = false;
    }
    obj.isChecked = false;
    deselectPrev(obj.prev, obj.isIndeterminate);
  };

  const selectPrev = (obj: IPractice | IAppData) => {
    if (!obj?.prev) {
      return;
    }

    if (
      (obj.hasOwnProperty('checkedLength') && isFinite(obj.checkedLength as number)) &&
      obj?.practices &&
      obj.checkedLength as number + 1 >= obj?.practices?.length
    ) {
      obj.isIndeterminate = false;
      obj.isChecked = true;
    } else {
      obj.isIndeterminate = true;
    }
    selectPrev(obj.prev);
  };

  const selectChildren = (obj: IPractice) => {
    if (!obj?.practices) {
      return;
    }
    obj.isIndeterminate = false;

    obj.practices.forEach((pract) => {
      if (obj.isChecked) {
        pract.isChecked = true;
        
      } else {
        pract.isChecked = false;
        pract.isIndeterminate = false;
      }
      selectChildren(pract);
    });
  };

  function deepClone(obj: IPractice): IPractice {
    if(!obj.practices) {
      return { ...obj }
    }
    const clonePractices = obj.practices.map((practice) => {
      return deepClone({...practice})
    })
    
    return { ...obj, practices: clonePractices }
  }

  const onCheck = (_option: IPractice, indexArray: number[]) => {

    const foundIndex = localState.practices.findIndex(ele => ele.id === indexArray[0])
    const copyPractice = deepClone(localState.practices[foundIndex])
    let tempLocalData = { ...localState }
    tempLocalData.practices = [ ...tempLocalData.practices ]
    tempLocalData.practices[foundIndex] = copyPractice

    let temp: IAppData | IPractice = tempLocalData;

    indexArray.forEach((ele, index) => {
      let tmp = temp;
      tmp.checkedLength = tmp?.practices?.filter(
        (practice) => practice.isChecked || practice.isIndeterminate
      )?.length || 0;

      temp = temp.practices!.find((itm) => itm.id === ele) as IPractice
      temp.prev = tmp;

      if (index === indexArray.length - 1) {
        if (!temp.isChecked) {
          temp.isChecked = true;
          selectPrev(temp);
        } else {
          temp.isChecked = false;
          deselectPrev(temp, false);
        }

        if (temp.practices?.length) {
          selectChildren(temp);
        }
      }
    });

    setLocalState(tempLocalData);
  };

  const deepSearch = (practice: IAppData | IPractice): boolean => {
    delete practice.prev
    if(!practice.practices || practice?.practices.length < 1) {
      return practice.name.includes(deferredSearch)
    } else {
      const copyPractices = current(practice.practices)
      const matched = practice.practices.filter((prac) => {
        return deepSearch(prac)
      })

      if(practice.name.includes(deferredSearch)) {
        practice.practices = copyPractices
      } else {
        practice.practices = matched
      }

      return !!matched.length || practice.name.includes(deferredSearch)
    }
  }

  function filterPractices() {
    if(deferredSearch) {
      const filtered: IAppData = produce(localState, draft => {
        deepSearch(draft)
      })

      return filtered
      
    } else {
      return localState
    }
  }

  const idKey = `${app.sp_app_name}-po-`

  function handleNavigation(event: React.KeyboardEvent<HTMLUListElement>) {
    event.preventDefault()
    if(parentFocus) {
      const childCount = parentFocus.current?.childElementCount || 0
      const children =  parentFocus.current?.children
      let nextElement = children?.[childIndex.current] as HTMLElement
      let prevElement

      if(event.code === 'ArrowDown' || event.code === 'ArrowUp') {
        if(event.code === 'ArrowDown') {
          childIndex.current++
          if(childIndex.current > childCount - 1) {
            childIndex.current = 0
          }

          prevElement = children?.[childIndex.current - 1] as HTMLElement
          nextElement = children?.[childIndex.current] as HTMLElement
        } else {
          childIndex.current--
          if(childIndex.current < 0) {
            childIndex.current = childCount - 1
          }

          prevElement = children?.[childIndex.current + 1] as HTMLElement
          nextElement = children?.[childIndex.current] as HTMLElement
        }

        if(prevElement) prevElement.classList.remove('Mui-focusVisible')
        if(nextElement) {
          nextElement.classList.add('Mui-focusVisible')
          const nextChild = nextElement.firstChild as HTMLElement
          nextChild?.focus()
        }

      } else if (event.code === 'ArrowLeft') {
        let count = 0
        let selectedSibling = nextElement
        while(selectedSibling && selectedSibling.getAttribute('aria-expanded') !== 'true') {
          selectedSibling = selectedSibling?.previousSibling as HTMLElement
          count++
        }

        if(selectedSibling) {
          childIndex.current = childIndex.current - count
          const btn = selectedSibling?.firstChild as HTMLElement
          btn.focus()
          btn?.click?.()
        }
      } else if (event.code === 'ArrowRight') {
        const btn = nextElement?.firstChild as HTMLElement

        btn.focus()
        btn?.click?.()
      }
    }
  }

  const saveAndClose = () => {
    setApp(localState, appIndex)
    anchorEl?.focus()
    setAnchorEl(null)
  }

  const revokeAndClose = () => {
    setLocalState(app)
    anchorEl?.focus()
    setAnchorEl(null)
  }

  const id = "dropdown";
  const open = Boolean(anchorEl);

  const checkedLength = app.practices.filter((practice) => practice.isChecked || practice.isIndeterminate).length || 0

  if (appData?.appDataLoading) {
    return <Typography>Loading...</Typography>;
  }

  return (
    <>
      <S_Button
        onClick={handleDropdownOpen}
        onKeyDown={handleDropdownOpen}
        // tabIndex={disabled ? -1 : 0}
        endIcon={<KeyboardArrowDown />}
        id={`${app.sp_app_name}-practicedd`}
        disabled={disabled}
        color='info'
        role='tree'
        aria-expanded={open}
        aria-haspopup="tree"
        aria-label={checkedLength ? `${checkedLength} Practices Selected` : `Practices`}
        // sx={{ pointerEvents: disabled ? 'none' : 'auto' }}
      >
        <Typography variant='body1'>
          {checkedLength ? `${checkedLength} Practices Selected` : `Practices`}
        </Typography>
      </S_Button>
      {/* <S_InputBox
        {...(disabled ? {} : { onClick: handleDropdownOpen, onKeyDown: handleDropdownOpen })}
        tabIndex={disabled ? -1 : 0}
        id={`${app.sp_app_name}-practicedd`}
        sx={{ pointerEvents: disabled ? 'none' : 'auto' }}
      >
        <Box width='100%' display='flex' alignItems='center' justifyContent='space-between' px={2} pr={0.1}>
          <Typography variant='body1' color={disabled ? 'text.disabled' : 'text.primary'}>{checkedLength ? `${checkedLength} Practices Selected` : `Practices`}</Typography>
          <KeyboardArrowDown color={disabled ? 'action' : 'inherit'} />
        </Box>
      </S_InputBox> */}
      <Popper
        id={id}
        open={open}
        anchorEl={anchorEl} sx={({ palette, shadows }) => ({
          backgroundColor: palette.background.default,
          boxShadow: shadows[2]
        })}
      >
      <ClickAwayListener onClickAway={saveAndClose} mouseEvent='onMouseDown'>
        <Box onKeyDown={escape}>
          <Box>
            <SearchBox
              value={search}
              onChange={(e: React.ChangeEvent<HTMLInputElement>) => setSearch(e.currentTarget.value)}
              placeholder="Search"
              autoFocus
              fullWidth
              onKeyDown={(e: React.KeyboardEvent<HTMLInputElement>) => {
                if(e.code === 'Tab' && e.shiftKey) {
                  revokeAndClose()
                }
              }}
              inputProps={{
                'aria-label': "Search Practices"
              }}
            />
            <Box
              component='ul'
              pl={0}
              maxHeight={400}
              width={anchorEl?.clientWidth}
              overflow="auto"
              tabIndex={0}
              ref={parentFocus}
              onKeyDown={(e: React.KeyboardEvent<any>) => { if(e.code !== 'Tab') handleNavigation(e) }}
            >
              {/* <CheckTree valueKey='id' childrenKey='practices' labelKey='name' data={app.practices} onChange={(values: any) => console.log("CHECK: ", values)} /> */}
              {filteredPractices && filteredPractices?.practices?.length > 0 ? filteredPractices?.practices?.map((panel, index) => (
                <Panel
                  key={index}
                  id={`${idKey}-${index}`}
                  index={[panel.id]}
                  option={panel}
                  onCheck={onCheck}
                  search={deferredSearch}
                  expanded={search ? !panel.name.includes(deferredSearch) : false}
                />
              )) : <ListItem><Typography variant="body1">No Options...</Typography></ListItem>}
            </Box>
          </Box>
          <Box sx={({palette, spacing}) => ({ 
              width: "100%",
              backgroundColor: palette.background.default,
              padding: spacing(1),
              borderTop: `1px solid ${palette.divider}`,
              display: 'flex',
              justifyContent: 'flex-end',
              '& :not(:last-child)': {
                marginRight: spacing(1)
              }
            })}>
              <Button
                variant='contained'
                color='secondary'
                size='small'
                onClick={revokeAndClose}
              >
                Cancel
              </Button>
              <Button
                id='save-btn'
                variant='contained'
                color='primary'
                size='small'
                onClick={saveAndClose}
                onKeyDown={(e: React.KeyboardEvent<HTMLButtonElement>) => {
                  if(e.code === 'Tab' && !e.shiftKey) {
                    revokeAndClose()
                  }
                }}
              >
                Save
              </Button>
            </Box>
          </Box>
      </ClickAwayListener>
      </Popper>
    </>
  );
}
