import React from "react";
import {
  Checkbox,
  ListItem,
  ListItemButton,
  ListItemIcon,
  ListItemText
} from "@mui/material";
import ArrowForwardIosSharpIcon from "@mui/icons-material/ArrowForwardIosSharp";
import { styled } from "@mui/material/styles";
import { ListItemProps } from "antd/lib/list";

interface StyledListItemProps extends ListItemProps {
  level: number
}

const StyledListItem = styled(ListItem)<StyledListItemProps>(({ theme, level }) => {
  return {
    position: "relative",
    "&::before": {
      content: level == 1 ? "none" : "' '",
      position: "absolute",
      left: theme.spacing(1.5 * level),
      height: "100%",
      borderLeft: "2px solid lightgray",
      zIndex: 100
    }
  }
});

interface Props {
  id: string,
  name: string,
  expandable: boolean,
  expanded?: boolean,
  onClick: (e: React.MouseEvent<HTMLDivElement>) => void,
  checked: boolean,
  labelId: string,
  level: number,
  onCheck: (e: React.SyntheticEvent) => void,
  indeterminate?: boolean
}

const Item = ({
  id,
  name,
  expandable,
  expanded,
  onClick,
  checked,
  labelId,
  level,
  onCheck,
  indeterminate
}: Props) => {
  return (
    <StyledListItem
      dense
      secondaryAction={
        expandable ? (
          <ArrowForwardIosSharpIcon sx={{ fontSize: "0.9rem", transform: expanded ? "rotate(90deg)" : "none", pointerEvents: 'inherit' }} />
        ) : null
      }
      disablePadding
      level={level}
      role='option'
      tabIndex={-1}
      {...(expandable ? { 'aria-expanded':expanded } : {})}
      aria-level={level}
      aria-checked={checked}
    >
      <ListItemButton
        disableRipple={!expandable}
        role={undefined}
        onClick={onClick}
        onKeyUp={(e: React.KeyboardEvent<HTMLDivElement>) => { if(e.code === 'Space') onCheck(e) }}
        dense
        sx={{ pl: level * 2, cursor: !expandable ? 'default' : 'pointer' }}
        tabIndex={-1}
        id={id}
        {...(expandable ? { 'aria-expanded':expanded } : {})}
        aria-level={level}
        aria-checked={checked}
      >
        <ListItemIcon>
          <Checkbox
            edge="start"
            checked={checked}
            indeterminate={indeterminate}
            onChange={(e) => {
              e.stopPropagation();
              onCheck(e);
            }}
            onClick={(e) => {
              e.stopPropagation();
            }}
            tabIndex={-1}
            disableRipple
            inputProps={{ "aria-labelledby": labelId }}
          />
        </ListItemIcon>
        <ListItemText id={labelId} primary={name} />
      </ListItemButton>
    </StyledListItem>
  );
};

export default Item;
