import { Dispatch, SetStateAction } from "react"
import { IAppData, IRole } from "../../context/UserContext/UserContext.types"

export interface IProfile {}
export interface IAccountFormInput {
	address: string
	first_name: string
	last_name: string
	email: string
	contact_no: string
	profile_image: string
}

export interface IPermissionsForm {
	product: string
	practice: string
	role: string
	sub_role: string
}

export interface IPermissionsResult {
	sp_practice_roles: IPermissionData[]
	statuscode: number
	message: string
}

export interface IPermissionData extends Omit<IAppData, 'roles'> {
	role: { sub_role: IRole } & Omit<IRole, 'sub_roles'> 
}

export interface IUserInfo {
	first_name: string
	last_name: string
	email: string
	contact_no: string
	address: string
	profile_image: string
}

export interface IGetUserInfo {
	user_info: IUserInfo
	statuscode: number
	message: string
}

//For Sajeel
export interface IUserResult {
	statuscode: number
	message: string
}

export type IUserType = "external" | "internal"

export interface IPracticeItem {
	id: number
}
export interface IPracticeRole {
	id: number
	sub_role: number
}
export interface IAppItem {
	id: number
	practices: IPracticeItem[]
	role: IPracticeRole
}
export interface IUserData {
	firstname: string
	lastname: string
	email: string
	type_of_user: IUserType
	dr_iq_gender_id: number | null
	apps: IAppItem[]
}

export enum SectionType {
	ACCOUNT = 'account',
	PERMISSION = 'permissions'
}

//Sidebar
export interface ISideBar {
	profileImage: string
	fullName: string
	isLoading: boolean
}

//Types for Sidebar
export type IImage = {
	image: File
}
export interface IImageUploadResponse {
	message: string
	statuscode: 201
}
