import { IAppData, IPractice } from "../context/UserContext/UserContext.types";

export default function pickPractices (obj: IPractice | IAppData, practices: { id: number }[]) {
  if (!obj?.practices) {
    return;
  }
  obj.practices.forEach((pract) => {
    if (pract.isChecked || pract.isIndeterminate) {
      practices.push({ id: pract.id })
    }
    pickPractices(pract, practices);
  });
};