import { Checkbox, FormControlLabel, TextField } from "@mui/material"
import InputStatus from "../InputStatus"
import { Controller, useFormContext } from "react-hook-form"
import { TFormInput } from "./FormInput.types"
const FormInput = <T extends { type: unknown }>({
	name,
	...rest
}: TFormInput<T>) => {
	const { control } = useFormContext()
	return (
		<Controller
			control={control}
			name={name}
			render={({
				field,
				fieldState,
				fieldState: { error },
				formState,
				formState: { errors }
			}) => {
				if (rest.type == "checkbox") {
					return (
						<FormControlLabel
							disabled={rest.disabled}
							control={<Checkbox {...field} disableRipple size="medium" />}
							label={rest.label}
						/>
					)
				}
				return (
					<TextField
						{...rest}
						{...field}
						error={!!error}
						helperText={
							errors?.[name] && <InputStatus text={errors?.[name]?.message} />
						}
					/>
				)
			}}
		/>
	)
}

export default FormInput
