import { yupResolver } from "@hookform/resolvers/yup"
import { Stack, Typography, Box } from "@mui/material"
import AuthCode from "react-auth-code-input"
import { FormProvider, SubmitHandler, useForm } from "react-hook-form"
import AuthContainer from "../../components/AuthContainer"
import IconContainer from "../../components/IconContainer"
import StyledButton from "../../components/StyledButton"
import StyledInput from "../../components/StyledInput"
import {
	FullWidthBoxStyled,
	StackStyled
} from "../../layout/ScrollableStack/index.styles"
import * as yup from "yup"
import { SAuthContainer } from "./index.styles"
import { useAppContext } from "../../context/AppContext/AppContext"
import { useMutation, UseMutationResult } from "react-query"
import { AxiosError, AxiosResponse } from "axios"
import { post } from "../../api/utils"
import FormInput from "../../components/FormInput/FormInput"
import { useNavigate } from "react-router-dom"
import { SForm } from "../ForgotPassword/index.styles"

type TForgotPasswordResponse = {
	statuscode: number
	message: string
}
type TFormData = {
	password: string
	confirmPassword: string
}
type TForgotPasswordError = {
	message: string
	statuscode: number
}

function setPassword({ password }: Omit<TFormData, "confirmPassword">) {
	return post<AxiosResponse<TForgotPasswordResponse>>(
		`set-password`,
		{
			password
		},
		{
			withCredentials: true
		}
	)
}

const passwordSchema = yup
	.object({
		password: yup
			.string()
			.required("Please enter new password")
			.matches(
				/^(?=.*[a-z])(?=.*[A-Z])(?=.*[0-9])(?=.*[!@#\$%\^&\*])(?=.{8,})/,
				"Must contain 8 characters, uppercase, lowercase, number and special case character"
			),
		confirmPassword: yup
			.string()
			.required("Please confirm new password")
			.oneOf([yup.ref("password")], "Passwords must match")
	})
	.required()

const ResetPassword = () => {
	const appState = useAppContext()
	const navigate = useNavigate()
	//To handle the form
	const { handleSubmit, setError, reset, ...rest } = useForm<TFormData>({
		defaultValues: {
			password: "",
			confirmPassword: ""
		},
		resolver: yupResolver(passwordSchema)
	})
	//rejoining this to pass to form context
	const methods = { handleSubmit, setError, reset, ...rest }

	const {
		mutate,
		isLoading
	}: UseMutationResult<
		AxiosResponse<TForgotPasswordResponse>,
		AxiosError<TForgotPasswordError>,
		Omit<TFormData, "confirmPassword">
	> = useMutation<
		AxiosResponse<TForgotPasswordResponse>,
		AxiosError<TForgotPasswordError>,
		Omit<TFormData, "confirmPassword">
	>(setPassword, {
		onSuccess: res => {
			const { data } = res
			const { message } = data
			appState?.setAlert({
				show: true,
				message: message,
				status: "success"
			})
			reset()
			navigate("sign-in")
		},
		onError: err => {
			const { response } = err
			if (!response) return
			const { data } = response
			const { message } = data
			setError("password", { type: "server", message })
		}
	})

	const handleSend: SubmitHandler<TFormData> = data => {
		const password = data?.password
		if (password) {
			mutate({ password })
		}
	}

	return (
		<FormProvider {...methods}>
			<SForm onSubmit={handleSubmit(handleSend)}>
				<AuthContainer>
					<Stack spacing={2}>
						<Typography variant="h5" fontWeight={600}>
							Reset your password
						</Typography>
						<Typography variant="body1">Enter new password</Typography>
						<StackStyled direction="column" spacing={2}>
							<FormInput
								placeholder="New Password"
								type="password"
								fullWidth
								variant="outlined"
								name="password"
								label="New Password"
							/>
							<FormInput
								placeholder="Confirm Password"
								type="password"
								fullWidth
								variant="outlined"
								name="confirmPassword"
								label="Confirm Password"
							/>
							{/* <StyledInput error type="password" /> */}
							<FullWidthBoxStyled>
								<StyledButton
									disabled={isLoading}
									type="submit"
									sx={{ mr: "auto" }}
									variant="contained"
								>
									Reset
								</StyledButton>
							</FullWidthBoxStyled>
						</StackStyled>
					</Stack>
				</AuthContainer>
			</SForm>
		</FormProvider>
	)
}

export default ResetPassword
