import {
	Box,
	CircularProgress,
	circularProgressClasses,
	CircularProgressProps,
	Typography
} from "@mui/material"

const Timer = (props: CircularProgressProps & { total: number }) => {
	return (
		<Box
			sx={{
				width: 180,
				height: 180,
				background: theme =>
					theme.palette.background.backgroundGradientMulti?.("63deg"),
				borderRadius: "50%",
				display: "flex",
				alignItems: "center",
				justifyContent: "center",
				marginBottom: 2
			}}
		>
			<Box
				sx={{
					position: "relative",
					display: "inline-flex",
					boxShadow: "0px 0px 9px 1px #dddddd",
					borderRadius: "50%"
				}}
			>
				<CircularProgress
					variant="determinate"
					sx={{
						backgroundColor: "#ffffff",
						borderRadius: "50%",
						color: theme =>
							theme.palette.grey[theme.palette.mode === "light" ? 200 : 800]
					}}
					size={150}
					thickness={2}
					{...props}
					value={100}
				/>
				<CircularProgress
					variant="determinate"
					sx={{
						animationDuration: "550ms",
						position: "absolute",
						left: 0,
						[`& .${circularProgressClasses.circle}`]: {
							strokeLinecap: "round"
						}
					}}
					size={150}
					thickness={2}
					{...props}
					value={Math.round((props.value! / props.total) * 100)}
				/>
				<Box
					sx={{
						top: 0,
						left: 0,
						bottom: 0,
						right: 0,
						position: "absolute",
						display: "flex",
						alignItems: "center",
						justifyContent: "center"
					}}
				>
					<Box>
						<Typography fontWeight="bold" textAlign="center" variant="h4">
							{props.value}
						</Typography>
						<Typography variant="subtitle2" color="text.secondary">
							Seconds
						</Typography>
					</Box>
				</Box>
			</Box>
		</Box>
	)
}

export default Timer
