import React, { useState, useDeferredValue } from "react"
import { Box } from "@mui/system"
import FilterInput, {
	IFilterInputProps
} from "../../components/FilterInput/FilterInput"
import FilterSelect, {
	SelectProps
} from "../../components/FilterInput/FilterSelect"
import FilterMultiSelect, { MultiSelectProps } from "../../components/FilterInput/FilterMultiSelect"
import {
	Button,
	ButtonGroup,
	Chip,
	Dialog,
	DialogContent,
	DialogTitle,
	Divider,
	Link as MuiLink,
	Stack,
	ToggleButton,
	ToggleButtonGroup,
	Tooltip,
	Typography
} from "@mui/material"
import { Link, useNavigate } from "react-router-dom"
import { Table } from "../../components/Table"
import { ColumnType, ITable } from "../../components/Table/Table.types"
import { CellProps } from "react-table"
import { Add, Search, KeyboardBackspace, ReceiptLong, ArrowDropUp, ArrowDropDown, Refresh } from "@mui/icons-material"

import { IUserManagement, IFilters } from "./UserManagement.types"
import {
	DeleteButton,
	EditButton
} from "../../components/Buttons/ActionButtons"
import { useIsFetching, useMutation, useQuery, useQueryClient } from "react-query"
import { get, remove } from "../../api/utils"
import StyledButton from "../../components/StyledButton"
import { DataLoadError } from "../../components/ErrorScreens"
import { IGetServiceProvidersData, IServiceProvider } from "../../layout/AppLayout/AppLayout.types"
import { AxiosResponse } from "axios"

function getUsers(entries: any, page: any, filters: any) {
	return get("users/", {
		params: {
			limit: entries,
			offset: page,
			...filters,
		}
	}).then(res => res.data)
}

//add the union of products
type TProducts = string

type TMetaData = {
	page: number
	page_count: number
	per_page: number
	total_records: number
}

interface IUserData {
	email: string
	first_name: string
	id: number
	last_name: string
	products: Array<TProducts>
}
interface IGetUsers {
	message: string
	status_code: number
	users_data: Array<IUserData>
	_metadata: TMetaData
}

interface IUserInfo {
	userId: number
}
function deleteUser(userInfo: IUserInfo) {
	return remove(`user/${userInfo.userId}`)
}

export default function UserManagement(_props: IUserManagement) {
	const [open, setOpen] = useState<boolean>(false)
	const [selectedItem, setSelectedItem] = useState<number | null>(null)

	const [filters, setFilters] = useState<IFilters>({ latest: 'desc' })
	const [variables, setVariables] = useState<IFilters>(filters)
	const [hiddenColumns] = useState(["id"])
	const deferredFilters = useDeferredValue(filters)

	const navigate = useNavigate()

	//For Api
	const [entries, setEntries] = useState(10)
	const [page, setPage] = useState(1)

	const queryClient = useQueryClient()

	const isFetchingSCs = useIsFetching(['splist']) > 0
	const serviceProviders = isFetchingSCs ? [] : queryClient.getQueryData<AxiosResponse<IGetServiceProvidersData>>(['splist'])?.data?.serviceproviders

	const filteredVariables = {
		...variables,
		latest: variables.latest === 'desc' ? true : false,
		practices: variables?.practices?.length ? variables?.practices.join(',') : 'All',
		...((variables?.status !== undefined && variables?.status !== null) && { status: variables?.status === 'active' ? true : false })
	}

	const usersQuery = useQuery(
		[
			"users",
			entries,
			page,
			{ ...filteredVariables }
		],
		() => getUsers(entries, page, filteredVariables),
		{ keepPreviousData: true, refetchOnWindowFocus: false }
	)

	const { mutate, isLoading } = useMutation(deleteUser, {
		onSuccess: _successResponse => {
			//do fetch the table data again
			//so invalidate all the table queries
			queryClient.invalidateQueries("users")
			//reset the selectedItem
			setOpen(false)
			setSelectedItem(null)
		}
	})

	const metaData = usersQuery.data?._metadata || {}
	const data = usersQuery.data?.users_data

	let [columns] = useState<ColumnType<IUserData>[]>([
		{
			Header: "#",
			Cell: ({ row, state }: any) => (
				<Typography>
					{row.index + 1 + state.pageIndex * state.pageSize}
				</Typography>
			)
		},
		{ Header: "First Name", accessor: "first_name" },
		{ Header: "Last Name", accessor: "last_name" },
		{
			Header: "Products",
			accessor: "products",
			Cell: ({ value, ..._rest }): any => {
				if (value.length <= 0) {
					return <Typography>No assigned product</Typography>
				}
				return (
					<Stack spacing={1} direction="row">
						{value.map(product => (
							<Chip label={product} size='small' />
						))}
					</Stack>
				)
			}
		},

		{
			Header: "Action",
			disableSortBy: true,
			Cell: ({ cell }: CellProps<IUserData, React.ReactNode>) => {
				const id = cell.row.original.id
				return (
					<Stack direction="row" spacing={1} justifyContent="center">
						<EditButton
							size="medium"
							onClick={() => {
								navigate(`/create-user/${id}`)
							}}
							aria-label="Edit User"
						/>
						<DeleteButton
							size="medium"
							onClick={() => {
								//open Delete Modal
								setOpen(true)
								setSelectedItem(id)
							}}
							aria-label="Delete User"
						/>
					</Stack>
				)
			}
		}
	])

	const onProductChange: MultiSelectProps["onChange"] = value => {
		setFilters({
			...filters,
			practices: value
		})
	}

	const onOrderChange: SelectProps["onChange"] = value => {
		setFilters({
			...filters,
			order_by: value || null
		})
	}

	const onStatusChange: SelectProps["onChange"] = value => {
		setFilters({
			...filters,
			status: value || null
		})
	}

	const onSearchChange: React.ChangeEventHandler<HTMLInputElement> = e => {
		setFilters({
			...filters,
			search: e.target.value
		})
	}

	const onEntriesChange: SelectProps["onChange"] = value => {
		setEntries(value ? parseInt(value) : entries)
		//reset the page no aswell
		setPage(1)
	}

	const handleArrangementChange = (event: React.MouseEvent<HTMLElement>, value: any) => {
		if (value !== null) {
			setVariables({
				...variables,
				latest: value
			})
		}
	}

	const refetchListingQuery = () => {
		setVariables({ ...filters })
	}

	const resetFilters = (e: React.SyntheticEvent) => {
		setFilters({
			practices: [],
			status: null,
			order_by: null,
			search: '',
			latest: 'desc'
		})
	}

	const handleClose = () => {
		setSelectedItem(null)
		setOpen(false)
	}

	const handleDelete = () => {
		if (typeof selectedItem == "number") {
			mutate({ userId: selectedItem })
		}
	}

	const changePage: ITable<{}, "paged">["setPage"] = pageNo => {
		if (pageNo) {
			setPage(pageNo)
		}
	}

	// if (usersQuery.isError) {
	// 	return <>Error fetching records</>
	// }

	return (
		<Box width='100%'>
			<Box display="flex" alignItems="center" mb={2}>
				<MuiLink component={Link} to="/" sx={{ display: "flex" }} aria-label='Go Back'>
					<KeyboardBackspace />
				</MuiLink>
				&nbsp;
				<Typography color="primary" component='h1' variant="h6" fontWeight="bold">
					USER MANAGEMENT
				</Typography>
			</Box>
			<Box
				mb={2}
				display="flex"
				justifyContent={{ xs: 'stretch', md: "space-between" }}
				alignItems="flex-end"
				flexWrap='wrap'
				gap={2}
			>
				<Stack
					direction={{ xs: 'column', sm: 'column', md: 'row' }}
					flex={1}
					flexWrap='wrap'
					alignItems={{ sm: 'stretch', md: "flex-end" }}
					maxWidth={{ sm: 400, md: 'unset' }}
					spacing={{ sm: 1, md: 2 }}
					gap={2}
				>
					<Stack direction="row" alignItems="flex-end" spacing={3.5}>
						<FilterInput
							label={'Search'}
							value={filters.search}
							onChange={onSearchChange}
							placeholder="Search"
							startAdornment={<Search fontSize='small' color="action" />}
							inputProps={{
								'aria-label': "Search users by name"
							}}
						/>
					</Stack>
					<FilterMultiSelect
						label="Product"
						value={filters.practices || []}
						onChange={onProductChange}
						isLoading={isFetchingSCs}
						disabled={isFetchingSCs}
					>
						{
							serviceProviders?.map(({ name, display_name }) => (
								<option label={display_name} value={name} />
							)) || []
						}
					</FilterMultiSelect>
					<FilterSelect
						label="Status"
						value={filters?.status}
						onChange={onStatusChange}
					>
						<option label="" value="">
							{" "}
						</option>
						<option label="Active" value="active">
							Active
						</option>
						<option label="Inactive" value="inactive">
							Inactive
						</option>
					</FilterSelect>
					<Stack direction='row' alignItems='flex-end' gap={0.5}>
						<FilterSelect
							label="Order By"
							value={filters?.order_by}
							onChange={onOrderChange}
							sx={{ flex: 1 }}
						>
							<option label="" value="">
								{" "}
							</option>
							<option label="First Name" value="first_name">
								First Name
							</option>
							<option label="Last Name" value="last_name">
								Last Name
							</option>
							<option label="Created Date" value="created_date">
								Created Date
							</option>
						</FilterSelect>
						<ToggleButtonGroup
							color="primary"
							size="small"
							value={variables.latest}
							exclusive
							// disabled={!filters.order_by}
							onChange={handleArrangementChange}
							aria-label="Order by arrangement"
						>
							<ToggleButton value={'desc'}>
								<Tooltip title='Descending Order'>
									<ArrowDropDown />
								</Tooltip>
							</ToggleButton>
							<ToggleButton value={'asc'}>
								<Tooltip title='Ascending Order'>
									<ArrowDropUp />
								</Tooltip>
							</ToggleButton>
						</ToggleButtonGroup>
					</Stack>
					<Box display='flex' gap={1} flexWrap='wrap'>
						<ButtonGroup variant="outlined" aria-label="filter buttons">
							<Button fullWidth variant='contained' onClick={refetchListingQuery}>
								Filter
							</Button>
							<Button onClick={resetFilters} aria-label='Reset filters'>
								<Refresh />
							</Button>
						</ButtonGroup>
					</Box>
				</Stack>
			</Box>
			<Divider variant="fullWidth" />
			<Box
				my={2}
				display="flex"
				justifyContent="space-between"
				alignItems="center"
				flexWrap='wrap'
				gap={3}
			>
				<Box display='flex' gap={2} flex={{ xs: 1, sm: 0 }}>
					<Button
						variant="text"
						onClick={() => navigate("/create-user")}
						startIcon={<Add />}
						role='link'
					>
						Add User
					</Button>
					<Button
						variant="text"
						onClick={() => navigate("/audit-trail")}
						startIcon={<ReceiptLong />}
						role='link'
					>
						Audit Logs
					</Button>
				</Box>
				<Stack direction="row" alignItems="center" spacing={2}>
					<Typography variant="body1">Show</Typography>
					<FilterSelect
						label={null}
						suffix="entries"
						onChange={onEntriesChange}
						defaultValue={entries.toString()}
						sx={{ width: 150 }}
						value={String(entries)}
						buttonProps={{
							'aria-label': `Number of entries shown in table, ${entries} Selected`
						}}
					>
						{[5, 10, 15, 20, 25].map(num => (
							<option key={num} label={`${num}`} value={num} />
						))}
					</FilterSelect>
				</Stack>
			</Box>
			{usersQuery.isError ? (
				<DataLoadError />
			) : (
				<Table
					columns={columns}
					data={data || []}
					// filters={deferredFilters}
					rowsPerPage={entries}
					useSorting={true}
					hiddenColumns={hiddenColumns}
					metaData={metaData}
					setPage={changePage}
					page={page}
					dataLoading={usersQuery.isLoading || usersQuery.isFetching}
					paginationType="paged"
				/>
			)}
			<Dialog open={open} maxWidth="xs" fullWidth onClose={handleClose}>
				<DialogTitle>Delete User</DialogTitle>
				<DialogContent dividers>
					<Box display="flex" alignItems="center" flexDirection="column">
						<Typography>Are you sure you want to delete this user</Typography>
						<Stack py={4} mx="auto" direction="row" spacing={1}>
							<StyledButton variant="outlined" onClick={handleClose}>
								Cancel
							</StyledButton>
							<StyledButton variant="contained" onClick={handleDelete}>
								{isLoading ? "Deleting..." : "Delete"}
							</StyledButton>
						</Stack>
					</Box>
				</DialogContent>
			</Dialog>
		</Box>
	)
}
