import { IconButton, SvgIcon, IconButtonProps } from '@mui/material'
import { ReactComponent as EditIcon } from '../../assets/svgs/edit-icon.svg'
import { ReactComponent as DeleteIcon } from '../../assets/svgs/delete-icon.svg'

const EditButton = (props: IconButtonProps) => (
  <IconButton {...props}>
    <SvgIcon viewBox='-2 0 38 38' fontSize={props.size}>
      <EditIcon />
    </SvgIcon>
  </IconButton>
)

const DeleteButton = (props: IconButtonProps) => (
  <IconButton {...props}>
    <SvgIcon viewBox='-2 0 38 38' fontSize={props.size}>
      <DeleteIcon />
    </SvgIcon>
  </IconButton>
)

export {
  EditButton,
  DeleteButton
}