import { useEffect, useState } from "react"
import { DevTool } from "@hookform/devtools"
import {
	Avatar,
	Badge,
	Box,
	IconButton,
	InputAdornment,
	InputLabel,
	LinearProgress,
	Link,
	Tabs,
	Typography
} from "@mui/material"
import { TabsUnstyled } from "@mui/base"
import { AxiosError, AxiosResponse } from "axios"
import { FormProvider, useForm } from "react-hook-form"
import {
	useMutation,
	UseMutationResult,
	useQuery,
	useQueryClient,
	UseQueryResult
} from "react-query"
import { get, post, put } from "../../api/utils"
import FormInput from "../../components/FormInput/FormInput"
import StyledButton from "../../components/StyledButton"
import {
	SLoadingBar,
	SProfileCard,
	SProfileCardContainer,
	SProfileContainer,
	TabsPanel
} from "./index.styles"
import { IAccountFormInput, IGetUserInfo, IPermissionsForm, IPermissionsResult, ISideBar, IUserInfo, SectionType } from "./Profile.types"
import { yupResolver } from "@hookform/resolvers/yup"
import { profileSchema } from "./ProfileValidations"
import Sidebar from "./Sidebar"
import MainContent from "./MainContent"
import { useParams, useLocation } from "react-router-dom"
import AccountPanel from "./AccountPanel"
import PermissionsPanel from "./PermissionsPanel"

const values = {
	address: "",
	first_name: "",
	last_name: "",
	email: "",
	contact_no: "",
	profile_image: ""
}

function capitalize(s = "") {
	return s.charAt(0).toUpperCase() + s.slice(1)
}

export function getProfile() {
	return get("user")
}

export function getPermissions() {
	return get("user/selected/apps/practices/roles")
}

function profileUpdate(formdata: Omit<IUserInfo, "email" | "profile_image">) {
	return put<IGetUserInfo>("user", formdata)
}

const Profile = () => {
	const queryClient = useQueryClient()
	const location = useLocation()
	const { tab } = useParams()
	const [section, setSection] = useState<SectionType>(tab ? tab as SectionType : SectionType.ACCOUNT)

	const { data: permissions, isLoading: permissionsLoading, isError: permissionsError }: UseQueryResult<AxiosResponse<IPermissionsResult>, AxiosError> = useQuery(
		"practicesAndRoles",
		getPermissions,
		{
			refetchOnWindowFocus: false
		}
	)

	const {
		data: result,
		isLoading,
		isError,
		error,
		isSuccess,
		isRefetching
	}: UseQueryResult<AxiosResponse<IGetUserInfo>, AxiosError> = useQuery(
		"profile",
		getProfile,
		{
			refetchOnWindowFocus: false
		}
	)
	//Mutation for profile update
	const {
		mutate,
		isLoading: _isLoading,
		isError: _isError,
		error: _error
	}: UseMutationResult<
		IGetUserInfo,
		AxiosError,
		Omit<IUserInfo, "email" | "profile_image">
	> = useMutation<
		IGetUserInfo,
		AxiosError,
		Omit<IUserInfo, "email" | "profile_image">
	>(profileUpdate, {
		onSuccess: () => {
			queryClient.invalidateQueries("profile")
		}
	})

	const userInfo = result?.data?.user_info
	const userPermissions = permissions?.data?.sp_practice_roles

	const firstName = userInfo?.first_name
	const lastName = userInfo?.last_name
	const profileImage = userInfo?.profile_image || ""

	const accountForm = useForm<IAccountFormInput>({
		defaultValues: values,
		resolver: yupResolver(profileSchema)
	})

	const permissionsForm = useForm<IPermissionsForm>({
		defaultValues: {
			role: "",
			sub_role: ""
		}
	})

	const { handleSubmit, reset, ...rest } = accountForm
	const { control } = permissionsForm

	const fullName = `${capitalize(firstName)}  ${capitalize(lastName)}` || ""

	useEffect(() => {
		if (isSuccess) {
			//resetting because of new data
			reset(userInfo)
		}
	}, [isSuccess, reset])

	const onSubmit = handleSubmit(submittedData => {
		const { first_name, last_name, contact_no, address } = submittedData
		mutate({ first_name, last_name, contact_no, address })
	})

	if (isError) {
		return <Box>{error.message}</Box>
	}

	return (
		<SProfileContainer>
			<TabsUnstyled
				orientation="vertical"
				value={section}
				onChange={(_, value) => {
					let pathname = location.pathname
					if (tab) {
						pathname = pathname.slice(0, pathname.indexOf(tab) - 1)
					}
					window.history.replaceState(null, "", `${pathname}/${value}`)
					setSection(value as SectionType)
				}}
				aria-label="Vertical tabs example"
				style={{ width: '100%' }}
			>
				{/* <Tabs
						orientation="vertical"
						variant="scrollable"
						value={section}
						onChange={(_, value) => setSection(value)}
						aria-label="Vertical tabs example"
					// sx={{ borderRight: 1, borderColor: 'divider' }}
					> */}
				<SProfileCardContainer>
					<SLoadingBar>
						{(isLoading || _isLoading) && <LinearProgress />}
					</SLoadingBar>
					<SProfileCard>
						<Sidebar
							fullName={fullName}
							isLoading={isLoading}
							profileImage={profileImage}
						/>
						<MainContent>
							<TabsPanel value={SectionType.ACCOUNT}>
								<FormProvider {...accountForm}>
									<form onSubmit={onSubmit}>
										<AccountPanel mutationLoading={_isLoading} isLoading={isLoading} />
									</form>
								</FormProvider>
							</TabsPanel>
							<TabsPanel value={SectionType.PERMISSION}>
								<FormProvider {...permissionsForm}>
									<PermissionsPanel permissions={userPermissions || []} isLoading={permissionsLoading} isError={permissionsError} />
								</FormProvider>
							</TabsPanel>
						</MainContent>
					</SProfileCard>
				</SProfileCardContainer>
				{/* </Tabs> */}
			</TabsUnstyled>
			<DevTool control={control} />
		</SProfileContainer>
	)
}

export default Profile
