import { AppBar, Box, Container, Menu, Toolbar, styled } from "@mui/material"
import { ReactComponent as Logo } from "../../assets/svgs/main.svg"
export const SFooterGrid = styled(Container)(
	({ theme: { palette, spacing } }) => ({
		background: palette.background.backgroundGradientMulti?.(),
		// background: 'transparent',
		backgroundColor: "transparent",
		padding: spacing(2.5, 3)
	})
)

export const SHeaderAppBar = styled(AppBar)(
	({ theme: { palette, spacing } }) => ({
		backgroundColor: "transparent",
		padding: spacing(1, 5)
	})
)
export const SHeaderToolbar = styled(Toolbar)(({ theme }) => ({
	display: "flex",
	justifyContent: "space-between",
	alignItems: "center",
	flexWrap: "wrap",
	columnGap: theme.spacing(2),
	[theme.breakpoints.down("sm")]: {
		justifyContent: "center"
	}
}))
export const SBox = styled(Box)(({ theme: { spacing } }) => ({
	alignItems: "center",
	gap: spacing(1.5)
}))
export const SMenu = styled(Menu)(({ theme: { spacing } }) => ({
	// [`& .${menuClasses.root}`]: {
	// 	minWidth: "200px"
	// },
	"& .MuiPaper-root": {
		minWidth: "200px"
	}
}))
export const SLogoBox = styled(Logo)(() => ({
	"&:hover": {
		cursor: "pointer"
	}
}))
