import { yupResolver } from "@hookform/resolvers/yup"
import { Stack, TextField, Typography } from "@mui/material"
import { AxiosError, AxiosResponse } from "axios"
import { useCallback } from "react"
import { FormProvider, SubmitHandler, useForm } from "react-hook-form"
import { useMutation, UseMutationResult } from "react-query"
import { useNavigate } from "react-router-dom"
import { post } from "../../api/utils"
import AuthContainer from "../../components/AuthContainer"
import StyledButton from "../../components/StyledButton"
import {
	FullWidthBoxStyled,
	StackStyled
} from "../../layout/ScrollableStack/index.styles"
import * as yup from "yup"
import FormInput from "../../components/FormInput/FormInput"
import { useAppContext } from "../../context/AppContext/AppContext"
import { SForm } from "./index.styles"

type TForgotPasswordResponse = {
	statuscode: number
	message: string
}
type TFormData = {
	email: string
}
type TForgotPasswordError = {
	message: string
	statuscode: number
}
function forgotPassword({ email }: TFormData) {
	return post<AxiosResponse<TForgotPasswordResponse>>(`change-password`, {
		email
	})
}

const emailSchema = yup
	.object({
		email: yup.string().email().required()
	})
	.required()

const ForgotPassword = () => {
	const navigate = useNavigate()
	const appState = useAppContext()

	//To handle the form
	const { handleSubmit, setError, reset, ...rest } = useForm<TFormData>({
		defaultValues: {
			email: ""
		},
		resolver: yupResolver(emailSchema)
	})
	//rejoining this to pass to form context
	const methods = { handleSubmit, setError, reset, ...rest }

	const {
		mutate,
		isLoading
	}: UseMutationResult<
		AxiosResponse<TForgotPasswordResponse>,
		AxiosError<TForgotPasswordError>,
		TFormData
	> = useMutation<
		AxiosResponse<TForgotPasswordResponse>,
		AxiosError<TForgotPasswordError>,
		TFormData
	>(forgotPassword, {
		onSuccess: res => {
			console.log("DATA:", res)
			const { data } = res
			const { message } = data
			appState?.setAlert({
				show: true,
				message: message,
				status: "success"
			})
			reset()
		},
		onError: err => {
			const { response } = err
			if (!response) return
			const { data } = response
			const { message } = data
			setError("email", { type: "server", message })
		}
	})

	const handleSend: SubmitHandler<TFormData> = data => {
		const email = data?.email
		if (email) {
			mutate({ email })
		}
	}

	return (
		<FormProvider {...methods}>
			<SForm onSubmit={handleSubmit(handleSend)}>
				{/* <form > */}
				<AuthContainer>
					<Stack spacing={1}>
						<Typography variant="h5" fontWeight={600}>
							Forgot password
						</Typography>
						<StackStyled spacing={3}>
							<Typography variant="body1">
								Enter your registered email address{" "}
							</Typography>
							<FormInput
								placeholder="Enter registered email"
								type="email"
								fullWidth
								variant="outlined"
								name="email"
								label="Email"
							/>
							<FullWidthBoxStyled>
								<StyledButton
									disabled={isLoading}
									type="submit"
									sx={{ mr: "auto" }}
									variant="contained"
								>
									Send Email
								</StyledButton>
							</FullWidthBoxStyled>
						</StackStyled>
					</Stack>
				</AuthContainer>
				{/* </form> */}
			</SForm>
		</FormProvider>
	)
}

export default ForgotPassword
