import { Box, styled } from "@mui/material"

export const SProfilePaper = styled(Box)(({ theme: { spacing, palette } }) => ({
	maxWidth: 320,
	minWidth: 300,
	height: "auto",
	padding: spacing(5, 5, 2, 5)
}))

export const SProfileBox = styled(Box)(({ theme: { spacing } }) => ({
	display: "flex",
	flexDirection: "column",
	gap: spacing(0.75),
	alignItems: "center"
}))

export const SLinkBox = styled(Box)(({ theme: { spacing } }) => ({
	display: "flex",
	flexDirection: "column",
	gap: spacing(1),
	marginTop: spacing(3),
	cursor: "pointer"
}))
