import { Box, Link, Typography } from "@mui/material"
import React, { useEffect, useState } from "react"
import ScrollableStack from "../../layout/ScrollableStack"
import Timer from "../Timer/Timer"
import { IRedirectToSp } from "./Authcomponents.types"

const defultTimer = 5
const RedirectToSp = ({
	redirect_url,
	saml_response,
	formRef
}: IRedirectToSp) => {
	const [timer, setTimer] = useState(defultTimer)

	useEffect(() => {
		const clearFormTimer = setTimeout(() => {
			console.log("Timer: ", formRef.current)
			formRef.current?.submit()
		}, defultTimer * 1000)

		const clearTimer = setInterval(() => {
			setTimer(prevTimer => {
				if (prevTimer <= 0) {
					console.log("Prev", prevTimer)
					clearTimeout(clearTimer)
				}
				return prevTimer - 1
			})
		}, 1000)

		return () => {
			clearInterval(clearTimer)
			clearTimeout(clearFormTimer)
		}
	}, [])

	return (
		<Box
			display="flex"
			flexDirection="column"
			alignItems="center"
			justifyContent="center"
		>
			<Timer value={timer} total={defultTimer} />
			<Typography fontWeight="bold" variant="h5">
				Please wait!
			</Typography>
			<Typography variant="subtitle1">You're being redirected...</Typography>
			<Box
				display="flex"
				alignItems="center"
				justifyContent="center"
				component={Typography}
				variant="subtitle1"
			>
				If you haven't been redirected, &nbsp;
				<Link underline="hover" component="button" type="submit">
					Click here
				</Link>
			</Box>

			<input type="hidden" name="SAMLResponse" value={saml_response} />
			<input type="hidden" name="RelayState" value="/whoami" />
		</Box>
	)
}

export default RedirectToSp
