function flatToNested(path: string, value: any, obj: any): void {
  const splitted = path.split('.')
  splitted.forEach((split, index) => {
    const indexOfSquare = split.indexOf('[');
    if (indexOfSquare !== -1) {
      obj = digArray(split.split('[')[0], split[indexOfSquare + 1], obj)
    } else if (index === splitted.length - 1) {
      obj = dig(split, value, obj)
    } else {
      obj = dig(split, null, obj)
    }
  })
}

function dig(path: string, value: string | null, obj: any) {
  if (!obj[path]) {
    if (value) {
      obj[path] = value
      return obj[path]
    } else {
      obj[path] = {
        ...obj[path]
      }
      return obj[path]
    }
  } else {
    if (value) {
      obj[path] = value
    }
    return obj[path]
  }
}

function digArray(path: string, index: string | number, obj: any) {
  if (!obj[path]) {
    obj[path] = []
  }
  if (!obj[path][index]) {
    obj[path][index] = {}
    return obj[path][index]
  } else {
    return obj[path][index]
  }
}

export default flatToNested