import {
	Box,
	Container,
	Grid,
	Stack,
	Typography,
	useMediaQuery,
	useTheme
} from "@mui/material"
import ProductContainer from "../../components/ProductContainer"
import { ActivityLog } from "../../components/StyedTable"
import DRIQ from "../../assets/svgs/DRIQ.svg"
import EZLogin from "../../assets/svgs/EZLogin.svg"
import EZDOC from "../../assets/svgs/EZDOC.svg"
