import EZWEB from "../../assets/svgs/EZWEB.svg"
interface ProductType {
	name: string
	icon: string
}
type TProducts = Array<ProductType>

export const products: TProducts = [
	{
		name: "EZ Login",
		icon: EZLogin
	},
	{
		name: "EZ Doc",
		icon: EZDOC
	},
	{
		name: "EZ Nav",
		icon: EZNAV
	},
	{
		name: "Dr. IQ",
		icon: DRIQ
	},
	{
		name: "EZ Analytics",
		icon: EZANALYTICS
	},
	{
		name: "EZ Path",
		icon: EZPATH
	},
	{
		name: "EZ Login",
		icon: EZWEB
	}
]
function Home() {
	const theme = useTheme()
	const breakpoint = useMediaQuery(theme.breakpoints.down("md"))
	return (
		<Container maxWidth="xl">
			<Box>
				<Grid container spacing={3}>
					<Grid item xs={12}>
						<Stack spacing={3}>
							<Typography fontWeight="bold" variant="h6">
								Products
							</Typography>
							<ProductContainer />
							<Stack spacing={2}>
								<Typography fontWeight="bold" variant="h6">
									Activity logs
								</Typography>
								<ActivityLog />
							</Stack>
						</Stack>
					</Grid>
					{/* <Grid item xs={12} md={4} order={breakpoint ? 1 : 2}>
						<Box
							height="100%"
							width="100%"
							display="flex"
							flexDirection="column"
							gap={2}
						>
							<Box bgcolor="#d5e3ff" height="50%" borderRadius={4}>
								{Graphs1}
							</Box>
							<Box bgcolor="#d5e3ef" height="50%" borderRadius={4}>
								{Graphs2}
							</Box>
						</Box>
					</Grid> */}
				</Grid>
			</Box>
		</Container>
	)
}

export default Home
