import { FC } from "react"
import { ErrorIcon } from "../../utils/Icons"
import { InputStyledBox } from "./index.styles"
import { IInputStatusProps } from "./InputStatus.types"

const InputStatus: FC<IInputStatusProps> = ({ text, icon = <ErrorIcon /> }) => {
	return (
		<InputStyledBox component={"span"}>
			{icon}
			{text}
		</InputStyledBox>
	)
}

export default InputStatus
