import {
	Avatar,
	Badge,
	Box,
	Link,
	IconButton,
	Typography,
	Skeleton,
	CircularProgress,
	CircularProgressProps,
	SvgIconProps,
	SvgIconTypeMap,
} from "@mui/material"
import { TabsUnstyled, TabUnstyled, TabsListUnstyled } from "@mui/base"
import {
	LockIcon,
	PersonIcon,
	SettingsIcon
} from "../../utils/Icons"
import {
	SListContainer,
	SListItem,
	SProfileImage,
	SProfileImageContainer,
	SProfileSideBar,
	Tab,
	TabsList
} from "./index.styles"
import { IImage, IImageUploadResponse, ISideBar, SectionType } from "./Profile.types"
import { AxiosError, AxiosResponse } from "axios"
import { put } from "../../api/utils"
import { useMutation, UseMutationResult, useQueryClient } from "react-query"
import { useAppContext } from "../../context/AppContext/AppContext"
import { OverridableComponent } from "@mui/material/OverridableComponent"
import { AddCircle } from "@mui/icons-material"

type MenuListType = {
	icon: OverridableComponent<SvgIconTypeMap<{}, "svg">>,
	value: SectionType,
	label: string,
	to: string,
	isActive: boolean
}

const menuList: MenuListType[] = [
	{
		icon: SettingsIcon,
		value: SectionType.ACCOUNT,
		label: "Account Settings",
		to: "/account",
		isActive: true
	},
	{
		icon: PersonIcon,
		value: SectionType.PERMISSION,
		label: "Personal Information",
		to: "/permissions",
		isActive: false
	},
	// {
	// 	icon: LockIcon,
	// 	label: "Privacy",
	// 	to: "/profile",
	// 	isActive: false
	// }
]

let activeStyle = {
	textDecoration: "underline"
}

function imageUpload(imageData: IImage) {
	const formData = new FormData()
	formData.append("image", imageData.image as Blob)
	return put<AxiosResponse<IImageUploadResponse>>(
		"user/profile-image",
		formData
	)
}

const Sidebar = ({ profileImage, fullName, isLoading }: ISideBar) => {
	const appState = useAppContext()
	const queryClient = useQueryClient()

	//mutation to update the Image
	const {
		data: ImageData,
		mutate,
		isLoading: isProfileLoading
	}: UseMutationResult<
		AxiosResponse<IImageUploadResponse>,
		AxiosError<IImageUploadResponse>,
		IImage
	> = useMutation<
		AxiosResponse<IImageUploadResponse>,
		AxiosError<IImageUploadResponse>,
		IImage
	>(imageUpload, {
		onSuccess: res => {
			const message = res.data.message
			queryClient.invalidateQueries("profile")

			appState?.setAlert({
				show: true,
				message,
				status: "success"
			})
		},
		onError: err => {
			const message = err.response?.data?.message
			if (!message) return
			appState?.setAlert({
				show: true,
				message,
				status: "error"
			})
		}
	})

	const handleFileUpload: React.ChangeEventHandler<
		HTMLInputElement
	> = async e => {
		if (e?.target) {
			const image = e.target.files?.[0]
			if (image) {
				mutate({ image })
			}
		}
	}

	function a11yProps(index: number) {
		return {
			id: `vertical-tab-${index}`,
			'aria-controls': `vertical-tabpanel-${index}`,
		};
	}

	return (
		<SProfileSideBar>
			<SProfileImageContainer>
				<SProfileImage>
					{isLoading && (
						<Box>
							<Skeleton
								animation="wave"
								variant="circular"
								width={100}
								height={100}
							/>
						</Box>
					)}
					{!isLoading && (
						<Badge
							overlap="circular"
							anchorOrigin={{ horizontal: "right", vertical: "top" }}
							badgeContent={
								<IconButton size="small" color="primary" component='label' htmlFor="upload-photo-handler" tabIndex={-1}>
									<input
										id="upload-photo-handler"
										accept=".png, .jpg, .jpeg, .webp"
										type="file"
										onChange={handleFileUpload}
										style={{ height: 0, width: 0 }}
										tabIndex={0}
										aria-label='Select picture to upload'
									/>
									<AddCircle width={28} height={28} />
								</IconButton>
							}
						>
							<CircularProgressWithChildren isLoading={isProfileLoading}>
								<Avatar
									sx={{ width: 100, height: 100 }}
									src={
										`${process.env.REACT_APP_SERVER_URL}/user/profile-image/${profileImage}` ||
										"https://picsum.photos/200"
									}
								/>
							</CircularProgressWithChildren>
						</Badge>
					)}
				</SProfileImage>
				<Typography color="primary" variant="subtitle1" fontWeight="bold">
					{isLoading ? <Skeleton animation="wave" width={100} /> : fullName}
				</Typography>
			</SProfileImageContainer>
			<SListContainer role='tablist'>
				<TabsList>
					{menuList.map(({ icon: Icon, label, value, to, isActive }, id) => (
						<Tab key={value} value={value} {...a11yProps(0)}>
							{<Icon color='inherit' />} {label}
						</Tab>
					))}
				</TabsList>
			</SListContainer>
		</SProfileSideBar>
	)
}

export default Sidebar

function CircularProgressWithChildren({
	isLoading,
	...rest
}: CircularProgressProps & {
	isLoading: boolean
	children: React.ReactNode
}) {
	return (
		<Box sx={{ position: "relative", display: "flex", alignItems: "center" }}>
			<CircularProgress
				thickness={1}
				size={110}
				variant="indeterminate"
				style={{ visibility: isLoading ? "visible" : "hidden" }}
				{...rest}
			/>

			<Box
				sx={{
					top: 0,
					left: 5,
					bottom: 0,
					right: 0,
					position: "absolute",
					display: "flex",
					alignItems: "center"
				}}
			>
				{rest.children}
			</Box>
		</Box>
	)
}
