import { Box, styled } from "@mui/material"

export const SAccessItemBox = styled(Box)(
	({ theme: { spacing, breakpoints } }) => ({
		display: "grid",
		gridTemplateColumns: "repeat(2,1fr)",
		[breakpoints.down("md")]: {
			gridTemplateColumns: "1fr"
		},
		marginTop: spacing(1),
		rowGap: spacing(5),
		columnGap:spacing(9)
	})
)
