import React, { useState } from 'react'
import { get } from '../api/utils'

type Props = {}

const TestRoute = (props: Props) => {
  const [res, setResponse] = useState<string>()

  return (
    <div>
      <button onClick={() => {
        setResponse(undefined)
        get('test-token').then(res => {
          setResponse("success")
        })
      }}>
        REQUEST
      </button>
      {res && <p>{res}</p>}
    </div>
  )
}

export default TestRoute