import { useContext, useState, createContext } from "react"
import { useMutation, UseMutationResult, useQuery, useQueryClient } from "react-query"
import { AxiosError } from "axios"
import {
	IAppData,
	IUserContext,
	IUserData,
	IUserErrorResponse,
	IUserResult,
	IUserState,
	TUserInfo,
	TUserPracticeInfo,
	TValidationErrors
} from "./UserContext.types"
import { get, post, put } from "../../api/utils"
import { useAppContext } from "../AppContext/AppContext"
import { useParams } from "react-router-dom"
import { validate } from "./UserContext.validations"
import invertSelected from "../../utils/invertSelected"
import generateUserData from "./generateUserData"

const INITIAL_USER_INFO: TUserInfo = {
	firstname: "",
	lastname: "",
	email: ""
}

const UserContext = createContext<IUserState | null>(null)

const getPracticesAndRoles = () => {
	return get("user/service-providers/practices/roles")
}

const getUserPracticesAndRoles = (id: number) => {
	return get(`user/${id}/service-providers/practices/roles`)
}

function createUser(formdata: IUserData) {
	return post<IUserResult>("user", formdata)
}

function updateUser(formdata: IUserData, id: number) {
	return put<IUserResult>(
		`user/${id}/service-providers/practices/roles`,
		formdata
	)
}

const useUserState = () => {
	const [appData, setAppData] = useState<IAppData[]>()
	const [userInfo, setUserInfo] = useState<TUserInfo>(INITIAL_USER_INFO)
	const [errors, setErrors] = useState<TValidationErrors>()
	const [disableAll, setDisableAll] = useState<boolean>(true)

	const queryClient = useQueryClient()

	const appState = useAppContext()
	const { id } = useParams()

	const mutationSuccessMsg = id
		? "User updated successfully"
		: "User added successfully"

	// Queries
	const {
		isLoading: appDataLoading,
		isError: dataLoadError,
		data: queryData
	} = useQuery(
		"practicesAndRoles",
		id ? () => getUserPracticesAndRoles(parseInt(id)) : getPracticesAndRoles,
		{
			onSuccess: data => {
				if (id) {
					setUserInfo({
						firstname: data.data.firstname,
						lastname: data.data.lastname,
						email: data.data.email,
						is_active: data.data.is_active || false
					})
				}
				setDisableAll(false)
				setAppData(data.data.sp_practice_roles)
			},
			refetchOnWindowFocus: false
		}
	)

	const {
		mutate,
		isLoading: isCreatingUser
	}: UseMutationResult<
		IUserResult,
		AxiosError<IUserErrorResponse>,
		IUserData
	> = useMutation<IUserResult, AxiosError<IUserErrorResponse>, IUserData>(
		id
			? (formdata: IUserData) => updateUser(formdata, parseInt(id))
			: createUser,
		{
			onSuccess: _data => {
				appState?.setAlert({
					show: true,
					message: mutationSuccessMsg,
					status: "success"
				})

				queryClient.invalidateQueries(['users'])

				if (!id) {
					setDisableAll(true)
				}
			},
			onError: _err => {
				const message = _err.response?.data?.message
				appState?.setAlert({
					show: true,
					message: message ?? "User add request failed",
					status: "error"
				})
			}
		}
	)

	const setSelected = (appId: number) => {
		const mapped = appData?.map(app => {
			if (app.id == appId) {
				return { ...app, selected: !app.selected }
			} else {
				return app
			}
		})
		setAppData(mapped)

		// const appIndex = appData!.findIndex(app => app.id == appId)

		// const mapped = produce(appData, (draft) => {
		//   draft![appIndex].selected = !draft![appIndex].selected
		// })

		// setAppData(mapped)
	}

	const setRole: IUserState["setRole"] = (appId, roleId) => {
		// const appIndex = appData!.findIndex(app => app.id == appId)

		// const mapped = produce(appData, (draft) => {
		//   const newRoles = invertSelected(draft![appIndex].roles, roleId)
		//   draft![appIndex].roles = newRoles
		// })

		// setAppData(mapped)

		const mapped = appData?.map(app => {
			if (app.id == appId) {
				const newRoles = invertSelected(app.roles, roleId)

				return { ...app, roles: newRoles }
			}
			return app
		})
		setAppData(mapped)
	}

	const setSubrole: IUserState["setSubrole"] = (appId, subRoleId) => {
		const mapped = appData?.map(app => {
			if (app.id == appId) {
				const selectedRoleIndex = app.roles.findIndex(ele => ele.isSelected)
				const newSubroles = invertSelected(
					app.roles[selectedRoleIndex]?.sub_roles,
					subRoleId
				)

				const newApp = { ...app }
				newApp.roles = [...newApp.roles]
				newApp.roles[selectedRoleIndex] = {
					...newApp.roles[selectedRoleIndex],
					sub_roles: newSubroles
				}
				return newApp
			}
			return app
		})
		setAppData(mapped)
	}

	const setGender: IUserState["setGender"] = (appId, genderId) => {
		// const appIndex = appData!.findIndex(app => app.id == appId)

		// const mapped = produce(appData, (draft) => {
		//   const newGender = invertSelected(draft![appIndex].gender, genderId)
		//   draft![appIndex].gender = newGender
		// })

		// setAppData(mapped)

		const mapped = appData?.map(app => {
			if (app.id == appId) {
				const newGender = invertSelected(app.gender, genderId)

				return { ...app, gender: newGender }
			}
			return app
		})
		setAppData(mapped)
	}

	const addUser = async () => {
		const userPractices: TUserPracticeInfo = generateUserData(appData)

		const user: IUserData = {
			...userInfo,
			...userPractices
		}

		let valErrors: TValidationErrors = await validate(user, appData)

		setErrors(valErrors)

		if (Object.keys(valErrors).length < 1) {
			user.apps = user.apps?.filter(app => app)
			mutate(user)
		}
	}

	const addNew = () => {
		setUserInfo(INITIAL_USER_INFO)
		setAppData(queryData?.data?.sp_practice_roles)
		setDisableAll(false)
	}

	return {
		appData,
		setAppData,
		dataLoadError,
		appDataLoading,
		isCreatingUser,
		setSelected,
		setRole,
		setSubrole,
		setGender,
		addUser,
		setUserInfo,
		userInfo,
		errors,
		disableAll,
		addNew
	}
}

export const UserProvider = ({ children }: IUserContext) => {
	const appData = useUserState()
	return <UserContext.Provider value={appData}>{children}</UserContext.Provider>
}

//use this hook to consume the context
export const useUserContext = () => {
	return useContext(UserContext)
}
