import { Box, Card, Link, styled } from "@mui/material"
import {
	TabUnstyled,
	tabUnstyledClasses,
	buttonUnstyledClasses,
	TabsListUnstyled,
	TabPanelUnstyled
} from "@mui/base"

export const SProfileContainer = styled(Box)(() => ({
	mt: "auto",
	display: "grid",
	placeItems: "center",
}))

export const SProfileCard = styled(Box)(({ theme }) => ({
	width: '100%',
	display: "flex",
	borderRadius: parseInt(`${theme.shape.borderRadius}`) * 8,
	// overflow: 'hidden'
	[theme.breakpoints.down('sm')]: {
		flexDirection: 'column'
	}
}))
export const SProfileCardContainer = styled(Card)(({ theme }) => ({
	width: '100%',
	display: "flex",
	flexDirection: "column",
	maxWidth: "940px",
	borderRadius: parseInt(`${theme.shape.borderRadius}`) * 8,
	overflow: 'hidden',
	margin: 'auto'
}))
export const SProfileSideBar = styled(Box)(({ theme }) => ({
	width: "300px",
	background: theme.palette.background.backgroundGradientMulti?.('50deg'),
	display: "flex",
	flexDirection: "column",
	padding: "1rem",
	gap: "2rem",
	[theme.breakpoints.down('md')]: {
		width: 230
	},
	[theme.breakpoints.down('sm')]: {
		width: '100%'
	}
}))
export const SProfileImageContainer = styled(Box)(() => ({
	display: "grid",
	placeItems: "center",
	marginTop: "2rem",
	gap: "0.5rem",
}))
export const SProfileImage = styled(Box)(() => ({
	display: "flex",
	justifyContent: "center",
	alignItems: "center",
	width: 105,
	height: 105,
	backgroundColor: "white",
	borderRadius: "50%"
}))
export const SListContainer = styled(Box)(({ theme }) => ({
	display: "flex",
	flexDirection: "column",
	gap: "1rem",
	marginTop: theme.spacing(2),
	[theme.breakpoints.down('sm')]: {
		display: 'grid',
		gridAutoFlow: 'column',
		gridAutoColumns: '1fr',
		justifyContent: 'space-around',
	}
}))
export const SListItem = styled(Box)(({ theme }) => ({
	display: "flex",
	alignItems: "center",
	gap: "1rem",
	[theme.breakpoints.down('sm')]: {
		fontSize: '0.85em',
		flexDirection: "column",
	}
}))

export const SLoadingBar = styled(Box)(() => ({
	height: "2px",
}))

export const Tab = styled(TabUnstyled)(({ theme }) => `
	font-family: ${theme.typography.fontFamily};
  cursor: pointer;
  font-size: 0.875rem;
  font-weight: bold;
  background-color: transparent;
  width: 100%;
  padding: ${theme.spacing(1)};
  padding-left: ${theme.spacing(2.5)};
  margin: 6px 6px;
  border: none;
  display: flex;
  justify-content: flex-start;
  gap: ${theme.spacing(2)};
  color: ${theme.palette.text.primary};

  & svg {
	color: ${theme.palette.grey[400]};
  }

  ${[theme.breakpoints.down('sm')]} {
	font-size: 0.85em;
	flex-direction: column;
	align-items: center;
	padding-left: ${theme.spacing(1)};
  }

  &:focus {
	background-color: ${theme.palette.action.hover};
	outline: none;
  }

  &.${tabUnstyledClasses.selected} {
    color: ${theme.palette.primary.main};
	& svg {
		color: ${theme.palette.primary.main};
	};
  }

  &.${buttonUnstyledClasses.disabled} {
    opacity: 0.5;
    cursor: not-allowed;
  }

  &:hover {
	color: ${theme.palette.primary[900]}
  }

`);

export const TabsList = styled(TabsListUnstyled)(
	({ theme }) => `
  background-color: transparent;
  border-radius: 12px;
  margin-bottom: 16px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  align-content: space-between;
  ${[theme.breakpoints.down('sm')]} {
	flex-direction: row;
  }
  `,
);

export const TabsPanel = styled(TabPanelUnstyled)(
	({ theme }) => ``,
);