import { Box, Typography } from "@mui/material"
import moment from "moment"
import { useCallback, useState } from "react"
import { Cell, Column, Row } from "react-table"
import { useAuthContext } from "../../context/AuthContext/AuthContext"
import {
	ActionLevelEnum,
	DirectionEnum,
	PageInfo,
	useActivityLogQuery,
	UserAction
} from "../../graphql/generated"
import useLocalStorage from "../../hooks/useLocalStorage"
import { IFilters } from "../../pages/AuditTrail/AuditTrail.types"
import { DataLoadError } from "../ErrorScreens"
import { Table } from "../Table"
import { ITable } from "../Table/Table.types"
import jwt_decode, { JwtPayload } from "jwt-decode"
import DRIQ from "../../assets/svgs/DRIQ.svg"
import EZLogin from "../../assets/svgs/EZLogin.svg"
import EZDOC from "../../assets/svgs/EZDOC.svg"
import EZPATH from "../../assets/svgs/EZPATH.svg"
import EZANALYTICS from "../../assets/svgs/EZANALYTICS.svg"
import EZNAV from "../../assets/svgs/EZNAV.svg"
import EZWEB from "../../assets/svgs/EZWEB.svg"

interface ProductType {
	name: string
	icon: string
}
type TProducts = Record<string, ProductType>

export const products: TProducts = {
	"ez-login": {
		name: "EZ Login",
		icon: EZLogin
	},
	"ez-doc": {
		name: "EZ Doc",
		icon: EZDOC
	},
	"ez-nav": {
		name: "EZ Nav",
		icon: EZNAV
	},
	"dr-iq": {
		name: "Dr. IQ",
		icon: DRIQ
	},
	"ez-analytics": {
		name: "EZ Analytics",
		icon: EZANALYTICS
	},
	"ezanalytics/saml": {
		name: "EZ Analytics",
		icon: EZANALYTICS
	},
	"ez-path": {
		name: "EZ Path",
		icon: EZPATH
	},
	"ezweb": {
		name: "EZ Web",
		icon: EZWEB
	}
}

const StyledTable = () => {
	const { storedValue } = useLocalStorage("access_token")
	const { sub } = jwt_decode<JwtPayload>(storedValue)
	const [variables, setVariables] = useState<IFilters>({
		actionLevel: ActionLevelEnum.User,
		first: 5,
		userEmail: sub
	})

	let columns: Column<UserAction>[] = [
		{
			Header: "Product",
			accessor: "appName",
			width: 500,
			Cell: ({ value, column }): any => {
				if (typeof value == "string") {
					const product = products[value]
					if (product) {
						return (
							<Box display="flex" gap="1rem">
								<img width={50} src={product.icon} />
								<Box>
									<Typography variant="caption" color="grey.500" fontWeight='bold'>
										{column.render("Header")}
									</Typography>
									<Typography fontWeight={600}>{product.name}</Typography>
								</Box>
							</Box>
						)
					}
				}
			}
		},
		{
			Header: "Date & time",
			accessor: "actionDate",
			Cell: ({ row, column }) => {
				const date = moment(
					new Date(`${row.values.actionDate}Z`)
						.toLocaleString("en-US", { timeZone: "Europe/London" })
						.toString()
				).format("dddd, Do MMM YYYY, h:mm A")
				return (
					<Box>
						<Typography variant="caption" color="grey.500" fontWeight='bold'>
							{column.render("Header")}
						</Typography>
						<Typography variant="subtitle2" fontWeight="bold">
							{date}
						</Typography>
					</Box>
				)
			},
			width: 300
		},
		{
			id: "duration",
			Header: "Duration",
			Cell: ({ row, column }: any) => {
				const time = moment(
					new Date(`${row.values.actionDate}Z`)
						.toLocaleString("en-US", { timeZone: "Europe/London" })
						.toString()
				).from(
					new Date()
						.toLocaleString("en-US", { timeZone: "Europe/London" })
						.toString()
				)
				return (
					<Box>
						<Typography variant="caption" color="grey.500" fontWeight='bold'>
							{column.render("Header")}
						</Typography>
						<Typography variant="subtitle2" fontWeight="bold">
							{time}
						</Typography>
					</Box>
				)
			},
			width: 200
		}
	]

	const { data, isLoading, isFetching, isError } = useActivityLogQuery(
		variables,
		{
			keepPreviousData: true,
			refetchOnWindowFocus: false,
			select: useCallback(
				(queryData: any): { actions: UserAction[]; pageInfo: PageInfo } => {
					const filtered =
						queryData?.data?.userActions?.edges?.map(
							(edge: any) => edge.node
						) || []
					return {
						actions: filtered,
						pageInfo: queryData?.data?.userActions?.pageInfo
					}
				},
				[]
			)
		}
	)

	const setPage: ITable<{}, "cursor">["setPage"] = (_pageNo, direction) => {
		if (direction) {
			setVariables({
				...variables,
				direction: direction as DirectionEnum,
				cursor:
					direction == DirectionEnum.Backward
						? pageInfo?.startCursor
						: pageInfo?.endCursor
			})
		}
	}

	const { actions: activity, pageInfo } = data || {}

	if (isError) {
		return <DataLoadError />
	}

	return (
		<Table
			columns={columns}
			data={activity || []}
			metaData={{
				hasNextPage: pageInfo?.hasNextPage,
				hasPreviousPage: pageInfo?.hasPreviousPage,
				total_records: pageInfo?.totalRecords
			}}
			disabled={isLoading || isFetching}
			dataLoading={isLoading || isFetching}
			paginationType="cursor"
			rowsPerPage={pageInfo?.perPage || 0}
			setPage={setPage}
			noHeader
			rowHighlight
			size="medium"
		/>
	)
}

export default StyledTable
