import { CancelOutlined } from '@mui/icons-material'
import { ListItemIcon, ListItemText, MenuItem, MenuItemProps, styled } from '@mui/material'

const ClearMenuItem = styled((props: MenuItemProps) => {
  return (
    <MenuItem {...props}>
      <ListItemIcon><CancelOutlined /></ListItemIcon>
      <ListItemText>Clear</ListItemText>
    </MenuItem>
  )
})(({ theme: { palette } }) => ({
  border: `1px solid ${palette.secondary[800]}`
}))

export default ClearMenuItem